import { engineInterface } from '../core/EngineInterface.js'

import { Project } from '../objects/Project.js'
import { ResourceManager } from './ResourceManager.js'
import { StorageManager } from './StorageManager.js'
import { SceneManager } from './SceneManager.js'
import { PageManager } from './PageManager.js'
import { AnimationManager } from './AnimationManager.js'

Date.prototype.format = function () {
  return this.getFullYear() + '/' + (this.getMonth() + 1) + '/' + this.getDate()
}

function ProjectManager () {
  // Singleton pattern
  if (ProjectManager._instance !== undefined) {
    return ProjectManager._instance
  }
  ProjectManager._instance = this

  var scope = this

  // Public variables /////////////////////////////////////////////////
  this.curProject = null
  // this.projectName = 'test'

  this.projectList = []

  // Private variables ////////////////////////////////////////////////

  // Public Methods ///////////////////////////////////////////////////
  this.createProject = function (projectName, projectPath, resourceUrl) {
    var storageManager = new StorageManager()

    var errorCode = ipcRenderer.sendSync('CreateProject', projectName, projectPath)

    if (errorCode !== 100) {
      console.log('errorCode:', errorCode)
      engineInterface.loadingProjectData.errorCode = errorCode
      engineInterface.loadingProjectData.hasFinished = false
      return errorCode
    }

    var project = new Project()
    project.title = projectName
    project.filePath = projectPath.replace(/\\/g, '/') + '/' + projectName

    var date = new Date()
    project.date = date.format()

    scope.curProject = project

    scope.projectList.push(project)

    engineInterface.loadingProjectData.hasFinished = false

    var resourceManager = new ResourceManager()
    var sceneManager = new SceneManager()
    var pageManager = new PageManager()
    var animationManager = new AnimationManager()

    storageManager.ifListenDo = false

    sceneManager.clear()
    pageManager.clear()
    resourceManager.clear()
    animationManager.clear()
    storageManager.clear()
    engineInterface.clearEngine()

    resourceManager.loading = true
    var promise = new Promise(function (resolve, reject) {
      resourceManager.importResource(resourceUrl, resolve, reject)
    })
    promise.then(function () {
      var modelList = resourceManager.modelList

      for (var url in modelList) {
        for (var name in modelList[url]) {
          sceneManager.addMesh(url, name)
        }
      }

      resourceManager.clearLoad()

      engineInterface.loadingProjectData.errorCode = 100
      engineInterface.loadingProjectData.hasFinished = true

      storageManager.saveEditor()
      storageManager.saveProject(true)
      engineInterface.updateProjectList()
      engineInterface.updateMeshList()

      ipcRenderer.sendSync('SetLocalStatic', project.title, project.filePath)

      engineInterface.hasSaved = true

      storageManager.ifListenDo = true
    }, function () {
      engineInterface.loadingProjectData.errorCode = 203
      engineInterface.hasSaved = true
      ipcRenderer.sendSync('DeleteProject', project.filePath)
    })
    // resourceManager.importResource(resourceUrl, function() {
    //   var modelList = resourceManager.modelList
    //   for (var url in modelList) {
    //     for (var name in modelList[url]) {
    //       sceneManager.addMesh(url, name)
    //     }
    //   }

    //   resourceManager.clearLoad()
    //   engineInterface.loadingProjectData.hasFinished = true

    //   storageManager.saveProject(true)
    //   engineInterface.updateProjectList()

    //   ipcRenderer.sendSync('SetLocalStatic', project.title, project.filePath)
    // })
  }

  this.openProject = function (projectPath) {
    var storageManager = new StorageManager()
    var resourceManager = new ResourceManager()
    var sceneManager = new SceneManager()
    var pageManager = new PageManager()
    var animationManager = new AnimationManager()

    projectPath = projectPath.replace(/\\/g, '/')

    var errorCode = ipcRenderer.sendSync('OpenProject', projectPath)

    if (errorCode !== 100) {
      console.log('errorCode:', errorCode)
      engineInterface.loadingProjectData.errorCode = errorCode
      engineInterface.loadingProjectData.hasFinished = false
      return errorCode
    }

    engineInterface.loadingProjectData.hasFinished = false

    storageManager.ifListenDo = false

    sceneManager.clear()
    pageManager.clear()
    resourceManager.clear()
    animationManager.clear()
    storageManager.clear()
    engineInterface.clearEngine()

    resourceManager.loading = true

    storageManager.loadProject(projectPath + '/save.json', function (res) {
      resourceManager.clearLoad()

      if (res) {
        engineInterface.loadingProjectData.errorCode = 100
        engineInterface.loadingProjectData.hasFinished = true
      } else {
        engineInterface.loadingProjectData.errorCode = 203
        engineInterface.loadingProjectData.hasFinished = false
      }

      storageManager.saveEditor()

      ipcRenderer.sendSync('SetLocalStatic', scope.curProject.title, scope.curProject.filePath)

      engineInterface.hasSaved = true

      storageManager.ifListenDo = true
    }, projectPath)
  }

  this.deployProject = function (deployInfo) {
    var storageManager = new StorageManager()
  	var fileList = []

    if (checkNone(deployInfo.title) || deployInfo.title.length === 0) { return false }

  	var jsonProject = storageManager.saveProject(true)

  	fileList = jsonProject.resourceList.urlList

    if (platform.name === 'Electron') {
      if (checkNone(scope.curProject)) { return false }
      ipcRenderer.send('Deploy',
        jsonProject.projectInfo,
        deployInfo,
        fileList)
    }
  }

  if (isEditor) {
    ipcRenderer.on('DeployResponse', (event, info) => {
      engineInterface.releaseData.releasingState = info.releasingState
      engineInterface.releaseData.releasingInfo = info.releasingInfo
    })
  }

  this.getDeployList = function () {
    if (isEditor) {
      var request = null
      if (window.XMLHttpRequest) {
        request = new XMLHttpRequest()
      } else if (window.ActiveXObject) {
        request = new ActiveXObject('Microsoft.XMLHTTP')
      }

      var jsonConfig = null
      var deployList = null

      if (request) {
        // 本地获取配置文件，获取服务器地址
        // request.open("GET", 'static/default/config.json', false)
        // request.onreadystatechange = function () {
        //   if (request.readyState === 4) {
        //     if (request.status == 200 || request.status == 0) {
        //       jsonConfig = JSON.parse(request.responseText)
        //       console.log('jsonConfig:', jsonConfig)
        //     }
        //   }
        // }
        // request.send(null)
        var configData = ipcRenderer.sendSync('getConfig')

        jsonConfig = JSON.parse(configData)
        console.log('jsonConfig:', jsonConfig)

        if (!checkNone(jsonConfig)) {
          var serverAddr = jsonConfig.serverAddr

          // request.open("GET", serverAddr + '/static/projects/deployProjects.json')
          request.open('GET', serverAddr + '/getDeployList')
          request.setRequestHeader('If-Modified-Since', '0')
          request.onreadystatechange = function () {
            if (request.readyState === 4) {
              if (request.status == 200 || request.status == 0) {
                deployList = JSON.parse(request.responseText)
                console.log('deployList:', deployList)
              }

              engineInterface.updateDeployProjectList(deployList)
            }
          }

          request.onload = function () {
            console.log('Finish get deploy list')
          }

          request.onerror = function () {
            console.log('None jsonEditor')
          }
          request.send(null)
        }
      }
    }
  }

  this.exportStaticPage = function () {
    if (isEditor && !checkNone(scope.curProject)) {
      var storageManager = new StorageManager()
      storageManager.saveProject(true)
      var res = ipcRenderer.sendSync('ExportStaticPage', scope.curProject.filePath)
    } else { var res = false }

    if (res) {
      alert('导出静态页面成功。')
    } else {
      alert('导出静态页面失败。')
    }
  }

  // function addMeshes(modelList) {
  //   for (var url in modelList) {
  //     for (var name in modelList[url]) {
  //       function loadMtl() {
  //         pcRenderer.sendSync('CheckMtl', projectName, projectPath)
  //         sceneManager.addMesh(url, name)
  //       }
  //     }
  //   }

  //                    if (!checkNone(childMesh.orgMaterial)) {
  //               if (!checkNone(matUrlList[childMesh.orgMaterial])) {
  //                 var url = matUrlList[childMesh.orgMaterial]
  //                 var sceneManager = new SceneManager()
  //                 console.log(22,childMesh.name, url)
  //                 sceneManager.setTexture(url, childMesh)
  //               }
  //             }
  //     });

  //   }
  // }
}

export { ProjectManager }



