  //     return false
  //   }

  //   if (frameIndex >= frameList.length) {
  //     console.log('Animation, setCurKeyFrame(), frameIndex: "FrameIndex need to be in the range of frameNumber."')
  //     return false
  //   }

  //   curFrameIndex = frameIndex
  // }

  // // 添加关键帧
  // this.addKeyFrame = function (frameTimepoint) {
  //   if (frameTimepoint <= 0) {
  //     console.log('Animation, addKeyFrame(), frameTimepoint: "FrameTimepoint need to be larger than 0."')
  //     return false
  //   }

  //   if (frameTimepoint > frameNumber) {
  //     console.log('Animation, addKeyFrame(), frameTimepoint: "FrameTimepoint need to be in the range of frameNumber."')
  //     return false
  //   }

  //   for (var index = 0; index < frameList.length; ++index) {
  //     if (frameTimepoint === frameList[index].frameTimepoint) {
  //       console.log('Animation, addKeyFrame(), frameTimepoint: "FrameTimepoint has a key frame already."')
  //       return false
  //     }
  //   }

  //   var keyFrame = new KeyFrame(frameTimepoint)
  //   frameList.push(keyFrame)

  //   frameList.sort(function (fstFrame, secFrame) {
  //     if (fstFrame.frameTimepoint > secFrame.frameTimepoint) { return true } else { return false }
  //   })
  // }

  // // 删除关键帧
  // this.removeKeyFrame = function (frameIndex) {
  //   if (!checkNone(frameList[frameIndex])) {
  //     frameList.splice(frameIndex, 1)
  //   }
  // }

  // // 获取动画中的镜头数据参数
  // this.getCameraModel = function () {
  //   return this.dataModel.camera
  // }

  // this.getCurKeyFrame = function () {
  //   if (curFrameIndex === -1) { return null }
  //   return frameList[curFrameIndex]
  // }

  // this.interpolateSceneObj = function (preFrame, nextFrame, frameTimepoint) {
  //   console.log(frameTimepoint)

  //   if (checkNone(nextFrame)) { return false }

  //   console.log(preFrame, nextFrame)

  //   var preTimepoint = 0
  //   var nextTimepoint = nextFrame.frameTimepoint

  //   var resDataModel = {}
  //   resDataModel.changeList = {}
  //   var changeList = resDataModel.changeList

  //   var originSceneObj = animationManager.getOriginSceneObj()

  //   var nextChangeList = nextFrame.dataModel.jsonSceneObj.changeList

