Animation.prototype.clone = function () {
  var animation = new Animation()
  animation.dataModel.copy(this.dataModel)
  return animation
}

Animation.prototype.toJSON = function () {
  return this.dataModel
}

function AnimationChange (animation) {
  // Parent constructor //////////////////////////////////////////////
  // 构造数据模型
  DataModelFactory.call(this, DataModelType.ANIMATION_CHANGE)

  var scope = this

  // Public variables ///////////////////////////////////////////////
  // sid
  // keyFrameList

  this.parent = animation

  // Private variables //////////////////////////////////////////////////
  var curKeyFrame = null

  var kidList = {}
  var frameBeginId = 100
  var frameEndId = 100000

  var playEnd = false

  var sceneManager = new SceneManager()
  var pageManager = new PageManager()

  // Public methods //////////////////////////////////////////////////////////
  this.removeSelf = function () {
    scope.parent.removeAnimationChangeByObj(scope)
  }

  this.playKeyFrame = function (timePoint) {
    playEnd = false

    var keyFrame = scope.getKeyFrameByTime(timePoint)

    if (checkNone(keyFrame)) { return false }

    var sceneManager = new SceneManager()
    var scene = sceneManager.getScene()

    var sceneObj = sceneManager.getObjBySid(scope.sid)
    if (!checkNone(sceneObj)) {
      keyFrame.applyToObj(sceneObj)
    }

    if (playEnd) {
      return false
    }
    return true
  }

  this.addKeyFrame = function (timePoint, setKeyFrame) {
    if (timePoint > 1500) { return false }

    if (checkNone(setKeyFrame)) {
      var sceneObj = sceneManager.getObjBySid(scope.sid)
      if (checkNone(sceneObj)) { return null }

      var keyFrame = new KeyFrame()

      curKeyFrame = keyFrame

      scope.addKid(keyFrame)

      keyFrame.timePoint = timePoint
      keyFrame.setFromObj(sceneObj)
      console.log(keyFrame)

      scope.keyFrameList.push(keyFrame)

      scope.keyFrameList.sort(sortKeyFrame)

      return keyFrame
    } else {
      var keyFrame = new KeyFrame()
      keyFrame.dataModel.setFrom(setKeyFrame)

      scope.setId(keyFrame, setKeyFrame.kid)

      scope.keyFrameList.push(keyFrame)

      return keyFrame
    }

    return null
  }

  // this.removeKeyFrame = function (index) {
  //   if (index >= 0 && index < scope.keyFrameList.length) {
  //     scope.removeKid(scope.keyFrameList[index].kid)
  //     scope.keyFrameList.splice(index, 1)
  //     return true
  //   }
  //   return false
  // }

  this.removeKeyFrame = function (timePoint) {
    for (var index = 0; index < scope.keyFrameList.length; ++index) {
      var keyFrame = scope.keyFrameList[index]
      if (keyFrame.timePoint === timePoint) {
        scope.removeKid(scope.keyFrameList[index].kid)
        scope.keyFrameList.splice(index, 1)
        return true
      }
    }
    return false
  }

  this.setKeyFrame = function (id, timePoint) {
    var keyFrame = scope.getKeyFrameById(id)
    if (checkNone(keyFrame)) { return false }

    keyFrame.timePoint = timePoint
    scope.keyFrameList.sort(sortKeyFrame)
    console.log(scope.keyFrameList)
  }

  this.removeAllKeyFrame = function () {
    scope.keyFrameList.forEach(keyFrame => {
      scope.removeKid(keyFrame.kid)
    })

    curKeyFrame = null

    scope.keyFrameList.splice(0, scope.keyFrameList.length)
  }

  // 新添加具有数据模型的对象到列表中
  this.addKid = function (sceneObj) {
    var kid = getId(frameBeginId, frameEndId)

    if (kid === 0) {
      console.log('Animation, addkid(), kid: "Failed to get storage id."')
      return false
    }
    if (!checkNone(sceneObj.dataModel)) {
      sceneObj.kid = kid
      kidList[kid] = true
    }
    return true
  }

  // 从模型列表中删除对象的数据模型
  this.removeKid = function (sceneObj) {
    if (checkNone(sceneObj.dataModel)) {
      return
    }
    var kid = sceneObj.kid
    if (checkId(kid)) {
      delete kidList[kid]
    }
  }

  // 设置对象的kid
  this.setId = function (sceneObj, kid) {
    if (!checkId(kid)) {
      kidList[kid] = true
      sceneObj.kid = kid
    }
  }

  this.setCurKeyFrame = function (index) {
    if (index < 0 || index > scope.keyFrameList.length) {
      curKeyFrame = null
      return false
    }

    curKeyFrame = scope.keyFrameList[index]

    engineInterface.curAniMeshData.update()

    return true
  }

  this.getCurKeyFrame = function () {
    return curKeyFrame
  }

  this.getKeyFrameById = function (kid) {
    for (var index = 0; index < scope.keyFrameList.length; ++index) {
      if (kid === scope.keyFrameList[index].kid) {
        return scope.keyFrameList[index]
      }
    }
    return null
  }

  this.getKeyFrameByTime = function (timePoint, notInter) {
    for (var index = 0; index < scope.keyFrameList.length; ++index) {
      if ((timePoint >= scope.keyFrameList[index].timePoint) &&
          (timePoint < scope.keyFrameList[index].timePoint + 1)) {
        curKeyFrame = scope.keyFrameList[index]

        // 播放到最大帧的时候停止
        if (index === scope.keyFrameList.length - 1) {
          playEnd = true
        }

        return scope.keyFrameList[index]
      }
    }

    curKeyFrame = null

    if (!notInter) { return scope.getInterpolateKeyFrame(timePoint) } else { return null }
  }

  this.getInterpolateKeyFrame = function (timePoint) {
    var nearFrame = scope.getPreNextFrame(timePoint)
    var preFrame = nearFrame.preFrame
    var nextFrame = nearFrame.nextFrame

    if (checkNone(preFrame) && checkNone(nextFrame)) { return null }

    var keyFrame = new KeyFrame()
    keyFrame.timePoint = timePoint

    if (checkNone(nextFrame)) {
      playEnd = true
    }

    animatePos(keyFrame, preFrame, nextFrame)
    animateRotation(keyFrame, preFrame, nextFrame)
    animateScale(keyFrame, preFrame, nextFrame)
    animateOpacity(keyFrame, preFrame, nextFrame)
    animatePivotPos(keyFrame, preFrame, nextFrame)

    return keyFrame
  }

  this.getPreNextFrame = function (timePoint) {
    var preFrame = null
    var nextFrame = null
    for (var index = 0; index < scope.keyFrameList.length; ++index) {
      var curKeyFrame = scope.keyFrameList[index]

      if (timePoint > curKeyFrame.timePoint) {
        preFrame = curKeyFrame
      }

      if (timePoint < curKeyFrame.timePoint) {
        nextFrame = curKeyFrame
        break
      }
    }

    var res = {}
    res.preFrame = preFrame
    res.nextFrame = nextFrame

    return res
  }

  this.apply = function () {
    if (scope.keyFrameList.length > 0) {
      var keyFrame = scope.keyFrameList[0]

      var sceneObj = sceneManager.getObjBySid(scope.sid)
      if (!checkNone(sceneObj)) {
        keyFrame.applyToObj(sceneObj)
      }
    }
  }

  this.cancel = function () {
    var sceneObj = sceneManager.getObjBySid(scope.sid)
    var scene = sceneManager.getScene()

    if (checkNone(sceneObj) || !sceneObj.isMesh) { return false }

    if (!checkNone(pageManager.jsonModelChangeList)) {
      var jsonModelChange = pageManager.jsonModelChangeList[sceneObj.sid]
      if (!checkNone(jsonModelChange)) {
        var pos = jsonModelChange.position
        var quat = jsonModelChange.quaternion
        var scale = jsonModelChange.scale
        var pivotPos = jsonModelChange.pivotPos
        sceneObj.dataModel.position.x = pos.x
        sceneObj.dataModel.position.y = pos.y
        sceneObj.dataModel.position.z = pos.z
        sceneObj.dataModel.quaternion._x = quat._x
        sceneObj.dataModel.quaternion._y = quat._y
        sceneObj.dataModel.quaternion._z = quat._z
        sceneObj.dataModel.quaternion._w = quat._w
        sceneObj.dataModel.scale.x = scale.x
        sceneObj.dataModel.scale.y = scale.y
        sceneObj.dataModel.scale.z = scale.z
        sceneObj.dataModel.pivotPos.x = pivotPos.x
        sceneObj.dataModel.pivotPos.y = pivotPos.y
        sceneObj.dataModel.pivotPos.z = pivotPos.z

        scene.setObjOpacity(sceneObj, jsonModelChange.opacity)
      }
    }

    scope.removeAllKeyFrame()

    var index = sceneObj.disposeList.indexOf(scope)
    if (index > -1) { sceneObj.disposeList.splice(index, 1) }
    // sceneObj.position.x = scope.orgData.position.x
    // sceneObj.position.y = scope.orgData.position.y
    // sceneObj.position.z = scope.orgData.position.z
    // sceneObj.quaternion._x = scope.orgData.quaternion._x
    // sceneObj.quaternion._y = scope.orgData.quaternion._y
    // sceneObj.quaternion._z = scope.orgData.quaternion._z
    // sceneObj.quaternion._w = scope.orgData.quaternion._w
    // sceneObj.scale.x = scope.orgData.scale.x
    // sceneObj.scale.y = scope.orgData.scale.y
    // sceneObj.scale.z = scope.orgData.scale.z

    // scene.setObjOpacity(sceneObj, scope.orgData.opacity)
  }

  // Private methods ////////////////////////////////////////////////////
  function animatePos (keyFrame, preFrame, nextFrame) {
    if (checkNone(preFrame) && checkNone(nextFrame)) { return false }

    var prePos = null
    var nextPos = null
    var ratio = 1

    if (checkNone(preFrame)) {
      prePos = new THREE.Vector3(0, 0, 0)
      nextPos = nextFrame.position
      ratio = 1
    } else if (checkNone(nextFrame)) {
      prePos = preFrame.position
      nextPos = new THREE.Vector3(0, 0, 0)
      ratio = 0
    } else {
      prePos = preFrame.position
      nextPos = nextFrame.position
      ratio = (keyFrame.timePoint - preFrame.timePoint) / (nextFrame.timePoint - preFrame.timePoint)
    }

    keyFrame.position.x = prePos.x + ratio * (nextPos.x - prePos.x)
    keyFrame.position.y = prePos.y + ratio * (nextPos.y - prePos.y)
    keyFrame.position.z = prePos.z + ratio * (nextPos.z - prePos.z)

    return true
  }

  function animateRotation (keyFrame, preFrame, nextFrame) {
    if (checkNone(preFrame) && checkNone(nextFrame)) { return false }

    var preQuat = null
    var nextQuat = null
    var resQuat = null

    if (checkNone(preFrame)) {
      resQuat = nextFrame.quaternion.clone()
    } else if (checkNone(nextFrame)) {
      resQuat = preFrame.quaternion.clone()
    } else {
      var ratio = (keyFrame.timePoint - preFrame.timePoint) / (nextFrame.timePoint - preFrame.timePoint)
      preQuat = preFrame.quaternion
      nextQuat = nextFrame.quaternion
      resQuat = preQuat.clone()
      resQuat.slerp(nextQuat, ratio)
    }

    keyFrame.quaternion._x = resQuat._x
    keyFrame.quaternion._y = resQuat._y
    keyFrame.quaternion._z = resQuat._z
    keyFrame.quaternion._w = resQuat._w

    return true
  }

  function animateScale (keyFrame, preFrame, nextFrame) {
    if (checkNone(preFrame) && checkNone(nextFrame)) { return false }

    var preScale = null
    var nextScale = null
    var ratio = 1

    if (checkNone(preFrame)) {
      preScale = new THREE.Vector3(0, 0, 0)
      nextScale = nextFrame.scale
      ratio = 1
    } else if (checkNone(nextFrame)) {
      preScale = preFrame.scale
      nextScale = new THREE.Vector3(0, 0, 0)
      ratio = 0
    } else {
      preScale = preFrame.scale
      nextScale = nextFrame.scale
      ratio = (keyFrame.timePoint - preFrame.timePoint) / (nextFrame.timePoint - preFrame.timePoint)
    }

    keyFrame.scale.x = preScale.x + ratio * (nextScale.x - preScale.x)
    keyFrame.scale.y = preScale.y + ratio * (nextScale.y - preScale.y)
    keyFrame.scale.z = preScale.z + ratio * (nextScale.z - preScale.z)

    return true
  }

  function animateOpacity (keyFrame, preFrame, nextFrame) {
    if (checkNone(preFrame) && checkNone(nextFrame)) { return false }

    var preOpacity = null
    var nextOpacity = null
    var ratio = 1

    if (checkNone(preFrame)) {
      preOpacity = 0
      nextOpacity = nextFrame.opacity
      ratio = 1
    } else if (checkNone(nextFrame)) {
      preOpacity = preFrame.opacity
      nextOpacity = 0
      ratio = 0
    } else {
      // resOpacity = preFrame.opacity
      preOpacity = preFrame.opacity
      nextOpacity = nextFrame.opacity
      ratio = (keyFrame.timePoint - preFrame.timePoint) / (nextFrame.timePoint - preFrame.timePoint)
    }

    // keyFrame.opacity = resOpacity
    keyFrame.opacity = preOpacity + ratio * (nextOpacity - preOpacity)

    return true
  }

  function animatePivotPos (keyFrame, preFrame, nextFrame) {
    if (checkNone(preFrame) && checkNone(nextFrame)) { return false }

    var prePos = null
    var nextPos = null
    var ratio = 1

    if (checkNone(preFrame)) {
      prePos = new THREE.Vector3(0, 0, 0)
      nextPos = nextFrame.pivotPos
      ratio = 1
    } else if (checkNone(nextFrame)) {
      prePos = preFrame.pivotPos
      nextPos = new THREE.Vector3(0, 0, 0)
      ratio = 0
    } else {
      prePos = preFrame.pivotPos
      nextPos = nextFrame.pivotPos
      ratio = (keyFrame.timePoint - preFrame.timePoint) / (nextFrame.timePoint - preFrame.timePoint)
    }

    keyFrame.pivotPos.x = prePos.x + ratio * (nextPos.x - prePos.x)
    keyFrame.pivotPos.y = prePos.y + ratio * (nextPos.y - prePos.y)
    keyFrame.pivotPos.z = prePos.z + ratio * (nextPos.z - prePos.z)

    return true
  }

  function sortKeyFrame (fstFrame, sndFrame) {
    return fstFrame.timePoint - sndFrame.timePoint
  }

  // 检查kid是否已经被用，已经使用返回true，否则返回false
  function checkId (kid) {
    if (checkNone(kidList[kid])) {
      return false
    }
    return kidList[kid]
  }

  // 获取当前可用的最小id,如果为0,获取失败
  function getId (begin, end) {
    if (begin >= end) {
      return 0
    }

    for (var indexId = begin; indexId < end; ++indexId) {
      if (!checkId(indexId)) {
        return indexId
      }
    }
    return 0
  }
}

AnimationChange.prototype.clone = function () {
  var animaChange = new AnimationChange()
  animaChange.dataModel.copy(this.dataModel)
  return animaChange
}

AnimationChange.prototype.toJSON = function () {
  return this.dataModel
}

function KeyFrame (frameTimepoint) {
  // Parent constructor //////////////////////////////////////////////
  this.position = new THREE.Vector3(0, 0, 0)
  this.quaternion = new THREE.Quaternion()
  this.scale = new THREE.Vector3(0, 0, 0)
  this.pivotPos = new THREE.Vector3(0, 0, 0)

  // 构造数据模型
  DataModelFactory.call(this, DataModelType.KEYFRAME)

  var scope = this
  // Public variables ///////////////////////////////////////////////
  // transform
  // opacity
  // timePoint
  // kid

  // Private variables //////////////////////////////////////////////////
  var sceneManager = new SceneManager()

  // Public methods //////////////////////////////////////////////////////////
  this.setFromObj = function (sceneObj) {
    scope.position.x = sceneObj.dataModel.position.x
    scope.position.y = sceneObj.dataModel.position.y
    scope.position.z = sceneObj.dataModel.position.z
    scope.quaternion._x = sceneObj.dataModel.quaternion._x
    scope.quaternion._y = sceneObj.dataModel.quaternion._y
    scope.quaternion._z = sceneObj.dataModel.quaternion._z
    scope.quaternion._w = sceneObj.dataModel.quaternion._w
    scope.scale.x = sceneObj.dataModel.scale.x
    scope.scale.y = sceneObj.dataModel.scale.y
    scope.scale.z = sceneObj.dataModel.scale.z

    scope.pivotPos.x = sceneObj.dataModel.pivotPos.x
    scope.pivotPos.y = sceneObj.dataModel.pivotPos.y
    scope.pivotPos.z = sceneObj.dataModel.pivotPos.z

    scope.opacity = sceneObj.dataModel.opacity
  }

  this.applyToObj = function (sceneObj) {
    var setObj = sceneObj

    setObj.position.x = scope.pivotPos.x
    setObj.position.y = scope.pivotPos.y
    setObj.position.z = scope.pivotPos.z

    if (sceneObj.hasPivot) { setObj = sceneObj.group }
    setObj.position.x = scope.position.x
    setObj.position.y = scope.position.y
    setObj.position.z = scope.position.z

    setObj.quaternion._x = scope.quaternion._x
    setObj.quaternion._y = scope.quaternion._y
    setObj.quaternion._z = scope.quaternion._z
    setObj.quaternion._w = scope.quaternion._w

    // 改正旧代码存档用
    if (scope.scale.x < 0) { scope.scale.x *= -1 }
    if (scope.scale.y < 0) { scope.scale.y *= -1 }
    if (scope.scale.z < 0) { scope.scale.z *= -1 }

    setObj.scale.x = scope.scale.x
    setObj.scale.y = scope.scale.y
    setObj.scale.z = scope.scale.z

    var scene = sceneManager.getScene()
    scene.setObjOpacity(sceneObj, scope.opacity)
  }

  // Private methods ////////////////////////////////////////////////////
}

KeyFrame.prototype.clone = function () {
  var keyFrame = new KeyFrame()
  keyFrame.dataModel.copy(this.dataModel)
  return keyFrame
}

KeyFrame.prototype.toJSON = function () {
  return this.dataModel
}

export { Animation, AnimationChange, KeyFrame }



