function PageFactory (pageType) {
  switch (pageType) {
    case PageType.DEFAULT:
      return new Page(pageType)

    case PageType.LOADING:
      return new LoadingPage(pageType)

    case PageType.BASEFUNC:
      return new BaseFuncPage(pageType)

    case PageType.MATERIAL:
      return new MaterialPage(pageType)

    case PageType.MARKLINE:
      return new MarkLinePage(pageType)

    case PageType.ANIMATION:
      return new AnimationPage(pageType)

    case PageType.HOTPOINT:
      return new HotPointPage(pageType)

    case PageType.LINK:
      return new LinkPage(pageType)
  }
}

function Page (pageType) {
  // Parent constructor //////////////////////////////////////////////
  // 构造数据模型
  DataModelFactory.call(this, pageType)
  // console.log(this.dataModel)

  var scope = this

  // Public variables ////////////////////////////////////////////////

  // Private variables ////////////////////////////////////////////////
  var uiidList = {}

  var uiBeginId = 100
  var uiEndId = 1000000

  // Public methods ////////////////////////////////////////////////
  // 从单例背景获取设置的背景信息
  this.setBackground = function (background) {
    this.background.setFrom(background)
  }

  // 从页面读取背景信息来设置单例背景
  this.loadBackground = function (button) {
    var sceneManager = new SceneManager()
    var background = sceneManager.getBackground()
    if (checkNone(button)) { var backgroundData = this.background } else {
      if (button.background.type === TextureType.NONE) { var backgroundData = this.background } else { var backgroundData = button.background }
    }
    switch (backgroundData.type) {
      case TextureType.NONE:
        background.setColor('#ffffff', true)
        break
      // 纯颜色材质
      case TextureType.COLOR:
        background.setColor(backgroundData.color)
        break
      // 图片材质
      case TextureType.TEXTURE:
        background.setTexture(backgroundData.textureUrl)
        break
    }
  }

  // 添加图片UI
  this.addImageUI = function (uiid) {
    var imageUI = new ImageUI()

    if (checkNone(uiid)) { scope.addUIid(imageUI) } else { scope.setId(imageUI, uiid) }

    scope.imgUIList.push(imageUI)

    return imageUI
  }

  this.removeImageUI = function (uiid) {
    var uiList = scope.imgUIList

    for (var index = 0; index < uiList.length; ++index) {
      if (uiid === uiList[index].uiid) {
        scope.removeUIid(uiList[index])
        uiList.splice(index, 1)
        return true
      }
    }
    return false
  }

  this.copyImageUI = function (uiid) {
    var uiList = scope.imgUIList

    for (var index = 0; index < uiList.length; ++index) {
      if (uiid === uiList[index].uiid) {
        var ui = scope.addImageUI()
        ui.dataModel.setFrom(uiList[index].dataModel)
        return true
      }
    }
    return false
  }

  // 添加文本UI
  this.addTextUI = function (uiid) {
    var textUI = new TextUI()

    if (checkNone(uiid)) { scope.addUIid(textUI) } else { scope.setId(textUI, uiid) }

    scope.textUIList.push(textUI)

    console.log(scope.textUIList)

    return textUI
  }

  this.removeTextUI = function (uiid) {
    var uiList = scope.textUIList

    for (var index = 0; index < uiList.length; ++index) {
      if (uiid === uiList[index].uiid) {
        scope.removeUIid(uiList[index])
        uiList.splice(index, 1)
        return true
      }
    }

    return false
  }

  this.copyTextUI = function (uiid) {
    var uiList = scope.textUIList

    for (var index = 0; index < uiList.length; ++index) {
      if (uiid === uiList[index].uiid) {
        var ui = scope.addTextUI()
        ui.dataModel.setFrom(uiList[index].dataModel)
        return true
      }
    }
    return false
  }

  // 新添加具有数据模型的对象到列表中
  this.addUIid = function (uiObject) {
    var uiid = scope.getId(uiBeginId, uiEndId)

    if (uiid === 0) {
      console.log('PageManager, addModel(), uiid: "Failed to get page id."')
      return false
    }

    if (!checkNone(uiObject)) {
      uiObject.uiid = uiid
      // uiidList[uiid] = true
      uiidList[uiid] = uiObject
    }

    return true
  }

  // 从模型列表中删除对象的数据模型
  this.removeUIid = function (uiObject) {
    if (checkNone(uiObject)) {
      return
    }

    var uiid = null

    var uiid = uiObject.uiid
    if (scope.checkId(uiid)) {
      delete uiidList[uiid]
    }
  }

  // 设置对象的uiid
  this.setId = function (uiObject, uiid) {
    if (!scope.checkId(uiid)) {
      uiidList[uiid] = uiObject
      uiObject.uiid = uiid
    }
  }

  this.getUIById = function (uiid) {
    return uiidList[uiid]
    // var uiList = scope.imgUIList
    // for (var index = 0; index < uiList.length; ++index) {
    //   if (uiid === uiList[index].uiid) {
    //     return uiList[index]
    //   }
    // }

    // uiList = scope.textUIList

    // for (var index = 0; index < uiList.length; ++index) {
    //   if (uiid === uiList[index].uiid) {
    //     return uiList[index]
    //   }
    // }

    // var uiObject = null

    // if (this.pageType === PageType.HOTPOINT) {
    //   uiObject = scope.getHotPointUIById(uiid)
    // }

    // return uiObject
  }

  // 检查uiid是否已经被用，已经使用返回true，否则返回false
  this.checkId = function (uiid) {
    if (checkNone(uiidList[uiid])) {
      return false
    }
    return uiidList[uiid]
  }

  // 获取当前可用的最小id,如果为0,获取失败
  this.getId = function (begin, end) {
    if (begin >= end) {
      return 0
    }

    for (var indexId = begin; indexId < end; ++indexId) {
      if (!scope.checkId(indexId)) {
        return indexId
      }
    }
    return 0
  }

  // Private methods ////////////////////////////////////////////////
}

// 删除页面中的对象
Page.prototype.removeContent = function () {
  this.imgUIList.splice(0, this.imgUIList.length)
  this.textUIList.splice(0, this.textUIList.length)
}

Page.prototype.addPageSceneObj = function () {
  this.loadBackground()
}

Page.prototype.removePageSceneObj = function () {
}

/// /////////////////////////////////////////////////////////////////////////////

function CameraPage () {
  this.setPageCamera = function () {
    var sceneManager = new SceneManager()
    var camera = sceneManager.getScene().getCamera()
    var pageCamera = sceneManager.getPageCamera()

    pageCamera.copyFromCamera(camera)
    this.pageCamera.copy(pageCamera.dataModel)
  }

  this.loadPageCamera = function (changePage) {
    var sceneManager = new SceneManager()
    if (isEditor) {
      var pageCamera = sceneManager.getPageCamera()
      pageCamera.dataModel.copy(this.pageCamera)
      pageCamera.update()
    } else if (isBrowser) {
      var cameraControl = sceneManager.getCameraControl()

      if (changePage) { cameraControl.moveTo(this.pageCamera.target, this.pageCamera.position) } else {
        var camera = sceneManager.getScene().getCamera()
        camera.position.copy(this.pageCamera.position)
        camera.quaternion.copy(this.pageCamera.quaternion)
        camera.scale.copy(this.pageCamera.scale)

        cameraControl.target.copy(this.pageCamera.target)
      }
    }
  }
}

