function MaterialPage (pageType) {
  // Parent constructor //////////////////////////////////////////////
  Page.call(this, pageType)
  CameraPage.call(this)

  var scope = this

  // Public variables ////////////////////////////////////////////////
  // uiid
  // pageName
  // pageType
  // background

  // pageCamera
  // curMaterial
  // matBtnList

  // var mb1 = new MaterialButton()
  // mb1.btnName = 'btnOne'
  // mb1.btnType = 1
  // mb1.shapeType = 'square'
  // mb1.actShapeColor = '#0999ff'
  // mb1.defShapeColor = '#333333'
  // mb1.actTextureUrl = ''
  // mb1.defTextureUrl = ''
  // mb1.actTextColor = '#ffffff'
  // mb1.defTextColor = '#333333'
  // mb1.fontSize = 0
  // mb1.opacity = 0.5
  // mb1.text = 'LC'
  // scope.addUIid(mb1)

  // var mb2 = new MaterialButton()
  // mb2.btnName = 'btnTwo'
  // mb2.btnType = 1
  // mb2.shapeType = 'circle'
  // mb2.actShapeColor = '#33ff11'
  // mb2.defShapeColor = '#111111'
  // mb2.actTextureUrl = ''
  // mb2.defTextureUrl = ''
  // mb2.actTextColor = '#030303'
  // mb2.defTextColor = '#ffffff'
  // mb2.fontSize = 0
  // mb2.opacity = 0.8
  // mb2.text = '按钮2'
  // scope.addUIid(mb2)

  // this.matBtnList.push(mb1)
  // this.matBtnList.push(mb2)

  // Private variables ////////////////////////////////////////////////
  var curButton = null

  // Public methods ////////////////////////////////////////////////

  this.addButton = function (uiid) {
    var matBtn = new MaterialButton()
    if (checkNone(uiid)) { scope.addUIid(matBtn) } else { scope.setId(matBtn, uiid) }

    scope.matBtnList.push(matBtn)

    return matBtn
  }

  this.removeButton = function (uiid) {
    if (!checkNone(curButton) && curButton.uiid === uiid) {
      scope.removePageSceneObj()
    }

    for (var index = 0; index < scope.matBtnList.length; ++index) {
      var button = scope.matBtnList[index]
      if (uiid === button.uiid) {
        scope.removeUIid(button)
        if (!checkNone(curButton) && curButton.uiid === button.uiid) { button.removeAllMaterialChange(true) } else { button.removeAllMaterialChange() }
        scope.matBtnList.splice(index, 1)
        break
      }
    }
  }

  this.removeAllButton = function () {
    if (!checkNone(curButton)) {
      scope.removePageSceneObj()
    }

    for (var index = 0; index < scope.matBtnList.length; ++index) {
      var button = scope.matBtnList[index]
      scope.removeUIid(button)
      // 如果删除当前选择按钮，则要取消效果，否则不用
      if (!checkNone(curButton) && curButton.uiid === button.uiid) { button.removeAllMaterialChange(true) } else { button.removeAllMaterialChange() }
    }

    curButton = null

    scope.matBtnList.splice(0, scope.matBtnList.length)
  }

  this.setCurButton = function (uiid) {
    var foundButton = scope.getButtonById(uiid)

    if (!checkNone(foundButton)) {
      if (checkNone(curButton)) {
        var storageManager = new StorageManager()
        var pageManager = new PageManager()
        if (isEditor) { pageManager.jsonMaterialList = storageManager.saveSceneObjMaterial() }
      }

      curButton = foundButton

      curButton.curTimePoint = 0

      curButton.setCurMaterialChangeFromSelected()
      // for (var index = 0; index < curButton.materialChangeList; ++index) {
      //   var matChange = curButton.materialChangeList[index]
      //   matChange.apply()
      // }
      scope.applyButton()

      return true
    }
    if (!checkNone(curButton)) {
      var pageManager = new PageManager()
      if (isBrowser) { pageManager.jsonMaterialList = null }

      scope.cancelButton()

      if (isEditor) { pageManager.jsonMaterialList = null }
    }

    curButton = null
    return false
  }

  this.getCurButton = function () {
    return curButton
  }

  this.getButtonById = function (uiid) {
    for (var index = 0; index < scope.matBtnList.length; ++index) {
      if (uiid === scope.matBtnList[index].uiid) {
        return scope.matBtnList[index]
      }
    }
    return null
  }

  // 点击材质按钮时设置背景颜色
  this.setBackgroundColor = function (color, ifNone) {
    if (!checkNone(curButton)) {
      curButton.setBackgroundColor(color, ifNone)
    }
  }

  // 点击材质按钮时设置背景图片
  this.setBackgroundTexture = function (texUrl) {
    if (!checkNone(curButton)) {
      curButton.setBackgroundTexture(texUrl)
    }
  }
}

MaterialPage.prototype = Object.assign(Object.create(Page), {
  constructor: MaterialPage,

  // 删除页面中的对象
  removeContent: function () {
    Page.prototype.removeContent.call(this)

    this.removeAllButton()
  },

  addPageSceneObj: function () {
    Page.prototype.addPageSceneObj.call(this)
    if (isBrowser) {
      if (checkNone(this.notFirstIn)) {
        this.setCurButton(this.matBtnList[0].uiid)
        this.notFirstIn = true
      } else {
        this.setCurButton(engineInterface.getCurDisplayPage().pageContent._curSelectBtnId)
      }
    }

    var sceneManager = new SceneManager()
    if (this.isCameraLock) {
      sceneManager.lockCamera()
    }
    // var storageManager = new StorageManager()
    // var pageManager = new PageManager()
    // pageManager.jsonMaterialList = storageManager.saveSceneObjMaterial()
  },

  removePageSceneObj: function () {
    Page.prototype.removePageSceneObj.call(this)

    var pageManager = new PageManager()
    var sceneManager = new SceneManager()

    if (isBrowser) { pageManager.jsonMaterialList = null }

    if (isEditor) {
      this.setCurButton(-1)
      pageManager.jsonMaterialList = null
    }

    sceneManager.unlockCamera()
  },

  // 应用材质按钮的效果
  applyButton: function () {
    var button = this.getCurButton()
    if (!checkNone(button)) {
      var list = button.materialChangeList
      for (var index = 0; index < list.length; ++index) {
        var matChange = list[index]
        matChange.apply()
      }
      this.loadBackground(button)
    }
  },

  // 取消材质按钮设置的效果
  cancelButton: function () {
    var storageManager = new StorageManager()
    var pageManager = new PageManager()
    storageManager.loadSceneObjMaterial(pageManager.jsonMaterialList)

    this.loadBackground()
    // var sceneManager = new SceneManager()
    // var button = this.getCurButton()
    // var list = button.materialChangeList
    // for (var index = 0; index < list.length; ++index) {
    //   var matChange = list[index]
    //   matChange.cancel()
    // }
  }
})

/// /////////////////////////////////////////////////////////////////////////////

function MarkLinePage (pageType) {
  // Parent constructor //////////////////////////////////////////////
  Page.call(this, pageType)
  CameraPage.call(this)

  var scope = this

  // Public variables ////////////////////////////////////////////////
  // uiid
  // pageName
  // pageType
  // background

  // pageCamera

  // markLineList

  // Private variables ////////////////////////////////////////////////
  // var curMarkLine = null

  // Public methods ////////////////////////////////////////////////

  this.addMarkLine = function (sid) {
    var sceneManager = new SceneManager()

    var markLine = sceneManager.addMarkLine(null, null, null, null, sid)

    scope.markLineList[markLine.sid] = true

    return markLine
  }

  this.removeMarkLine = function (sid) {
    var sceneManager = new SceneManager()
    var markLine = sceneManager.getObjBySid(sid)

    if (checkNone(markLine)) { return false }

    sceneManager.removeObj(markLine)

    delete scope.markLineList[sid]

    return true
  }

  this.addAutoMarkLine = function () {
    var sceneManager = new SceneManager()

    var markLineList = sceneManager.addAutoMarkLine()

    markLineList.forEach(markLine => {
      scope.markLineList[markLine.sid] = true
    })

    return markLineList
  }

  // 用于读取，
  // 因为场景管理器负责删除场景对象，所以这里不用删除对象
  this.removeAllMarkLine = function (loading) {
    for (var sid in scope.markLineList) {
      if (!loading) { scope.removeMarkLine(sid) }
      delete scope.markLineList[sid]
    }
  }

  // this.setCurMarkLine = function (sid) {
  //   var sceneManager = new SceneManager()

  //   var markLine = sceneManager.getObjBySid(sid)

  //   if (checkNone(markLine)) {
  //     curMarkLine = null
  //   }

  //   curMarkLine = markLine

  // }

  // this.getCurMarkLine = function () {
  //   return curMarkLine
  // }

  this.getMarkLineById = function (sid) {
    var sceneManager = new SceneManager()
    return sceneManager.getObjBySid(sid)
  }
}

MarkLinePage.prototype = Object.assign(Object.create(Page), {
  constructor: MarkLinePage,

  // 删除页面中的对象
  removeContent: function (loading) {
    Page.prototype.removeContent.call(this)
    this.removeAllMarkLine(loading)
  },

  addPageSceneObj: function () {
    Page.prototype.addPageSceneObj.call(this)
    for (var sid in this.markLineList) {
      var markLine = this.getMarkLineById(sid)

      if (!checkNone(markLine) && !checkNone(markLine.show)) {
        markLine.show()
      }
    }
  },

  removePageSceneObj: function () {
    Page.prototype.removePageSceneObj.call(this)
    for (var sid in this.markLineList) {
      var markLine = this.getMarkLineById(sid)

      if (!checkNone(markLine) && !checkNone(markLine.hide)) {
        markLine.hide()
      }
    }
  }
})

