function AnimationPage (pageType) {
  // Parent constructor //////////////////////////////////////////////
  Page.call(this, pageType)
  CameraPage.call(this)

  var scope = this

  // Public variables ////////////////////////////////////////////////
  // uiid
  // pageName
  // pageType
  // background

  // pageCamera

  // Private variables ////////////////////////////////////////////////
  var curAnimation = null

  // Public methods ////////////////////////////////////////////////
  // 用于播放动画的更新
  // this.update = function () {
  //   if (!checkNone(curAnimation)) {
  //     curAnimation.update()
  //   }
  // }

  // this.playAnimation = function () {
  //   if (!checkNone(curAnimation)) {
  //     curAnimation.curTimePoint = 0
  //     curAnimation.playing = true
  //   }
  // }

  this.setAnimationCamera = function () {
    var sceneManager = new SceneManager()
    var pageCamera = sceneManager.getPageCamera()

    if (!checkNone(curAnimation)) {
      curAnimation.setCameraPos()

      pageCamera.position.copy(curAnimation.cameraPos)
      pageCamera.lookAt(curAnimation.cameraTarget)
      pageCamera.update()
    }
  }

  this.setAnimationMusic = function (musicUrl) {
    if (!checkNone(curAnimation)) {
      curAnimation.audio.src = musicUrl
      curAnimation.animationMusic = musicUrl
    }
  }

  this.setAnimaChangeLoop = function (bLoop) {
    if (!checkNone(curAnimation)) { curAnimation.setAnimaChangeLoop(bLoop) }
  }

  this.addAnimation = function (uiid) {
    var sceneManager = new SceneManager()
    var animation = new Animation(sceneManager.getCameraControl())
    if (checkNone(uiid)) { scope.addUIid(animation) } else { scope.setId(animation, uiid) }

    scope.animationList.push(animation)

    return animation
  }

  this.removeAnimation = function (uiid) {
    if (!checkNone(curAnimation) && curAnimation.uiid === uiid) {
      scope.removePageSceneObj()
    }

    for (var index = 0; index < scope.animationList.length; ++index) {
      var animation = scope.animationList[index]
      if (uiid === animation.uiid) {
        scope.removeUIid(animation)
        if (!checkNone(curAnimation) && curAnimation.uiid === animation.uiid) {
          animation.removeAllAnimationChange(true)
        } else {
          animation.removeAllAnimationChange()
        }
        scope.animationList.splice(index, 1)

        if (animation.audio.parentNode === document.body) { document.body.removeChild(animation.audio) }
        break
      }
    }
  }

  this.removeAllAnimation = function () {
    for (var index = 0; index < scope.animationList.length; ++index) {
      var animation = scope.animationList[index]
      scope.removeUIid(animation)

      if (animation.audio.parentNode === document.body) { document.body.removeChild(animation.audio) }

      if (!checkNone(curAnimation) && curAnimation.uiid === animation.uiid) {
        animation.removeAllAnimationChange(true)
      } else {
        animation.removeAllAnimationChange()
      }
    }

    curAnimation = null

    scope.animationList.splice(0, scope.animationList.length)
  }

  this.setCurButton = function (uiid) {
    var foundAnimation = scope.getAnimationById(uiid)

    var sceneManager = new SceneManager()
    var pageCamera = sceneManager.getPageCamera()

    if (!checkNone(foundAnimation)) {
      if (checkNone(curAnimation)) {
        var storageManager = new StorageManager()
        var pageManager = new PageManager()
        pageManager.jsonModelChangeList = storageManager.saveModelChange()
      }
      curAnimation = foundAnimation

      curAnimation.setCurAnimationChangeFromSelected()

      // curAnimation.curTimePoint = 0
      curAnimation.playing = true

      // if (isBrowser) {

      // }

      pageCamera.position.copy(curAnimation.cameraPos)
      pageCamera.lookAt(curAnimation.cameraTarget)
      pageCamera.update()

      var animationManager = new AnimationManager()
      // if (isEditor)
      //   animationManager.setPlayFrame(curAnimation.curTimePoint)
      // if (isBrowser) {
      curAnimation.curTimePoint = 0
      animationManager.setPlayFrame(0)
      // }

      curAnimation.onClick(function () {
        // if (isBrowser) {
        animationManager.setPlaying(true)
        // }
      })

      sceneManager.setPivotMode(false)

      scope.applyAnimation()

      return true
    }

    if (!checkNone(curAnimation)) {
      scope.cancelAnimation()
      var pageManager = new PageManager()
      pageManager.jsonModelChangeList = null

      pageCamera.position.copy(scope.pageCamera.position)
      pageCamera.lookAt(scope.pageCamera.target)

      pageCamera.update()
    }

    curAnimation = null
    return false
  }

  this.getCurButton = function () {
    return curAnimation
  }

  this.getAnimationById = function (uiid) {
    for (var index = 0; index < scope.animationList.length; ++index) {
      if (uiid === scope.animationList[index].uiid) {
        return scope.animationList[index]
      }
    }
    return null
  }
}

AnimationPage.prototype = Object.assign(Object.create(Page), {
  constructor: AnimationPage,

  // 删除页面中的对象
  removeContent: function () {
    Page.prototype.removeContent.call(this)
    this.removeAllAnimation()
  },

  addPageSceneObj: function () {
    Page.prototype.addPageSceneObj.call(this)
    // var storageManager = new StorageManager()
    // var pageManager = new PageManager()
    // pageManager.jsonModelChangeList = storageManager.saveModelChange()
  },

  removePageSceneObj: function () {
    Page.prototype.removePageSceneObj.call(this)
    this.setCurButton(-1)
  },

  // 应用动画按钮的效果
  applyAnimation: function () {
    var animation = this.getCurButton()
    var list = animation.changeList
    for (var index = 0; index < list.length; ++index) {
      var animaChange = list[index]
      animaChange.apply()
      animaChange.playKeyFrame(animation.curTimePoint)
    }
  },

  // 取消动画按钮设置的效果
  cancelAnimation: function () {
    var storageManager = new StorageManager()
    var pageManager = new PageManager()
    var animation = this.getCurButton()

    // console.log(pageManager.jsonModelChangeList)
    storageManager.loadModelChange(pageManager.jsonModelChangeList)
    animation.audio.pause()
    // var animation = this.getCurAnimation()
    // var list = animation.changeList
    // for (var index = 0; index < list.length; ++index) {
    //   var animaChange = list[index]
    //   animaChange.cancel()
    // }
  }
})

