function HotPointPage (pageType) {
  // Parent constructor //////////////////////////////////////////////
  Page.call(this, pageType)
  CameraPage.call(this)

  var scope = this

  // Public variables ////////////////////////////////////////////////
  // uiid
  // pageName
  // pageType
  // background

  // pageCamera

  // Private variables ////////////////////////////////////////////////
  // var curHotPoint = null

  // Public methods ////////////////////////////////////////////////
  this.setHotPointCamera = function () {
    var sceneManager = new SceneManager()
    var camera = sceneManager.getScene().getCamera()
    var pageCamera = sceneManager.getPageCamera()

    var curHotPoint = sceneManager.getCurSelected()

    if (!checkNone(curHotPoint)) {
      curHotPoint.setCameraPos()
      pageCamera.position.copy(curHotPoint.cameraPos)
      pageCamera.lookAt(curHotPoint.cameraTarget)
      pageCamera.update()
    }
  }

  this.addHotPoint = function (pos, sid) {
    var sceneManager = new SceneManager()

    var hotPoint = sceneManager.addHotPoint(pos, sid)

    scope.hotPointList[hotPoint.sid] = new HotPointUI()

    return hotPoint
  }

  this.removeHotPoint = function (sid) {
    var sceneManager = new SceneManager()
    var hotPoint = sceneManager.getObjBySid(sid)

    if (checkNone(hotPoint)) { return false }

    sceneManager.removeObj(hotPoint)

    scope.hotPointList[sid].remove()

    delete scope.hotPointList[sid]

    return true
  }

  // 用于读取，
  // 因为场景管理器负责删除场景对象，所以这里不用删除对象
  this.removeAllHotPoint = function (loading) {
    for (var sid in scope.hotPointList) {
      if (!loading) { scope.removeHotPoint(sid) }
      delete scope.hotPointList[sid]
    }
  }

  // this.setCurHotPoint = function (sid) {
  //   var sceneManager = new SceneManager()

  //   var hotPoint = sceneManager.getObjBySid(sid)

  //   if (checkNone(hotPoint)) {
  //     curHotPoint = null
  //   }

  //   curHotPoint = markLine

  // }

  // this.getCurHotPoint= function () {
  //   return curHotPoint
  // }

  this.getHotPointById = function (sid) {
    var sceneManager = new SceneManager()
    return sceneManager.getObjBySid(sid)
  }

  this.addHotPointHiddenUI = function (uiid, sid) {
    var curSid = null
    if (checkNone(sid)) {
      var curHotPoint = scope.getCurHotPoint()

      if (checkNone(curHotPoint)) { return null }

      curSid = curHotPoint.sid
    } else { curSid = sid }

    var hotPointUI = this.hotPointList[curSid]
    hotPointUI.hiddenPageUIList.push(uiid)
  }

  this.removeHotPointHiddenUI = function (sid) {
    var curSid = null
    if (checkNone(sid)) {
      var curHotPoint = scope.getCurHotPoint()

      if (checkNone(curHotPoint)) { return null }

      curSid = curHotPoint.sid
    } else { curSid = sid }

    var hotPointUI = this.hotPointList[curSid]
    var uiList = hotPointUI.hiddenPageUIList

    uiList.splice(0, uiList.length)
    return true
  }

  // 添加图片UI
  this.addHotPointImageUI = function (sid, uiid) {
    var curSid = null
    if (checkNone(sid)) {
      var curHotPoint = scope.getCurHotPoint()

      if (checkNone(curHotPoint)) { return null }

      curSid = curHotPoint.sid
    } else { curSid = sid }

    var imageUI = new ImageUI()

    if (checkNone(uiid)) { scope.addUIid(imageUI) } else { scope.setId(imageUI, uiid) }

    var hotPointUI = this.hotPointList[curSid]
    hotPointUI.hpImageUIList.push(imageUI)

    return imageUI
  }

  this.removeHotPointImageUI = function (sid, uiid) {
    var curSid = null
    if (checkNone(sid)) {
      var curHotPoint = scope.getCurHotPoint()

      if (checkNone(curHotPoint)) { return null }

      curSid = curHotPoint.sid
    } else { curSid = sid }

    var hotPointUI = this.hotPointList[curSid]
    var uiList = hotPointUI.hpImageUIList

    for (var index = 0; index < uiList.length; ++index) {
      if (uiid === uiList[index].uiid) {
        uiList.splice(index, 1)
        scope.removeUIid(uiList[index])
        return true
      }
    }
    return false
  }

  this.copyHotPointImageUI = function (sid, uiid) {
    var curSid = null
    if (checkNone(sid)) {
      var curHotPoint = scope.getCurHotPoint()

      if (checkNone(curHotPoint)) { return null }

      curSid = curHotPoint.sid
    } else { curSid = sid }

    var hotPointUI = this.hotPointList[curSid]
    var uiList = hotPointUI.hpImageUIList

    for (var index = 0; index < uiList.length; ++index) {
      if (uiid === uiList[index].uiid) {
        var ui = scope.addHotPointImageUI(curSid, null)
        ui.dataModel.setFrom(uiList[index].dataModel)
        return true
      }
    }
    return false
  }

  // 添加文本UI
  this.addHotPointTextUI = function (sid, uiid) {
    var curSid = null
    if (checkNone(sid)) {
      var curHotPoint = scope.getCurHotPoint()

      if (checkNone(curHotPoint)) { return null }

      curSid = curHotPoint.sid
    } else { curSid = sid }

    var textUI = new TextUI()

    if (checkNone(uiid)) { scope.addUIid(textUI) } else { scope.setId(textUI, uiid) }

    var hotPointUI = this.hotPointList[curSid]
    hotPointUI.hpTextUIList.push(textUI)

    return textUI
  }

  this.removeHotPointTextUI = function (sid, uiid) {
    var curSid = null
    if (checkNone(sid)) {
      var curHotPoint = scope.getCurHotPoint()

      if (checkNone(curHotPoint)) { return null }

      curSid = curHotPoint.sid
    } else { curSid = sid }

    var hotPointUI = this.hotPointList[curSid]
    var uiList = hotPointUI.hpTextUIList

    for (var index = 0; index < uiList.length; ++index) {
      if (uiid === uiList[index].uiid) {
        uiList.splice(index, 1)
        scope.removeUIid(uiList[index])
        return true
      }
    }

    return false
  }

  this.copyHotPointTextUI = function (sid, uiid) {
    var curSid = null
    if (checkNone(sid)) {
      var curHotPoint = scope.getCurHotPoint()

      if (checkNone(curHotPoint)) { return null }

      curSid = curHotPoint.sid
    } else { curSid = sid }

    var hotPointUI = this.hotPointList[curSid]
    var uiList = hotPointUI.hpTextUIList

    for (var index = 0; index < uiList.length; ++index) {
      if (uiid === uiList[index].uiid) {
        var ui = scope.addHotPointTextUI(curSid, null)
        ui.dataModel.setFrom(uiList[index].dataModel)
        return true
      }
    }
    return false
  }

  // 添加视频UI
  this.addHotPointVideoUI = function (sid, uiid) {
    var curSid = null
    if (checkNone(sid)) {
      var curHotPoint = scope.getCurHotPoint()

      if (checkNone(curHotPoint)) { return null }

      curSid = curHotPoint.sid
    } else { curSid = sid }

    var videoUI = new VideoUI()

    if (checkNone(uiid)) { scope.addUIid(videoUI) } else { scope.setId(videoUI, uiid) }

    var hotPointUI = this.hotPointList[curSid]
    hotPointUI.hpVideoUIList.push(videoUI)

    return videoUI
  }

  this.removeHotPointVideoUI = function (sid, uiid) {
    var curSid = null
    if (checkNone(sid)) {
      var curHotPoint = scope.getCurHotPoint()

      if (checkNone(curHotPoint)) { return null }

      curSid = curHotPoint.sid
    } else { curSid = sidl }

    var hotPointUI = this.hotPointList[curSid]
    var uiList = hotPointUI.hpVideoUIList

    for (var index = 0; index < uiList.length; ++index) {
      if (uiid === uiList[index].uiid) {
        uiList.splice(index, 1)
        scope.removeUIid(uiList[index])
        return true
      }
    }

    return false
  }

  this.copyHotPointVideoUI = function (sid, uiid) {
    var curSid = null
    if (checkNone(sid)) {
      var curHotPoint = scope.getCurHotPoint()

      if (checkNone(curHotPoint)) { return null }

      curSid = curHotPoint.sid
    } else { curSid = sid }

    var hotPointUI = this.hotPointList[curSid]
    var uiList = hotPointUI.hpVideoUIList

    for (var index = 0; index < uiList.length; ++index) {
      if (uiid === uiList[index].uiid) {
        var ui = scope.addHotPointVideoUI(curSid, null)
        ui.dataModel.setFrom(uiList[index].dataModel)
        return true
      }
    }
    return false
  }

  this.getCurHotPoint = function () {
    var sceneManager = new SceneManager()
    var curHotPoint = sceneManager.getCurSelected()

    if (checkNone(curHotPoint) || !curHotPoint.isHotPoint) { return null }

    return curHotPoint
  }

  this.getHotPointUIById = function (uiid) {
    for (var sid in scope.hotPointList) {
      var hotPointUI = scope.hotPointList[sid]
      var uiList = hotPointUI.hpImageUIList

      for (var index = 0; index < uiList.length; ++index) {
        if (uiid === uiList[index].uiid) {
          return uiList[index]
        }
      }

      uiList = hotPointUI.hpTextUIList

      for (var index = 0; index < uiList.length; ++index) {
        if (uiid === uiList[index].uiid) {
          return uiList[index]
        }
      }

      uiList = hotPointUI.hpVideoUIList

      for (var index = 0; index < uiList.length; ++index) {
        if (uiid === uiList[index].uiid) {
          return uiList[index]
        }
      }
    }

    return null
  }
}

HotPointPage.prototype = Object.assign(Object.create(Page), {
  constructor: HotPointPage,

  // 删除页面中的对象
  removeContent: function (loading) {
    Page.prototype.removeContent.call(this)
    this.removeAllHotPoint(loading)
  },

  addPageSceneObj: function () {
    Page.prototype.addPageSceneObj.call(this)

    for (var sid in this.hotPointList) {
      var hotPoint = this.getHotPointById(sid)

      if (!checkNone(hotPoint) && !checkNone(hotPoint.show)) {
        hotPoint.show()
      }
    }
  },

  removePageSceneObj: function () {
    Page.prototype.removePageSceneObj.call(this)
    for (var sid in this.hotPointList) {
      var hotPoint = this.getHotPointById(sid)

      if (!checkNone(hotPoint) && !checkNone(hotPoint.show)) {
        hotPoint.hide()
      }
    }
  }
})

