function LinkPage (pageType) {
  // Parent constructor //////////////////////////////////////////////
  Page.call(this, pageType)

  var scope = this

  // Public variables ////////////////////////////////////////////////
  // uiid
  // pageName
  // pageType
  // background

  // Public methods ////////////////////////////////////////////////

  // 添加图片UI
  this.addImageHyperLink = function (uiid) {
    var imageUI = new ImageHyperLinkUI()

    if (checkNone(uiid)) { scope.addUIid(imageUI) } else { scope.setId(imageUI, uiid) }

    scope.imgHyperLinkList.push(imageUI)

    return imageUI
  }

  this.removeImageHyperLink = function (uiid) {
    var uiList = scope.imgHyperLinkList

    for (var index = 0; index < uiList.length; ++index) {
      if (uiid === uiList[index].uiid) {
        scope.removeUIid(uiList[index])
        uiList.splice(index, 1)
        return true
      }
    }
    return false
  }

  this.copyImageHyperLink = function (uiid) {
    var uiList = scope.imgHyperLinkList

    for (var index = 0; index < uiList.length; ++index) {
      if (uiid === uiList[index].uiid) {
        var ui = scope.addImageHyperLink()
        ui.dataModel.setFrom(uiList[index].dataModel)
        return true
      }
    }
    return false
  }

  // 添加文本UI
  this.addButtonHyperLink = function (uiid) {
    var buttonUI = new ButtonHyperLinkUI()

    if (checkNone(uiid)) { scope.addUIid(buttonUI) } else { scope.setId(buttonUI, uiid) }

    scope.btnHyperLinkList.push(buttonUI)

    console.log(scope.btnHyperLinkList)

    return buttonUI
  }

  this.removeButtonHyperLink = function (uiid) {
    var uiList = scope.btnHyperLinkList

    for (var index = 0; index < uiList.length; ++index) {
      if (uiid === uiList[index].uiid) {
        scope.removeUIid(uiList[index])
        uiList.splice(index, 1)
        return true
      }
    }

    return false
  }

  this.copyButtonHyperLink = function (uiid) {
    var uiList = scope.btnHyperLinkList

    for (var index = 0; index < uiList.length; ++index) {
      if (uiid === uiList[index].uiid) {
        var ui = scope.addButtonHyperLink()
        ui.dataModel.setFrom(uiList[index].dataModel)
        return true
      }
    }
    return false
  }
}

LinkPage.prototype = Object.assign(Object.create(Page), {
  constructor: LinkPage,

  // 删除页面中的对象
  removeContent: function () {
    Page.prototype.removeContent.call(this)
  },

  addPageSceneObj: function () {
    Page.prototype.addPageSceneObj.call(this)
  },

  removePageSceneObj: function () {
    Page.prototype.removePageSceneObj.call(this)
  }
})

export { PageFactory, PageType }



