  // Public Method /////////////////////////////////////////////////////
  this.removeCurSelected = function (info) {
    scope.begindo()

    var pageManager = new PageManager()
    var sceneManager = new SceneManager()

    var curSelected = sceneManager.getCurSelected()

    if (checkNone(curSelected) || checkNone(curSelected.sid)) { return false }

    var sid = curSelected.sid

    if (curSelected.isMesh) {
      sceneManager.removeObj()
      scope.updateMeshList()
      scope.updatePageContent()
    } else if (curSelected.isLight) {
      sceneManager.removeObj()
      scope.updateLightList()
    } else if (curSelected.isMarkLine) {
      pageManager.removeMarkLine(sid)
      scope.updatePageContent()
    } else if (curSelected.isHotPoint) {
      pageManager.removeHotPoint(sid)
      scope.updatePageContent()
    }

    scope.enddo()
  }

  this.copyCurSelected = function (info) {
    scope.begindo()

    var pageManager = new PageManager()
    var sceneManager = new SceneManager()
    var storageManager = new StorageManager()
    var scene = sceneManager.getScene()

    var curSelected = sceneManager.getCurSelected()

    if (checkNone(curSelected) || checkNone(curSelected.sid)) { return false }

    var sid = curSelected.sid

    var sceneObj = storageManager.addSceneObj(curSelected.dataModel, null, scene)

    if (sceneObj.isMesh) {
      scope.updateMeshList()
      scope.updatePageContent()
    } else if (sceneObj.isLight) {
      // sceneManager.copyLight()
      scope.updateLightList()
    } else if (sceneObj.isMarkLine) {
      // pageManager.copyMarkLine()
      var curPage = pageManager.getCurPage()
      curPage.markLineList[sceneObj.sid] = true
      sceneObj.show()
      scope.updatePageContent()
    } else if (sceneObj.isHotPoint) {
      // var curPageData = scope.getCurPageData()
      // pageManager.copyHotPoint(sid)
      var curPage = pageManager.getCurPage()
      curPage.hotPointList[sceneObj.sid] = new HotPointUI()
      storageManager.loadHotPointUI(curPage.hotPointList[sid], sceneObj.sid, curPage)
      sceneObj.show()
      scope.updatePageContent()
    }

    scope.enddo()
  }

