  this.updatePageList = function () {
    var pageManager = new PageManager()

    var pageList = pageManager.pageList

    // 清空原列表
    scope.pageList.splice(0, scope.pageList.length)
    for (var index = 0; index < pageList.length; ++index) {
      var page = pageList[index]

      var pageData = new PageData(page.pageType, engine)

      pageData.update(page)

      scope.pageList.push(pageData)

      // console.log(scope.pageList)
    }

    if (checkNone(pageManager.getCurPage())) {
      scope._curPageId = -1
    }
  }

