  // 更新模型列表
  this.updateModelList = function () {
    var resourceManager = new ResourceManager()
    var modelList = resourceManager.modelList

    console.log(233434, modelList)

    // 清空原列表
    scope.modelList.splice(0, scope.modelList.length)
    for (var modelUrl in modelList) {
      for (var modelName in modelList[modelUrl]) {
        var model = modelList[modelUrl][modelName]
        if (!model.deleted) {
          var modelData = {}
          modelData.url = modelUrl
          modelData.name = modelName
          scope.modelList.push(modelData)
        }
      }
    }

    // console.log('modelList: ', scope.modelList)
  }

