      var hotPointList = curPageData.pageContent.hotPointList

      if (!checkNone(curHotPoint)) {
        for (var index = 0; index < hotPointList.length; ++index) {
          var displayHotPoint = hotPointList[index]

          if (displayHotPoint.hotPointData.id === curHotPoint.sid) {
            var pageHotPointUI = curPage.hotPointList[curHotPoint.sid]
            displayHotPoint.hotPointUI.update(pageHotPointUI)

            break
          }
        }
      }

      // if (!checkNone(curHotPoint)) {
