  // 添加光源 0:平行光/ 1:点光源/ 2:聚光灯 3:矩形光
  this.addLight = function (type) {
    scope.begindo()

    var sceneManager = new SceneManager()
    switch (type) {
      case 0:
        sceneManager.addLight('Direction')
        break
      case 1:
        sceneManager.addLight('Point')
        break
      case 2:
        sceneManager.addLight('Spot')
        break
      case 3:
        sceneManager.addLight('RectArea')
        break
    }
    scope.updateLightList()

    scope.enddo()
  }

