  this.removeUIs = function (uiList) {
    scope.begindo()

    var pageManager = new PageManager()
    console.log(uiList)
    uiList.forEach(ui => {
      var uiid = ui._id

      if (ui instanceof HotPointImageUIData) {
        pageManager.removeHotPointImageUI(uiid)
      } else if (ui instanceof HotPointTextUIData) {
        pageManager.removeHotPointTextUI(uiid)
      } else if (ui instanceof HotPointVideoUIData) {
        pageManager.removeHotPointVideoUI(uiid)
      } else if (ui instanceof ButtonHyperLinkData) {
        pageManager.removeButtonHyperLink(uiid)
      } else if (ui instanceof ImageHyperLinkData) {
        pageManager.removeImageHyperLink(uiid)
      } else if (ui instanceof ImageUIData) {
        pageManager.removeImageUI(uiid)
      } else if (ui instanceof TextUIData) {
        pageManager.removeTextUI(uiid)
      }
    })
    scope.updatePageUI()
    var curPage = pageManager.getCurPage()
    if (!checkNone(curPage)) {
      if (curPage.pageType === PageType.HOTPOINT) {
        scope.updateHotPointUIList()
      } else if (curPage.pageType === PageType.LINK) {
        scope.updateHyperLinkList()
      }
    }

    scope.enddo()
  }

  this.copyUIs = function (uiList) {
    scope.begindo()

    var pageManager = new PageManager()
    console.log(uiList)
    uiList.forEach(ui => {
      var uiid = ui._id

      if (ui instanceof HotPointImageUIData) {
        pageManager.copyHotPointImageUI(uiid)
      } else if (ui instanceof HotPointTextUIData) {
        pageManager.copyHotPointTextUI(uiid)
      } else if (ui instanceof HotPointVideoUIData) {
        pageManager.copyHotPointVideoUI(uiid)
      } else if (ui instanceof ButtonHyperLinkData) {
        pageManager.copyButtonHyperLink(uiid)
      } else if (ui instanceof ImageHyperLinkData) {
        pageManager.copyImageHyperLink(uiid)
      } else if (ui instanceof ImageUIData) {
        pageManager.copyImageUI(uiid)
      } else if (ui instanceof TextUIData) {
        pageManager.copyTextUI(uiid)
      }
    })
    scope.updatePageUI()
    var curPage = pageManager.getCurPage()
    if (!checkNone(curPage)) {
      if (curPage.pageType === PageType.HOTPOINT) {
        scope.updateHotPointUIList()
      } else if (curPage.pageType === PageType.LINK) {
        scope.updateHyperLinkList()
      }
    }

    scope.enddo()
  }

