function TransformData () {
  var scope = this

  this.position = { _x: 0, _y: 0, _z: 0 }
  this.rotation = { _x: 0, _y: 0, _z: 0 }
  this.scale = { _x: 0, _y: 0, _z: 0 }

  this.position.setFunction = function () {
    engineInterface.begindo()

    console.log('set position')
    var sceneManager = new SceneManager()
    sceneManager.setObjPos(scope.position._x, scope.position._y, scope.position._z)

    engineInterface.enddo()
  }

  this.rotation.setFunction = function () {
    engineInterface.begindo()

    var sceneManager = new SceneManager()
    engineInterface.stopUpdate = true

    sceneManager.setObjRotate(
      scope.rotation._x,
      scope.rotation._y,
      scope.rotation._z)

    engineInterface.enddo()
  }

  this.scale.setFunction = function () {
    engineInterface.begindo()

    console.log('scale set')
    var sceneManager = new SceneManager()
    sceneManager.setObjScale(scope.scale._x, scope.scale._y, scope.scale._z)

    engineInterface.enddo()
  }

  bindFunc(this, this.position, bindTransform, 'position', 'x')
  bindFunc(this, this.position, bindTransform, 'position', 'y')
  bindFunc(this, this.position, bindTransform, 'position', 'z')

  bindFunc(this, this.rotation, bindTransform, 'rotation', 'x')
  bindFunc(this, this.rotation, bindTransform, 'rotation', 'y')
  bindFunc(this, this.rotation, bindTransform, 'rotation', 'z')

  bindFunc(this, this.scale, bindTransform, 'scale', 'x')
  bindFunc(this, this.scale, bindTransform, 'scale', 'y')
  bindFunc(this, this.scale, bindTransform, 'scale', 'z')
}

TransformData.prototype.update = function (sceneObj) {
  var sceneManager = new SceneManager()
  var dataObj = sceneObj

  if (checkNone(sceneObj)) {
    if (checkNone(sceneManager.getCurSelected())) {
      return
    }
    dataObj = sceneManager.getCurSelected()
    // console.log(dataModel.position);
  }

  sceneManager.getTransformControl().update()

  if (dataObj.hasPivot) {
    this.position._x = dataObj.group.position.x
    this.position._y = dataObj.group.position.y
    this.position._z = dataObj.group.position.z

    if (!engineInterface.stopUpdate) {
      var euler = new THREE.Euler()
      euler.setFromQuaternion(dataObj.group.quaternion)
      this.rotation._x = euler.x
      this.rotation._y = euler.y
      this.rotation._z = euler.z
    }

    this.scale._x = dataObj.group.scale.x
    this.scale._y = dataObj.group.scale.y
    this.scale._z = dataObj.group.scale.z
  } else {
    this.position._x = dataObj.position.x
    this.position._y = dataObj.position.y
    this.position._z = dataObj.position.z

    if (!engineInterface.stopUpdate) {
      var euler = new THREE.Euler()
      euler.setFromQuaternion(dataObj.quaternion)
      this.rotation._x = euler.x
      this.rotation._y = euler.y
      this.rotation._z = euler.z
    }

    this.scale._x = dataObj.scale.x
    this.scale._y = dataObj.scale.y
    this.scale._z = dataObj.scale.z
  }
}

