function CurMeshData (engine) {
  SceneObjData.call(this, engine)

  this.material = new CurMaterialData()

  this.numOfPoints = 0
  this.numOfLines = 0
  this.numOfFaces = 0

  this._pivotMode = false
  this.pivotPos = { _x: 0, _y: 0, _z: 0 }

  var scope = this

  this.pivotPos.setFunction = function () {
    engineInterface.begindo()

    console.log('set pivot pos')
    var sceneManager = new SceneManager()
    sceneManager.setObjPivotPos(
      scope.pivotPos._x,
      scope.pivotPos._y,
      scope.pivotPos._z)

    engineInterface.enddo()
  }

  bindFunc(this, bindValue, 'pivotMode')
  bindFunc(this, this.pivotPos, bindTransform, 'pivotPos', 'x')
  bindFunc(this, this.pivotPos, bindTransform, 'pivotPos', 'y')
  bindFunc(this, this.pivotPos, bindTransform, 'pivotPos', 'z')

  this.engine = engine
}

CurMeshData.prototype = Object.assign(Object.create(SceneObjData.prototype), {
  constructor: CurMeshData,

  setFunction: function () {
    engineInterface.begindo()

    var sceneManager = new SceneManager()

    SceneObjData.prototype.setFunction.call(this)

    sceneManager.setPivotMode(this._pivotMode)

    // 更新名字
    engineInterface.updateMeshList()
    // dataInterface.testupdateMeshList();

    engineInterface.enddo()
  },

  update: function () {
    var sceneManager = new SceneManager()
    var sceneObj = sceneManager.getCurSelected()

    if (checkNone(sceneObj) || !sceneObj.isMesh) { return }

    SceneObjData.prototype.update.call(this)

    var geometry = sceneObj.geometry

    this._name = sceneObj.name

    if (geometry.isBufferGeometry) {
      var attributes = geometry.attributes
      var faceNum = attributes.position.count / 3

      this.numOfFaces = faceNum
      this.numOfPoints = faceNum / 2
      this.numOfLines = faceNum * 3
    } else if (geometry.isGeometry) {
      this.numOfFaces = geometry.faces.length
      this.numOfPoints = geometry.vertices.length
      this.numOfLines = this.numOfFaces * 3
    }

    this.material.update(sceneObj)

    this._pivotMode = sceneManager.getTransformControl().pivotMode
    this.pivotPos._x = sceneObj.group.position.x
    this.pivotPos._y = sceneObj.group.position.y
    this.pivotPos._z = sceneObj.group.position.z
  }
})

/// ///////////////////////////////////////////////////////////////////////

function CurLightData () {
  SceneObjData.call(this)

  // 添加光源 0:平行光/ 1:点光源/ 2:聚光灯 3:矩形光
  this._type = -1
  this._color = '#ffffff'
  this._intensity = 30
  this._decay = 2

  this._shadowBlur = 1
  this._distance = 10
  this._angle = 60

  this._width = 10
  this._height = 10
  this._castShadow = false

  bindFunc(this, bindValue, 'type')
  bindFunc(this, bindValue, 'color')
  bindFunc(this, bindValue, 'intensity')
  bindFunc(this, bindValue, 'decay')

  bindFunc(this, bindValue, 'shadowBlur')
  bindFunc(this, bindValue, 'distance')
  bindFunc(this, bindValue, 'angle')

  bindFunc(this, bindValue, 'width')
  bindFunc(this, bindValue, 'height')
  bindFunc(this, bindValue, 'castShadow')
}

CurLightData.prototype = Object.assign(Object.create(SceneObjData.prototype), {
  constructor: CurLightData,

  setFunction: function () {
    engineInterface.begindo()

    SceneObjData.prototype.setFunction.call(this)

    var sceneManager = new SceneManager()

    // 更新共有属性
    sceneManager.setLightColor(this._color)
    sceneManager.setLightIntensity(this._intensity / 30)
    // sceneManager.setLightIntensity(this._intensity)
    if (checkNone(sceneManager.getCurSelected())) {
      return
    }
    var sceneObj = sceneManager.getCurSelected()

    // 更新私有属性
    switch (this._type) {
      // 平行光
      case 0:
        sceneManager.setLightIntensity(this._intensity / 30 * 2)
        break

        // 点光源
      case 1:
        sceneManager.setLightIntensity(this._intensity / 30 * 2)
        sceneObj.distance = this._distance / 10
        break

        // 聚光灯
      case 2:
        sceneManager.setLightIntensity(this._intensity / 30 * 10)
        sceneObj.distance = this._distance / 10
        sceneObj.angle = this._angle / 180 * Math.PI
        break

        // 矩形光
      case 3:
        sceneManager.setLightIntensity(this._intensity * 2)
        if (this._width === 0) { this._width = 0.01 }
        if (this._height === 0) { this._height = 0.01 }

        sceneObj.width = this._width
        sceneObj.height = this._height
        break
    }

    sceneManager.setLightCastShadow(this._castShadow)

    sceneObj.update()

    // 更新名字
    engineInterface.updateLightList()

    engineInterface.enddo()
  },

  update: function () {
    var sceneManager = new SceneManager()
    var sceneObj = sceneManager.getCurSelected()
    if (checkNone(sceneObj) || !sceneObj.isLight) {
      return
    }
    var sceneObj = sceneManager.getCurSelected()

    SceneObjData.prototype.update.call(this)

    if (sceneObj.isPointLight) {
      this._type = 1
      this._distance = sceneObj.distance * 10
    } else if (sceneObj.isSpotLight) {
      this._type = 2
      this._distance = sceneObj.distance * 10
      this._angle = sceneObj.angle * 180 / Math.PI
    } else if (sceneObj.isDirectionalLight) {
      this._type = 0
    } else if (sceneObj.isRectAreaLight) {
      this._type = 3
      this._width = sceneObj.width
      this._height = sceneObj.height
    }

    this._color = '#' + sceneObj.color.getHexString()
    this._intensity = sceneObj.intensity * 30
    // this._intensity = sceneObj.intensity
    if (sceneObj.isDirectionalLight) {
      this._intensity /= 2
    }
    if (sceneObj.isPointLight) {
      this._intensity /= 2
    }
    if (sceneObj.isSpotLight) {
      this._intensity /= 10
    }
    if (sceneObj.isRectAreaLight) {
      this._intensity = sceneObj.intensity / 2
    }

    this._castShadow = sceneObj.castShadow
  }
})

