function CurAniMeshData (engine) {
  TransformData.call(this, engine)

  var scope = this

  this.position.setFunction = function () {
    engineInterface.begindo()

    console.log('set animation position')
    var pageManager = new PageManager()
    var sceneManager = new SceneManager()

    sceneManager.setObjPos(scope.position._x, scope.position._y, scope.position._z)

    var curAnimaChange = pageManager.getCurAnimaChange()
    var curKeyFrame = pageManager.getCurKeyFrame()
    if (!checkNone(curKeyFrame)) {
      var sceneObj = sceneManager.getObjBySid(curAnimaChange.sid)
      if (!checkNone(sceneObj) && sceneObj.isMesh) {
        curKeyFrame.setFromObj(sceneObj)
      }
    }

    engineInterface.enddo()
  }

  this.rotation.setFunction = function () {
    engineInterface.begindo()

    var sceneManager = new SceneManager()
    engineInterface.stopUpdate = true

    var pageManager = new PageManager()

    var curAnimaChange = pageManager.getCurAnimaChange()
    var curKeyFrame = pageManager.getCurKeyFrame()

    sceneManager.setObjRotate(
      scope.rotation._x,
      scope.rotation._y,
      scope.rotation._z)

    if (!checkNone(curKeyFrame)) {
      var sceneObj = sceneManager.getObjBySid(curAnimaChange.sid)
      if (!checkNone(sceneObj) && sceneObj.isMesh) {
        curKeyFrame.setFromObj(sceneObj)
      }
    }

    engineInterface.enddo()
  }

  this.scale.setFunction = function () {
    engineInterface.begindo()

    console.log('scale animation set')

    var pageManager = new PageManager()
    var sceneManager = new SceneManager()

    sceneManager.setObjScale(scope.scale._x, scope.scale._y, scope.scale._z)

    var curAnimaChange = pageManager.getCurAnimaChange()
    var curKeyFrame = pageManager.getCurKeyFrame()
    if (!checkNone(curKeyFrame)) {
      var sceneObj = sceneManager.getObjBySid(curAnimaChange.sid)
      if (!checkNone(sceneObj) && sceneObj.isMesh) {
        curKeyFrame.setFromObj(sceneObj)
      }
    }

    engineInterface.enddo()
  }

  this._opacity = 1.0

  bindFunc(this, bindValue, 'opacity')
}

CurAniMeshData.prototype = Object.assign(Object.create(TransformData.prototype), {
  constructor: CurAniMeshData,

  setFunction: function () {
    engineInterface.begindo()

    var pageManager = new PageManager()
    var sceneManager = new SceneManager()

    sceneManager.setObjOpacity(this._opacity)

    var curAnimaChange = pageManager.getCurAnimaChange()
    var curKeyFrame = pageManager.getCurKeyFrame()
    if (!checkNone(curKeyFrame)) {
      var sceneObj = sceneManager.getObjBySid(curAnimaChange.sid)
      if (!checkNone(sceneObj) && sceneObj.isMesh) {
        curKeyFrame.setFromObj(sceneObj)
      }
    }

    engineInterface.enddo()
  },

  update: function () {
    var pageManager = new PageManager()
    var sceneManager = new SceneManager()
    var curAnimaChange = pageManager.getCurAnimaChange()

    if (checkNone(curAnimaChange)) { return }

    var sceneObj = sceneManager.getObjBySid(curAnimaChange.sid)

    engineInterface.stopUpdate = false

    sceneObj.rotation.setFromQuaternion(sceneObj.quaternion)

    TransformData.prototype.update.call(this, sceneObj)

    if (!checkNone(sceneObj)) {
      this._opacity = sceneObj.dataModel.opacity
    }
  }
})

