/// ///////////////////////////////////////////////////////////////////////////

function SceneData () {
  var scope = this

  this._fov = 75
  this._envMapId = 0
  this._blur = 0
  this._ambientIntensity = 0.5
  this._hemisphereIntensity = 0.5
  this._bkgMusicSrc = ''

  this.lastFov = 0
  this.lastEnvMapId = 0
  this.lastBlur = 0
  this.lastAmbientIntensity = 0
  this.lastHemisphereIntensity = 0
  this.lastBkgMusicSrc = ''

  this.setFunction = function () {
    engineInterface.begindo()

    var sceneManager = new SceneManager()
    if (this._fov !== this.lastFov) {
      sceneManager.setCameraFov(this._fov)
      this.lastFov = this._fov
    }

    if (this._envMapId !== this.lastEnvMapId) {
      sceneManager.setEnv(this._envMapId, updateEnv)
      this.lastEnvMapId = this._envMapId
    }
    // if (this._envMapId !== this.lastEnvMapId) {
    //   switch (this._envMapId) {
    //     case 0:
    //       sceneManager.loadEnv('')
    //       break
    //     case 1:
    //       sceneManager.loadEnv('static/default/images/摄影棚.jpg', updateEnv)
    //       break
    //     case 2:
    //       sceneManager.loadEnv('static/default/images/房间.jpg', updateEnv)
    //       break
    //     case 3:
    //       sceneManager.loadEnv('static/default/images/室外.jpg', updateEnv)
    //       break
    //     case 4:
    //       sceneManager.loadEnv('static/default/images/阴天.jpg', updateEnv)
    //       break
    //   }
    //   this.lastEnvMapId = this._envMapId
    // }

    if (this._blur !== this.lastBlur) {
      sceneManager.setEnvBlur(this._blur * 20)
      this.lastBlur = this._blur
    }

    if (this._ambientIntensity !== this.lastAmbientIntensity) {
      sceneManager.setAmbientIntensity(this._ambientIntensity / 100)
      this.lastAmbientIntensity = this._ambientIntensity
    }

    if (this._hemisphereIntensity !== this.lastHemisphereIntensity) {
      sceneManager.setHemisphereIntensity(this._hemisphereIntensity)
      this.lastHemisphereIntensity = this._hemisphereIntensity
    }

    if (this._bkgMusicSrc !== this.lastBkgMusicSrc) {
      sceneManager.getScene().bkgMusicSrc = this._bkgMusicSrc
      this.lastBkgMusicSrc = this._bkgMusicSrc
    }

    engineInterface.enddo()
  }

  function updateEnv () {
    var sceneManager = new SceneManager()
    sceneManager.setEnvBlur(scope._blur * 20)
  }

  this.update = function () {
    var sceneManager = new SceneManager()
    var scene = sceneManager.getScene()
    this._fov = scene.getCamera().fov
    this._envMapId = scene.dataModel.envIdx
    this._blur = scene.dataModel.blur / 20
    this._ambientIntensity = scene.dataModel.ambientIntensity * 100
    this._hemisphereIntensity = scene.dataModel.hemisphereIntensity
    this._bkgMusicSrc = scene.dataModel.bkgMusicSrc

    this.lastFov = this._fov
    this.lastEnvMapId = this._envMapId
    this.lastBlur = this._blur
    this.lastAmbientIntensity = this._ambientIntensity
    this.lastHemisphereIntensity = this._hemisphereIntensity
    this.lastBkgMusicSrc = this._bkgMusicSrc
  }

  bindFunc(this, bindValue, 'fov')
  bindFunc(this, bindValue, 'envMapId')
  bindFunc(this, bindValue, 'blur')
  bindFunc(this, bindValue, 'ambientIntensity')
  bindFunc(this, bindValue, 'hemisphereIntensity')
  bindFunc(this, bindValue, 'bkgMusicSrc')
}

