function BackgroundData (engine, ifButton) {
  this.engine = engine

  // 是否是材质按钮设置背景
  // true即为按钮的设置背景，false为页面的设置背景
  this.ifButton = ifButton

  this._bkgType = TextureType.COLOR
  this._bkgColor = '#ffffff'
  this._bkgTex = ''

  bindFunc(this, bindValue, 'bkgType')
  bindFunc(this, bindValue, 'bkgColor')
  bindFunc(this, bindValue, 'bkgTex')
}

BackgroundData.prototype.setFunction = function () {
  engineInterface.begindo()
  // console.log(this.engine)
  var sceneManager = new SceneManager()
  var resourceManager = new ResourceManager()
  var projectManager = new ProjectManager()
  var pageManager = new PageManager()
  var curPage = pageManager.getCurPage()
  var curProject = projectManager.curProject

  switch (this._bkgType) {
    case TextureType.NONE:
      if (checkNone(this.ifButton)) {
        sceneManager.setBackgroundColor('#ffffff', true)
        if (!checkNone(curProject) && !checkNone(curPage) && curPage.pageType === PageType.LOADING) {
          curProject.cover = 'static/default/images/project.png'
        }
      }
      // 材质按钮设置背景功能调用
      else if (!checkNone(curPage)) { curPage.setBackgroundColor('#ffffff', true) }

      break
    // 纯颜色材质
    case TextureType.COLOR:
      if (checkNone(this.ifButton)) {
        sceneManager.setBackgroundColor(this._bkgColor)
        if (!checkNone(curProject) && !checkNone(curPage) && curPage.pageType === PageType.LOADING) {
          curProject.cover = 'static/default/images/project.png'
        }
      }
      // 材质按钮设置背景功能调用
      else if (!checkNone(curPage)) { curPage.setBackgroundColor(this._bkgColor) }

      break
    // 图片材质
    case TextureType.TEXTURE:
      if (checkNone(this.ifButton)) {
        sceneManager.setBackgroundTexture(resourceManager.getImage(this._bkgTex))
        if (!checkNone(curProject) && !checkNone(curPage) && curPage.pageType === PageType.LOADING) {
          curProject.cover = this._bkgTex
        }
      }
      // 材质按钮设置背景功能调用
      else if (!checkNone(curPage)) { curPage.setBackgroundTexture(resourceManager.getImage(this._bkgTex)) }

      break
  }

  engineInterface.enddo()
}

BackgroundData.prototype.update = function (page) {
  var resourceManager = new ResourceManager()
  var pageDataModel = page.dataModel
  var background = pageDataModel.background

  this._bkgType = background.type
  this._bkgColor = background.color
  this._bkgTex = resourceManager.getImage(background.textureUrl)

  // var material = sceneObj.material
  // if (checkNone(material.map)) {
  //   this._type = 0
  // } else {
  //   this._type = 1
  // }

  // this._matColor = '#' + material.color.getHexString()
  // this._curSelectTex = sceneObj.dataModel.textureUrl
}

