import { DataModelFactory, DataModelType } from '../core/DataModel.js'
import { SceneManager } from '../managers/SceneManager.js'
import { PageManager } from '../managers/PageManager.js'
import { PageType, TextureType } from '../core/Type.js'

function MaterialChangeData (matButton) {
  // Parent constructor //////////////////////////////////////////////
  // 构造数据模型
  DataModelFactory.call(this, DataModelType.MATERIAL_CHANGE)

  var scope = this

  // Private variables //////////////////////////////////////////////
  var sceneManager = new SceneManager()
  var pageManager = new PageManager()

  // this.sid = 0
  // this.material = new TextureDataModel()

  this.parent = matButton

  // Public methods ///////////////////////////////////////////////////////
  this.apply = function () {
    var scene = sceneManager.getScene()
    var sceneObj = sceneManager.getObjBySid(scope.sid)
    switch (scope.material.type) {
      case TextureType.COLOR:
        sceneManager.setColor(scope.material.color, sceneObj)
        break
      case TextureType.TEXTURE:
        sceneManager.setTexture(scope.material.textureUrl, sceneObj)
        break
    }

    sceneManager.setUVTranslate(scope.uvData.tx, scope.uvData.ty, sceneObj)
    sceneManager.setUVScale(scope.uvData.sx, scope.uvData.sy, sceneObj)
  }

  this.cancel = function () {
    var sceneObj = sceneManager.getObjBySid(scope.sid)
    if (!checkNone(pageManager.jsonMaterialList)) {
      var jsonMaterial = pageManager.jsonMaterialList[scope.sid]
      if (!checkNone(jsonMaterial)) {
        if (checkNone(sceneObj) || !sceneObj.isMesh) { return false }

        switch (jsonMaterial.type) {
          case TextureType.COLOR:
            sceneManager.setColor(jsonMaterial.color, sceneObj)
            break
          case TextureType.TEXTURE:
            sceneManager.setTexture(jsonMaterial.textureUrl, sceneObj)
            break
        }

        var uvData = jsonMaterial.uvData
        sceneManager.setUVTranslate(uvData.tx, uvData.ty, sceneObj)
        sceneManager.setUVScale(uvData.sx, uvData.sy, sceneObj)
      }
    }

    if (!checkNone(sceneObj)) {
      var index = sceneObj.disposeList.indexOf(scope)
      if (index > -1) { sceneObj.disposeList.splice(index, 1) }
    }
  }

  this.removeSelf = function () {
    scope.parent.removeMaterialChangeByObj(scope)
  }
}

MaterialChangeData.prototype.clone = function () {
  var matChange = new MaterialChangeData()
  matChange.dataModel.copy(this)

  return matChange
}

MaterialChangeData.prototype.toJSON = function () {
  return this.dataModel
}

export { MaterialChangeData }



