import { engineInterface } from '../core/EngineInterface.js'

import { ProjectManager } from '../managers/ProjectManager.js'
import { StorageManager } from '../managers/StorageManager.js'
import { SceneManager } from '../managers/SceneManager.js'
import { Mesh } from '../objects/Mesh.js'
import { OBJLoader2 } from '../loaders/OBJLoader2.js'
import {} from '../loaders/FBXLoader.js'
import {} from '../exporters/OBJExporter.js'
import {} from '../loaders/MTLLoader.js'
import {} from '../libs/inflate.min.js'

function ResourceManager () {
  // Singleton pattern
  if (ResourceManager._instance !== undefined) {
    return ResourceManager._instance
  }
  ResourceManager._instance = this
  // Public variables //////////////////////////////////////////////////
  // 模型资源列表
  var modelList = {}
  this.modelList = modelList

  // 图片资源列表
  var imageList = {}
  this.imageList = imageList

  // rid分配列表
  var ridList = {}

  // rid范围
  var resourceBeginId = 100
  var resourceEndId = 1000000

  this.loading = false
  this.loadTotal = 0

  // Private variables //////////////////////////////////////////////////
  var scope = this

  // 加载管理器
  var loaderManager = new THREE.LoadingManager()

  var projectManager = new ProjectManager()

  // 模型加载器
  var ModelLoader = {}
  ModelLoader.OBJLoader = new OBJLoader2(loaderManager)
  ModelLoader.FBXLoader = new THREE.FBXLoader(loaderManager)

  // 贴图加载器
  // var textureLoader = new THREE.TextureLoader(loaderManager)

  // 字体加载器
  var fontLoader = new THREE.FontLoader()

  // 模型导出器
  var modelExporter = new THREE.OBJExporter()

  // 字体资源列表
  var fontList = {}
  // 纹理资源列表
  var textureList = {}
  // 默认图片资源列表
  var defImageList = {}

  var loadedCount = 0
  // var loadingTotal = 0
  var loadingRatio = 0
  // var loadingCount = 0
  // var lastLoadingCount = 0

  // Public Methods //////////////////////////////////////////////////////////
  this.clearLoad = function () {
    loadingRatio = 0
    // loadingTotal = 0
    // loadingCount = loadingTotal
    // lastLoadingCount = loadingTotal

    loadedCount = 0

    scope.loading = false
    scope.loadTotal = 0
  }

  // Import resource from url
  this.importResource = function (resourceUrl, resolve, reject, isDefault) {
    // 检查url是否为字符串
    if (typeof resourceUrl !== 'string') {
      console.log("ResourceManager, importResource(), resourceUrl: \"Url should be a \'string\'.\"")
      return
    }

    // 获得加载物体的URL的文件类型后缀
    var urlType = resourceUrl.split(/\./).pop().toLowerCase()
    switch (urlType) {
      case 'fbx':
      case 'obj':
        importModel(resourceUrl, urlType, resolve, reject)
        break
      case 'jpg':
      case 'png':
        importImage(resourceUrl, urlType, resolve, reject, isDefault)
        break
      case 'typeface':
        importFonts(resourceUrl)
        break
      default:
        console.log("ResourceManager, importResource(), urlType: \"Can\'t import ." + urlType + ' file."')
    }
  }

  this.removeResource = function (resourceUrl, callback) {
    // 检查url是否为字符串
    var urlType = null
    if (typeof resourceUrl === 'object') {
      urlType = resourceUrl.url.split(/\./).pop().toLowerCase()
    } else { urlType = resourceUrl.split(/\./).pop().toLowerCase() }
    // if (typeof resourceUrl !== 'string') {
    //   console.log("ResourceManager, removeResource(), resourceUrl: \"Url should be a \'string\'.\"")
    //   return
    // }

    // 获得加载物体的URL的文件类型后缀

    switch (urlType) {
      case 'fbx':
      case 'obj':
        var modelUrl = resourceUrl.url
        var modelName = resourceUrl.name
        if (!checkNone(modelList[modelUrl])) {
          if (!checkNone(modelList[modelUrl][modelName])) {
            modelList[modelUrl][modelName].dispose()
            modelList[modelUrl][modelName].deleted = true
          }
          var count = 0
          // 判定一个模型文件里是否所有模型资源都被删除了
          for (var name in modelList[modelUrl]) {
            if (!modelList[modelUrl][name].deleted) { ++count }
          }
          if (count === 0) {
            delete modelList[modelUrl]
          }
        }
        // importModel(resourceUrl, urlType)
        break
      case 'jpg':
      case 'png':
        if (!checkNone(imageList[resourceUrl])) { delete imageList[resourceUrl] }
        if (!checkNone(textureList[resourceUrl])) {
          textureList[resourceUrl].dispose()
        }
        // importImage(resourceUrl, urlType)
        break
      case 'typeface':
        // importFonts(resourceUrl)
        break
      default:
        console.log("ResourceManager, importResource(), urlType: \"Can\'t import ." + urlType + ' file."')
    }

    // 如果资源删除，撤销和重做操作会出现问题，所以进行清除撤销和重做记录
    var storageManager = new StorageManager()
    storageManager.clear()

    storageManager.ifListenDo = true

    if (!checkNone(callback)) {
      callback()
    }
  }

  this.clear = function () {
    scope.clearLoad()
    for (var url in modelList) {
      for (var name in modelList[url]) {
        modelList[url][name].dispose()
        delete modelList[url][name]
      }
      delete modelList[url]
    }

    for (var arg in imageList) {
      delete imageList[arg]
    }

    for (var arg in ridList) {
      delete ridList[arg]
    }

    for (var url in textureList) {
      textureList[url].dispose()
      delete textureList[url]
    }

    scope.loading = false

    engineInterface.updateModelList()
    engineInterface.updateImageList()
  }

  // 根据Url获取图片
  this.getImage = function (imgUrl) {
    if (checkNone(imgUrl)) { return '' }
    // 检查url是否为字符串
    if (typeof imgUrl !== 'string') {
      console.log("ResourceManager, getImage(), imgUrl: \"Url should be a \'string\'.\"")
      return ''
    }

    // if (isBrowser && imgUrl.match(/http/) === null) {
    //   imgUrl = 'http://47.104.20.11:3389/' + imgUrl
    // }

    if (!checkNone(defImageList[imgUrl])) { return defImageList[imgUrl] }

    // 不存在对应模型，返回空
    if (checkNone(imageList[imgUrl])) {
      // console.log("ResourceManager, getImage(), imgUrl: \"Can\'t find " + imgUrl + ' in the list."')
      // return null
      for (var url in imageList) {
        if (imageList[url] === imgUrl) { return url }
      }

      return ''
    } else {
      // 列表中存在对应模型，返回模型的拷贝
      return imageList[imgUrl]
    }
  }

  // 根据Url获取模型
  this.getModel = function (modelUrl, name) {
    // 检查url是否为字符串
    if (typeof modelUrl !== 'string') {
      console.log("ResourceManager, getImage(), modelUrl: \"Url should be a \'string\'.\"")
      return null
    }

    // 不存在对应模型，返回空
    if (checkNone(modelList[modelUrl])) {
      console.log("ResourceManager, getMesh(), modelUrl: \"Can\'t find " + modelUrl + ' in the list."')
      return null
    } else {
      console.log(modelUrl, name, modelList)
      var mesh = modelList[modelUrl][name].clone()
      modelList[modelUrl][name].disposeList.push(mesh)

      mesh.position.set(0, 0, 0)
      mesh.quaternion.set(0, 0, 0, 1)
      // mesh.scale.set(1, 1, 1)
      mesh.scale.x = mesh.group.scale.x > 0 ? 1 : -1
      mesh.scale.y = mesh.group.scale.y > 0 ? 1 : -1
      mesh.scale.z = mesh.group.scale.z > 0 ? 1 : -1
      mesh.group.scale.x *= mesh.group.scale.x > 0 ? 1 : -1
      mesh.group.scale.y *= mesh.group.scale.y > 0 ? 1 : -1
      mesh.group.scale.z *= mesh.group.scale.z > 0 ? 1 : -1

      mesh.group.add(mesh)

      // 列表中存在对应模型，返回模型的拷贝
      return mesh
    }
  }

  // 根据Url获取贴图
  this.getTexture = function (texUrl) {
    // 检查url是否为字符串
    if (typeof texUrl !== 'string') {
      console.log("ResourceManager, getTexture(), texUrl: \"Url should be a \'string\'.\"")
      return
    }

    // if (isBrowser && texUrl.match(/http/) === null) {
    //   texUrl = 'http://47.104.20.11:3389/' + texUrl
    // }

    var img = scope.getImage(texUrl)

    if (img.length === 0) {
      // console.log('ResourceManager, getTexture(), texUrl: "getImage(' + texUrl + ') failed."')
      return null
    } else {
      // 不能直接返回加载结果，会导致监听器无法释放
      // 先压入列表，从而使得读取逻辑变成同步
      // 同时也可以使得同一个纹理资源不需要加载多次
      if (checkNone(textureList[texUrl])) {
        var textureLoader = new THREE.TextureLoader(loaderManager)
        textureLoader.crossOrigin = 'Anonymous'
        var texture = textureLoader.load(img,
          function onLoad (tex) {
          },
          function onProgress (xhr) {
            // if (loadingTotal === 0)
            //   return

          },
          function onError (xhr) {
            console.log('ResourceManager, getTexture(), texUrl: "Invalid url - ' + texUrl + '."')
          })

        textureList[texUrl] = texture
      }

      return textureList[texUrl]
    }
  }

  // 根据URL获取字体资源
  this.getFont = function (fontUrl) {
    // 检查url是否字符串
    if (typeof fontUrl !== 'string') {
      console.log("ResourceManager, getFont(), fontUrl: \"Url should be a \'string\'.\"")
      return
    }

    // 不存在该字体资源
    if (checkNone(fontList[fontUrl])) {
      console.log("ResourceManager, getFont(), fontUrl: \"Can\'t find " + fontUrl + ' in the list."')
      return null
    } else {
      return fontList[fontUrl]
    }
  }

  // Private Methods //////////////////////////////////////////////////////////
  // 从url导入模型资源
  function importModel (modelUrl, modelType, resolve, reject) {
    var loader = null

    var fileName = modelUrl.split(/\\|\//).pop().split(/\./)
    var modelName = fileName[0]
    for (var index = 1; index < fileName.length - 1; ++index) {
      modelName += '.' + fileName[index]
    }

    var childMesh = null

    var existLoader = true

    // 根据文件名字后缀类型，决定模型加载器的类型
    switch (modelType) {
      case 'fbx':
        // loader = ModelLoader.FBXLoader
        loader = new THREE.FBXLoader(loaderManager)

        break
      case 'obj':
        // loader = ModelLoader.OBJLoader2
        loader = new OBJLoader2(loaderManager)
        break
      default:
        existLoader = false
        console.log('ResourceManager, importModel(), modelType: "No loader for .' + modelType + ' files."')
    }

    if (existLoader) {
      // modelUrl = ipcRenderer.sendSync(
      //   'ImportModel',
      //   projectManager.projectName,
      //   modelUrl)
      var modelInfo = null
      var readUrl = modelUrl
      if (platform.name === 'Electron' && isEditor) {
        if (checkNone(projectManager.curProject)) { return false }
        modelInfo = ipcRenderer.sendSync(
          'ImportModel',
          projectManager.curProject.filePath,
          modelUrl)

        if (modelInfo.noFile) { console.log('ResourceManager, importModel(), modelUrl: "Can\'t find file' + modelUrl + '."') }

        modelUrl = modelInfo.url
        readUrl = modelInfo.path
      }

      console.log('modelUrl: ', modelUrl)
      console.log('modelInfo: ', modelInfo)

      if (!checkNone(modelList[modelUrl])) {
        if (!checkNone(resolve)) { resolve() }
        return true
      }

      modelList[modelUrl] = {}

      var localLoad = 0
      var localTotal = 0
      var matUrlList = {}
      // console.log('Resource Url: ', readUrl)
      // loader.load(modelUrl,
      // if (isBrowser) {
      //   readUrl = 'http://47.104.20.11:3389/' + readUrl
      // }

      var scene = new SceneManager().getScene()
      if (!checkNone(modelInfo) && !checkNone(modelInfo.mtlPath)) {
        var mtlLoader = new THREE.MTLLoader()
        mtlLoader.setPath('static/projects/' + projectManager.curProject.title + '/images/')
        mtlLoader.setCrossOrigin('anonymous')
        mtlLoader.load(modelInfo.mtlPath, function (materials) {
          // materials.preload();
          console.log(33, materials)

          function getMaterialUrl (value) {
            var items = value.split(/\s+/)
            var pos

            pos = items.indexOf('-bm')

            if (pos >= 0) {
              items.splice(pos, 2)
            }

            pos = items.indexOf('-s')

            if (pos >= 0) {
              items.splice(pos, 4) // we expect 3 parameters here!
            }

            pos = items.indexOf('-o')

            if (pos >= 0) {
              items.splice(pos, 4) // we expect 3 parameters here!
            }

            var url = items.join(' ').trim()

            // return 'static/projects/' + projectManager.curProject.title +
            // '/images/' + url.split(/\\|\//).pop()
            return url
          }

          for (var name in materials) {
            if ('map_kd' in materials[name]) {
              var url = getMaterialUrl(materials[name].map_kd)
              matUrlList[name] = url
            }
          }

          // loader.setMaterials( materials.materials );
          // loader.setPath( 'static/default/models/' );
          loader.setDebug(false, false)

          // var orgCallback = callback
          // callback = function () {

          //   if (!checkNone(orgCallback))
          //     orgCallback()
          // }

          function importResourceList () {
            var promiseArray = []

            for (var name in matUrlList) {
              var url = matUrlList[name]
              var promise = new Promise(function (resolve, reject) {
                scope.importResource(url, resolve, reject)
              })
              promiseArray.push(promise)
            }

            return Promise.all(promiseArray)
          }

          var loadPromise = importResourceList()
          loadPromise.then(function () {
            loadModel()
            engineInterface.updateImageList()
          }, function () {
            if (checkNone(reject)) { reject() }
          })
        })
      } else {
        loadModel()
      }

      console.log(readUrl)

      function loadModel () {
        loader.load(readUrl,
          function onLoad (model) {
            console.log('import:', model)

            // scene.add(model)
            var childNameList = []

            // 遍历模型子节点
            if (model.children.length === 1) {
              model.children[0].name = modelName
            }

            model.traverse(function (child) {
              if (child instanceof Mesh) {
                childMesh = child

                childMesh.receiveShadow = true
                childMesh.castShadow = true

                // childMesh.group.applyMatrix(childMesh.matrix)
                // childMesh.group.matrix.copy(childMesh.matrix)
                childMesh.group.position.copy(childMesh.position)
                childMesh.group.quaternion.copy(childMesh.quaternion)
                childMesh.group.scale.copy(childMesh.scale)
                childMesh.group.updateMatrix()

                if (modelType === 'obj') {
                  // 计算模型包围盒
                  childMesh.geometry.computeBoundingBox()

                  var box = childMesh.geometry.boundingBox
                  var offset = new THREE.Vector3()
                  offset.x = (box.max.x + box.min.x) / 2
                  offset.y = (box.max.y + box.min.y) / 2
                  offset.z = (box.max.z + box.min.z) / 2

                  // 重新设置模型中心
                  childMesh.geometry.center()
                  childMesh.group.position.copy(offset)
                }
                // childMesh.geometry.computeBoundingBox()

                // childMesh.geometry.translate(1, 0, 0)
                // childMesh.position.sub(new THREE.Vector3(1, 0, 0))

                // 赋予url
                childMesh.url = modelUrl
                childMesh.orgName = childMesh.name

                if (isEditor) {
                  if (!checkNone(childMesh.orgMaterial)) {
                    if (!checkNone(matUrlList[childMesh.orgMaterial])) {
                      var url = 'static/projects/' + projectManager.curProject.title +
                        '/images/' + matUrlList[childMesh.orgMaterial].split(/\\|\//).pop()

                      childMesh.orgMaterialUrl = url
                    }
                  }
                }

                var bExist = false
                for (var index = 0; index < childNameList.length; ++index) {
                  if (childNameList[index] === childMesh.name) {
                    bExist = true
                  }
                }

                // if (platform.name === 'Electron') {
                if (!bExist) { childNameList.push(childMesh.name) }
                // }
              }
            })

            function getObjectsByName (model, meshName) {
              var meshList = []
              model.children.forEach(child => {
                if (child.name === meshName) {
                  meshList.push(child)
                }
              })
              return meshList
            }

            // if (platform.name === 'Electron') {
            console.log('childNameList: ', childNameList)
            for (var index = 0; index < childNameList.length; ++index) {
              var meshName = childNameList[index]
              // var child = model.getObjectByName(meshName)
              var meshList = getObjectsByName(model, meshName)

              if (meshList.length > 1) {
                var count = 0
                meshList.forEach(mesh => {
                  var tmpName = meshName + count
                  mesh.orgName = tmpName
                  modelList[modelUrl][tmpName] = mesh
                  modelList[modelUrl][tmpName].disposeList = []
                  ++count
                })
              } else if (meshList.length === 1) {
                modelList[modelUrl][meshName] = meshList[0]
                modelList[modelUrl][meshName].disposeList = []
              }
            }
            // }

            // console.log(modelList)

            if (!checkNone(resolve)) {
              resolve()
            }

            console.log(modelUrl + ' Imported.')

            if (scope.loading) {
              if (localTotal <= 0 || scope.loadTotal <= 0) { loadingRatio += 1 / scope.loadTotal } else { loadingRatio = loadingRatio + (1 - localLoad / localTotal) / scope.loadTotal }

              ++loadedCount
              if (isBrowser) { engineInterface.loadingPageData.curProgress = Math.round(loadingRatio * 100) }

              if (isEditor) { engineInterface.loadingProjectData.curProgress = Math.round(loadingRatio * 100) }

              console.log('LoadingRatio', scope.loadTotal, loadedCount, loadingRatio, localLoad, localTotal)
            }
          },
          function onProgress (xhr) {
            if (!scope.loading) { return }
            if (xhr.total <= 0 || scope.loadTotal <= 0) { return }

            loadingRatio += (xhr.loaded - localLoad) / xhr.total / scope.loadTotal
            localLoad = xhr.loaded
            localTotal = xhr.total
            if (isBrowser) { engineInterface.loadingPageData.curProgress = Math.round(loadingRatio * 100) }

            if (isEditor) { engineInterface.loadingProjectData.curProgress = Math.round(loadingRatio * 100) }

            console.log('LoadingRatio', scope.loadTotal, loadingRatio, localLoad, localTotal)
            // if (loadingTotal - lastLoadingCount >= 0)
            //   return

            // if (isBrowser) {
            //   // loadingRatio += (xhr.loaded - localLoad) / xhr.total / loadingTotal
            //   loadingRatio = (loadingCount - lastLoadingCount + (xhr.loaded / xhr.total)) / (loadingTotal - lastLoadingCount)
            //   localLoad = xhr.loaded
            //   // console.log(1111111, loadingRatio)

            //   engineInterface.loadingPageData.curProgress = Math.round(loadingRatio * 100)
            //   // console.log(222222, engineInterface.loadingPageData.curProgress)
            // }

            // if (isEditor) {
            //   // loadingRatio += (xhr.loaded - localLoad) / xhr.total / loadingTotal
            //   loadingRatio = (loadingCount - lastLoadingCount + (xhr.loaded / xhr.total)) / (loadingTotal - lastLoadingCount)
            //   localLoad = xhr.loaded

            //   engineInterface.loadingProjectData.curProgress = Math.round(loadingRatio * 100)
            // }

            // console.log('Loading Ratio:', loadingRatio, 'loadingCount', loadingCount, lastLoadingCount, loadingTotal)
          },
          function onError (error) {
            console.log(error)
            // alert(error)
            if (!checkNone(reject)) {
              reject()
            }
            alert('ResourceManager, importModel(), load(): "Load ' + modelUrl + ' failed."')
            console.log('ResourceManager, importModel(), load(): "Load ' + modelUrl + ' failed."')
          }
        )
      }
    } else if (!checkNone(reject)) { reject() }
  }

  function importImage (imgUrl, imgType, resolve, reject, isDefault) {
    var supportType = true

    if (!checkNone(imageList[imgUrl])) {
      if (!checkNone(resolve)) {
        resolve()
      }
      return true
    }

    // if (isBrowser) {
    //   imgUrl = 'http://47.104.20.11:3389/' + imgUrl
    // }

    var imgInfo = null
    var imgPath = imgUrl

    if (platform.name === 'Electron' && !isDefault && isEditor) {
      if (checkNone(projectManager.curProject)) { return false }
      imgInfo = ipcRenderer.sendSync(
        'ImportImage',
        projectManager.curProject.filePath,
        imgUrl)

      // console.log(77, imgInfo, imgUrl)
      if (imgInfo.noFile) {
        if (!checkNone(resolve)) { resolve() }
        return false
      }

      imgPath = imgInfo.path
      imgUrl = imgInfo.url
    }

    console.log(imgUrl)
    console.log('info: ', imgInfo)

    switch (imgType) {
      case 'jpg':
      case 'png':

        break
      default:
        supportType = false
        console.log('ResourceManager, importImage(), imgType: "No support for .' + imgType + ' files."')
    }

    if (supportType) {
      // var img = new Image()
      // if (isEditor)
      //   img.src = imgPath
      // else
      //   img.src = imgUrl

      if (isDefault) {
        defImageList[imgUrl] = imgUrl
      } else {
        // 由于异步加载，所以先加入列表，而不是等加载完成后才加入
        imageList[imgUrl] = imgPath
        console.log('test', imgUrl, imgPath)
      }

      // img.onload = function () {
      if (!isDefault && checkNone(textureList[imgUrl])) {
        var textureLoader = new THREE.TextureLoader(loaderManager)
        textureLoader.crossOrigin = 'Anonymous'

        var localLoad = 0
        var localTotal = 0

        textureLoader.load(imgPath,
          function onLoad (tex) {
            textureList[imgUrl] = tex
            if (!checkNone(resolve)) { resolve() }

            if (scope.loading) {
              if (localTotal <= 0 || scope.loadTotal <= 0) { loadingRatio += 1 / scope.loadTotal } else { loadingRatio = loadingRatio + (1 - localLoad / localTotal) / scope.loadTotal }
              ++loadedCount

              if (isBrowser) { engineInterface.loadingPageData.curProgress = Math.round(loadingRatio * 100) }

              if (isEditor) { engineInterface.loadingProjectData.curProgress = Math.round(loadingRatio * 100) }

              console.log('LoadingRatio', scope.loadTotal, loadingRatio, loadedCount, localLoad, localTotal)
            }
          },
          function onProgress (xhr) {
            console.log('Loading Ratio(texture):', loadingRatio)
            if (!scope.loading) { return }
            if (xhr.total <= 0 || scope.loadTotal <= 0) { return }
            loadingRatio += (xhr.loaded - localLoad) / xhr.total / scope.loadTotal
            localLoad = xhr.loaded
            localTotal = xhr.total

            if (isBrowser) { engineInterface.loadingPageData.curProgress = Math.round(loadingRatio * 100) }

            if (isEditor) { engineInterface.loadingProjectData.curProgress = Math.round(loadingRatio * 100) }

            console.log('LoadingRatio', scope.loadTotal, loadingRatio, localLoad, localTotal)

            // if (isBrowser) {
            //   if (loadingTotal <= 1)
            //     loadingRatio = xhr.loaded / xhr.total
            //   else {
            //     // loadingRatio += (xhr.loaded - localLoad) / xhr.total / loadingTotal
            //     loadingRatio = (loadingCount - lastLoadingCount + (xhr.loaded / xhr.total)) / (loadingTotal - lastLoadingCount)
            //     localLoad = xhr.loaded
            //   }
            //   // console.log(1111111, loadingRatio)

            //   engineInterface.loadingPageData.curProgress = Math.round(loadingRatio * 100)
            //   // console.log(222222, engineInterface.loadingPageData.curProgress)
            // }

            // if (isEditor) {
            //   if (loadingTotal <= 1)
            //     loadingRatio = xhr.loaded / xhr.total
            //   else {
            //     // loadingRatio += (xhr.loaded - localLoad) / xhr.total / loadingTotal
            //     loadingRatio = (loadingCount - lastLoadingCount + (xhr.loaded / xhr.total)) / (loadingTotal - lastLoadingCount)
            //     localLoad = xhr.loaded
            //   }

            //   engineInterface.loadingProjectData.curProgress = Math.round(loadingRatio * 100)
            // }

            // console.log('Loading Ratio(texture):', loadingRatio)
          },
          function onError (xhr) {
            if (!checkNone(resolve)) { resolve() }
            // defImageList[imgUrl] = null
            // imageList[imgUrl] = null
            // importImage (imgUrl, imgType, callback, isDefault)
            console.log('ResourceManager, getTexture(), texUrl: "Invalid url - ' + imgUrl + '."')
          })
      } else {
        if (!checkNone(resolve)) { resolve() }
      }
      // }
      // 图片加载完成
      // img.onload = function () {
      //   console.log(imgUrl + ' Loaded.')
      //   if (!checkNone(callback)) { callback() }
      // }
      // 图片加载失败
      // img.onerror = function () {
      //   console.log('ResourceManager, importImage(), imgUrl: "Load ' + imgUrl + ' failed."')
      // }

      // if (!checkNone(callback)) { callback() }
    }
  }

  // 导入字体资源
  function importFonts (fontUrl) {
    // 暂时未对字体资源进行区分

    fontLoader.load(fontUrl,
      function onLoad (font) {
        fontList[fontUrl] = font
        console.log(fontUrl + ' Loaded.')
      },
      function onProgress () {
      },
      function onError () {
        console.log('ResourceManager, importImage(), fontUrl: "Load ' + fontUrl + ' failed."')
      })
  }

  loaderManager.onStart = function (url, itemsLoaded, itemsTotal) {
    if (!scope.loading) { return }
    console.log('Started loading file: ' + url + '.\nLoaded ' + itemsLoaded + ' of ' + itemsTotal + ' files.')

    // loadingCount = itemsLoaded
    // loadingTotal = itemsTotal + 1
    // loadingRatio = itemsLoaded / itemsTotal
    // console.log(1111, loadingRatio)
  }

  loaderManager.onProgress = function (url, itemsLoaded, itemsTotal) {
    if (!scope.loading) { return }
    console.log('Loading file: ' + url + '.\nLoaded ' + itemsLoaded + ' of ' + itemsTotal + ' files.')

    // loadingTotal = itemsTotal
    // loadingCount = itemsLoaded
  }

  loaderManager.onLoad = function () {
    // if (loadingTotal - lastLoadingCount === 0)
    //   return
    // loadingRatio = loadingCount / (loadingTotal - lastLoadingCount)

    if (!scope.loading) { return }
    // if (loadingTotal === 0)
    // loadingRatio = 100
    if (scope.loadTotal === loadedCount) { loadingRatio = 1 }
    // console.log('loading log', loadingCount, loadingTotal)
    if (isBrowser) {
      engineInterface.loadingPageData.curProgress = loadingRatio * 100
      // engineInterface.loadingPageData.hasFinished = true
    }
    if (isEditor) {
      engineInterface.loadingProjectData.curProgress = loadingRatio * 100
    }

    console.log('Loading complete!')
  }

  // 初始化资源管理器
  function init () {
    // 必须加载系统资源
    scope.importResource('static/default/fonts/helvetiker_bold.typeface')// 必须加载默认字体
    // scope.importResource('static/default/fonts/SimHei_Regular.typeface')// 必须加载默认字体

    scope.importResource('static/default/images/direction.png', null, null, true)// 必须加载默认字体
    scope.importResource('static/default/images/point.png', null, null, true)// 必须加载默认字体
    scope.importResource('static/default/images/spot.png', null, null, true)// 必须加载默认字体
    scope.importResource('static/default/images/rect.png', null, null, true)// 必须加载默认字体
    scope.importResource('static/default/images/camera.png', null, null, true)// 必须加载默认字体
    scope.importResource('static/default/images/hpPlus.png', null, null, true)// 必须加载默认字体
    scope.importResource('static/default/images/hpPoint.png', null, null, true)// 必须加载默认字体
    scope.importResource('static/default/images/hpEye.png', null, null, true)// 必须加载默认字体
    scope.importResource('static/default/images/circle.png', null, null, true)// 必须加载默认字体
  }

  init()
}

export { ResourceManager }



