<template>
  <div class="pageContainer" :style="backgroundStyle">
    <ui-button-sp-list-component :isActive="curActivePage.pageInfo.pageType === PageType.MATERIAL" :btnList="matBtnList" :curSelectBtnId.sync="curSelectMatBtnId"></ui-button-sp-list-component>
    <ui-button-sp-list-component :isActive="curActivePage.pageInfo.pageType === PageType.ANIMATION" :btnList="aniBtnList" :curSelectBtnId.sync="curSelectAniBtnId"></ui-button-sp-list-component>
    <div class="windowUIContainer">
      <ui-image-component class="commonUI" :style="getCommonUIStyle(item.id)" v-for="item in curActivePage.pageUI.imgsData" :key="item.id" :imgData="item"></ui-image-component>
      <ui-text-component class="commonUI" :style="getCommonUIStyle(item.id)" v-for="item in curActivePage.pageUI.textData" :key="item.id" :textData="item"></ui-text-component>

      <!-- 当前页面是热点注释会有额外的UI组件 -->
      <!-- <ui-image-component v-if="hotPointDisabled" v-for="item in curHotPointUI.imgsData" :imgData="item"></ui-image-component>
      <ui-text-component v-if="hotPointDisabled" v-for="item in curHotPointUI.textData" :textData="item"></ui-text-component>
      <ui-video-component v-if="hotPointDisabled" v-for="item in curHotPointUI.videoData" :videoData="item"></ui-video-component> -->
      <hotPoint-ui-component :disabled="item.hotPointData.id !== $store.getters.curHotPointId" :curHotPointUI="item.hotPointUI" v-for="(item, index) in curHotPointList" :key="index"></hotPoint-ui-component>

      <!-- 当前页面是跳转链接会有额外的UI组件 -->
      <ui-image-component v-if="curActivePage.pageInfo.pageType === PageType.LINK" v-for="item in curHyperLinkUI.imgHyperLinks" :key="item.id" :imgData="item"></ui-image-component>
      <ui-button-component v-if="curActivePage.pageInfo.pageType === PageType.LINK" v-for="item in curHyperLinkUI.btnHyperLinks" :key="item.id" :btnData="item"></ui-button-component>
    </div>
  </div>
</template>

<script>
import UIImageComponent from './ui/UIImage.vue'
import UITextComponent from './ui/UIText.vue'
import UIVideoComponent from './ui/UIVideo.vue'
import UIButtonComponent from './ui/UIButton.vue'
import UIButtonSPList from './ui/UIButton_SP_List.vue'
import hotPointUI from './hotPointUI.vue'

export default {
  name: 'page',
  components: {
    'ui-image-component': UIImageComponent,
    'ui-text-component': UITextComponent,
    'ui-video-component': UIVideoComponent,
    'ui-button-component': UIButtonComponent,
    'ui-button-sp-list-component': UIButtonSPList,
    'hotPoint-ui-component': hotPointUI
  },
  props: {
    curActivePage: Object,
    disabled: {
      default: true,
      type: Boolean
    }
  },
  data () {
    return {
      PageType: this.$store.state.PageType,
      TextureType: this.$store.state.TextureType,
      curPageOpacity: 0
    }
  },
  watch: {
    disabled: function (newValue, oldValue) {
      if (newValue) {
        this.curPageOpacity = 0
      } else {
        this.curPageOpacity = 1
      }
      // var lastTime = performance.now()
      // var ease = (timeStamp) => {
      //   var delta = timeStamp - lastTime
      //   if (newValue) {
      //     this.curPageOpacity -= 0.002 * delta
      //     if (this.curPageOpacity > 0) requestAnimationFrame(ease)
      //   } else {
      //     this.curPageOpacity += 0.002 * delta
      //     if (this.curPageOpacity < 1) requestAnimationFrame(ease)
      //   }
      // }
      // requestAnimationFrame(ease)
    }
  },
  computed: {
    matBtnList: function () {
      if (this.curActivePage.pageInfo.pageType === this.PageType.MATERIAL) {
        return this.curActivePage.pageContent.matBtnList.map(function (x) {
          return {
            id: x.id,
            btnData: x.btnData
          }
        })
      } else {
        return []
      }
    },
    curSelectMatBtnId: {
      get: function () {
        if (this.curActivePage.pageInfo.pageType === this.PageType.MATERIAL) {
          return this.curActivePage.pageContent.curSelectBtnId
        } else return -1
      },
      set: function (val) {
        if (this.curActivePage.pageInfo.pageType === this.PageType.MATERIAL) {
          this.curActivePage.pageContent.curSelectBtnId = val
        }
      }
    },
    aniBtnList: function () {
      if (this.curActivePage.pageInfo.pageType === this.PageType.ANIMATION) {
        return this.curActivePage.pageContent.animationBtn.map(ele => {
          return {
            id: ele.id,
            btnData: ele.btnData
          }
        })
      } else {
        return []
      }
    },
    curSelectAniBtnId: {
      get: function () {
        if (this.curActivePage.pageInfo.pageType === this.PageType.ANIMATION) {
          return this.curActivePage.pageContent.curSelectBtnId
        } else return -1
      },
      set: function (val) {
        if (this.curActivePage.pageInfo.pageType === this.PageType.ANIMATION) {
          this.curActivePage.pageContent.curSelectBtnId = val
        }
      }
    },
    // 当前页面的热点注释的UI，可能为空
    curHotPointUI: function () {
      if (this.curActivePage.pageInfo.pageType === this.PageType.HOTPOINT) {
        for (var index in this.curActivePage.pageContent.hotPointList) {
          if (this.curActivePage.pageContent.hotPointList[index].hotPointData.id === this.$store.getters.curHotPointId) {
            // console.log(this.curActivePage.pageContent.hotPointList[index].hotPointUI)
            return this.curActivePage.pageContent.hotPointList[index].hotPointUI
          }
        }
        // 假如当前页面没有选择热点，同样返回一个空的UI信息
        return {
          imgsData: [],
          textData: [],
          videoData: []
        }
      } else {
        // 返回一个空的UI信息给组件渲染，防止报错
        return {
          imgsData: [],
          textData: [],
          videoData: []
        }
      }
    },
    curHotPointList: function () {
      if (this.curActivePage.pageInfo.pageType === this.PageType.HOTPOINT) {
        return this.curActivePage.pageContent.hotPointList
      } else return []
    },
    hotPointDisabled: function () {
      return this.curActivePage.pageInfo.pageType === this.PageType.HOTPOINT
    },
    // 当前页面的跳转链接的UI，可能为空
    curHyperLinkUI: function () {
      if (this.curActivePage.pageInfo.pageType === this.PageType.LINK) {
        return this.curActivePage.pageContent.hyperLinks
      } else {
        // 当前页面非跳转链接，返回空的UI组件进行渲染，以免报错
        return {
          imgHyperLinks: [],
          btnHyperLinks: []
        }
      }
    },
    // 当前加载页面的加载动画
    curLoadingPageIcon: function () {
      if (this.curActivePage.pageInfo.pageType === this.PageType.LOADING) {
        return this.curActivePage.pageContent.loadPageData.loadingIcon
      } else {
        return {}
      }
    },
    // 在跳转连接没有开启画布的时候的背景
    backgroundStyle: function () {
      if (this.curActivePage.pageInfo.pageType === this.PageType.LINK) {
        return {
          'background-size': '100% 100%',
          'background-image': this.curActivePage.pageContent.backgroundData.bkgType === this.TextureType.COLOR ? undefined : `url(${this.curActivePage.pageContent.backgroundData.bkgTex})`,
          'background-color': this.curActivePage.pageContent.backgroundData.bkgType === this.TextureType.COLOR ? this.curActivePage.pageContent.backgroundData.bkgColor : undefined,
          opacity: this.curPageOpacity
        }
      } else {
        // 返回一个空的样式(因为画布会覆盖掉背景所以任何背景样式都不会看见)
        return {
          opacity: this.curPageOpacity
        }
      }
    }
  },
  methods: {
    hiddenUI: function (id) {
      // 当前非热点页面，无需隐藏
      if (this.curActivePage.pageInfo.pageType !== this.PageType.HOTPOINT) return true
      for (var index in this.curActivePage.pageContent.hotPointList) {
        if (this.curActivePage.pageContent.hotPointList[index].hotPointData.id === this.$store.getters.curHotPointId) {
          var hotPoint = this.curActivePage.pageContent.hotPointList[index]
          for (var hi in hotPoint.hotPointUI.hiddenPageUIId) {
            // 当前UI的ID在隐藏列表中
            if (hotPoint.hotPointUI.hiddenPageUIId[hi] === id) return false
          }
          // 当前UI不在隐藏列表中
          return true
        }
      }
      // 当前没有任何热点被选中，不需要隐藏
      return true
    },
    getCommonUIStyle: function (id) {
      if (!this.hiddenUI(id)) return { opacity: 0 }
    }
  }
}

</script>

<style>
.pageContainer {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
}

.commonUI {
  transition: opacity 0.3s;
}

.pageBkgImg {
  position: absolute;
  z-index: -10;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
</style>


