var PageType = {
  DEFAULT: 300,
  LOADING: 301,
  BASEFUNC: 302,
  MATERIAL: 303,
  MARKLINE: 304,
  ANIMATION: 305,
  HOTPOINT: 306,
  LINK: 307
}

var TextureType = {
  NONE: 0,
  COLOR: 1,
  TEXTURE: 2
}

var TextAlignType = {
  LEFT: 0,
  MIDDLE: 1,
  RIGHT: 2
}

var TextDirectionType = {
  HORIZONTAL: 0,
  VERTICAL: 1
}

var MarkLineType = {
  NONE: 0,
  ARROW: 10,
  CONE: 20,
  BOX: 30,
  SPHERE: 40,
  RECTANGLE: 50
}

var FontType = {
  DEFAULT: 0
}

var SelectType = {
  NONE: 0,
  MESH: 10,
  LIGHT: 20,
  MARKLINE: 30,
  HOTPOINT: 40
}

var UIDefaultZ = 1000

var UIType = {
  IMAGE: 0,
  TEXT: 1,
  VIDEO: 2
}

var HotPointType = {
  PLUS: 0,
  POINT: 1,
  EYE: 2
}

/*
  KeyFrame - changeMarkList
  列表大小为4，true表示已改变，每一个代表意义如下：
  0: translate
  1: rotate
  2: scale
  3: opacity
*/

export {
  SelectType,
  PageType,
  TextureType,
  TextAlignType,
  TextDirectionType,
  MarkLineType,
  FontType,
  UIType,
  HotPointType
}



