import { SceneManager } from '../managers/SceneManager.js'

// 布告板类，继承自Sprite
// 由于Threejs原生的Point类不能设置点击的大小，所以通过Sprite来实现布告板
function Billboard (cameraControl) {
  // if(cameraControl === null || cameraControl === undefined || !(cameraControl instanceof THREE.OrbitControls)) {
  // 	console.log("Billboard, constructor(), cameraControl: \"cameraControl should be OrbitControls.\"");
  // 	return;
  // }
  // Parent constructor //////////////////////////////////////////////
  var material = new THREE.SpriteMaterial({
    color: 0xffffff,
    // transparent: false,
    // opacity: 1.0,
    depthTest: false
    // blending: THREE.AdditiveBlending
  })

  THREE.Sprite.call(this, material)

  // Public variables ///////////////////////////////////////////////
  if (checkNone(cameraControl)) { this.cameraControl = new SceneManager().getCameraControl() } else { this.cameraControl = cameraControl }

  this.renderOrder = -1

  // 设置布告板大小是否固定
  this.fixedSize = false
  this.scaleFactor = 1.0

  // size用于表示布告板的大小
  Object.defineProperty(this, 'size', {
    get: function () {
      return size
    },
    set: function (value) {
      size = value
      this.update()
    }
  })

  // Private variables ///////////////////////////////////////////////
  var scope = this

  var size = 1// 热点的尺寸，屏幕像素为单位

  // 由于使用到bind函数，所以需要记录函数地址
  // 从而可以正确的找到函数对应的监听事件，才能正确删除
  var updateHandler = this.update.bind(this)

  this.addListener = function () {
    // this.cameraControl.addEventListener('autoTraning', this.update);
    this.cameraControl.addEventListener('change', updateHandler)
    this.cameraControl.addEventListener('end', updateHandler)
  }

  this.removeListener = function () {
    // this.cameraControl.removeEventListener('autoTraning', this.update);
    this.cameraControl.removeEventListener('change', updateHandler)
    this.cameraControl.removeEventListener('end', updateHandler)
    // 释放handler
    updateHandler = null
  }

  this.update()
}

Billboard.prototype = Object.assign(Object.create(THREE.Sprite.prototype), {
  constructor: Billboard,

  isBillboard: true,

  isCustom: true,

  update: function () {
    // console.log(this.cameraControl.object);
    var camera = this.cameraControl.object
    var cameraPos = new THREE.Vector3()
    cameraPos.setFromMatrixPosition(camera.matrixWorld)
    if (!this.fixedSize) {
      // 根据镜头位置计算放缩的大小，从而使得镜头前布告板大小不变
      var scale = this.position.distanceTo(cameraPos) / 10 * this.size
      this.scale.set(
        scale * this.scaleFactor,
        scale * this.scaleFactor,
        scale * this.scaleFactor)
    }
  },

  addToScene: function (scene, shutSelect) {
    if (checkNone(shutSelect) || shutSelect === false) {
      this.intersectList = scene.spriteIntersectList
      this.intersectList.push(this)
    }
    this.addListener()
  },

  removeFromScene: function (shutSelect) {
    if (checkNone(shutSelect) || shutSelect === false) {
      var index = this.intersectList.indexOf(this)
      if (index > -1) {
        this.intersectList.splice(index, 1)
      }
    }

    this.material.dispose()

    this.removeListener()
  },

  setTexture: function (textureUrl) {
    new SceneManager().setTexture(textureUrl, this)
  }

  // addListener: function (){
  // 	// this.cameraControl.addEventListener('autoTraning', this.update);
  // 	this.cameraControl.addEventListener('change', this.update);
  // 	this.cameraControl.addEventListener('end', this.update);
  // },

  // removeListener: function (){
  // 	// this.cameraControl.removeEventListener('autoTraning', this.update);
  // 	this.cameraControl.removeEventListener('change', this.update);
  // 	this.cameraControl.removeEventListener('end', this.update);
  // }
})

export { Billboard }



