var RunType = {
  Editor: 0,
  Browser: 1
}

var runType = RunType.Browser

window.isEditor = (runType === RunType.Editor)
window.isBrowser = (runType === RunType.Browser)

window.isStatic = true

// 如果为空，返回true
// 不为空，返回false
window.checkNone = function (object) {
  return (object === undefined || object === null)
}

