import { engineInterface } from '../core/EngineInterface.js'

import { SceneManager } from '../managers/SceneManager.js'
import { StorageManager } from '../managers/StorageManager.js'
import { PageManager } from '../managers/PageManager.js'
import { Animation } from '../objects/Animation.js'
import { PageType } from '../core/Type.js'

function AnimationManager () {
  // Singleton pattern ////////////////////////////////////////////////
  if (AnimationManager._instance !== undefined) {
    return AnimationManager._instance
  }
  AnimationManager._instance = this

  // Public variables ///////////////////////////////////////////////

  // Private variables //////////////////////////////////////////////////
  var scope = this

  var sceneManager = new SceneManager()
  var storageManager = new StorageManager()
  var pageManager = new PageManager()

  // 是否播放中
  var playing = false

  // 播放第一帧
  var firstPlay = false
  var firstLoop = true

  // 当前播放的帧的下标
  var curFrameTimepoint = 0

  var previousTime = 0
  var currentTime = 0
  var delta = 0

  var frameNumber = 1500

  // Public methods //////////////////////////////////////////////////////////
  // 用于自动播放动画
  this.update = function () {
    if (!playing) {
      return false
    }

    currentTime = new Date().getTime()
    delta = currentTime - previousTime

    // if (delta > 40) {
    //   previousTime = currentTime
    // }
    // else
    //   return

    var page = pageManager.getCurPage()

    if (checkNone(page) || page.pageType !== PageType.ANIMATION) {
      return false
    }

    var curAnimation = page.getCurButton()

    if (checkNone(curAnimation)) {
      playing = false
      engineInterface._playing = false
      return false
    }

    if (firstPlay || delta > 40) {
      previousTime = currentTime

      var ifEndPlay = curAnimation.play(curFrameTimepoint + delta / 40, firstLoop)

      engineInterface.updateCurFrame()

      if (ifEndPlay === 0) {
        // console.log(curFrameTimepoint)
        ++curFrameTimepoint
      } else if (ifEndPlay === 1) {
        playing = firstPlay
        engineInterface._playing = false
        curFrameTimepoint = 0
        curAnimation.audio.pause()
      }

      if (firstPlay) {
        // console.log(777777, curFrameTimepoint / 25 + delta / 1000)
        curAnimation.audio.currentTime = curFrameTimepoint / 25 + delta / 1000
        curAnimation.audio.play()
        firstPlay = false
      }

      if (ifEndPlay === 2) {
        firstPlay = true
        firstLoop = false
        curFrameTimepoint = 0
        // engineInterface._playing = true
        curAnimation.audio.pause()
      }
    } else { curAnimation.play(curFrameTimepoint + delta / 40) }
  }

  // // 播放动画
  // this.playAnimation = function () {
  //   // scope.setCurKeyFrame(DEFAULT_FRAMEINDEX)
  //   playing = true
  //   engineInterface._playing = true
  // }

  this.setPlayFrame = function (timePoint) {
    if (timePoint > frameNumber) { timePoint = frameNumber }
    curFrameTimepoint = timePoint

    var page = pageManager.getCurPage()

    if (checkNone(page) || page.pageType !== PageType.ANIMATION) {
      return false
    }
    var curAnimation = page.getCurButton()
    if (!checkNone(curAnimation)) {
      curAnimation.play(timePoint, true)
      curAnimation.audio.currentTime = curFrameTimepoint / 25
    }
  }

  this.setPlaying = function (ifPlaying) {
    if (ifPlaying) {
      firstPlay = true
      firstLoop = true
      previousTime = new Date().getTime()
    }
    playing = ifPlaying
    engineInterface._playing = ifPlaying

    var page = pageManager.getCurPage()

    if (checkNone(page) || page.pageType !== PageType.ANIMATION) {
      return false
    }
    var curAnimation = page.getCurButton()
    curAnimation.audio.pause()
  }

  this.getPlaying = function () {
    return playing
  }

  this.clear = function () {
    // 是否播放中
    playing = false

    // 当前播放的帧的下标
    curFrameTimepoint = 0

    previousTime = 0
    currentTime = 0
    delta = 0
  }
}

export { AnimationManager }



