<template>
  <div class="UIButton_SP" :role="btnData.type === TextureType.COLOR ? btnData.shapeType : ''" @click.stop="onClick">
    <!-- 删除按钮 -->
    <span v-if="canDelete" role="deleteBtn" @touchstart.stop="onDelete">+</span>
    <div :style="{'opacity': btnData.opacity}">
      <!-- 用来当作背景实现描边效果 -->
      <span v-show="btnData.type === TextureType.COLOR" role="back" :style="{'background-color': btnData.actShapeColor}"></span>
      <!-- 用来当作前景遮蔽背景实线描边效果 -->
      <span v-show="btnData.type === TextureType.COLOR" role="front" :style="{backgroundColor: isOn ? btnData.actShapeColor : btnData.defShapeColor}" ></span>

      <span v-show="btnData.type === TextureType.TEXTURE && !isOn" role="texDef" :style="texDefStyle"></span>
      <span v-show="btnData.type === TextureType.TEXTURE && isOn" role="texAct" :style="texActStyle"></span>
      <p :style="fontStyle">{{ btnData.text }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ui-button-list',
  props: {
    btnData: Object,
    // 按钮是否可以被删除
    canDelete: {
      default: true,
      type: Boolean
    },
    isOn: {
      default: false,
      type: Boolean
    }
  },
  data () {
    return {
      TextureType: this.$store.state.TextureType
    }
  },
  computed: {
    fontStyle: function () {
      return {
        'font-size': `${(this.fontSize) / 14}rem`,
        color: this.isOn ? this.btnData.actTextColor : this.btnData.defTextColor,
        'margin-top': this.btnData.type === this.TextureType.TEXTURE ? `5.7rem` : undefined
        // 'font-family': this.$store.state.fontFamily[this.btnData.fontType].value
      }
    },
    fontSize: function () {
      return parseInt(this.$store.state.fontSizes[this.btnData.fontSize].name)
    },
    texDefStyle: function () {
      return {
        'background-image': `url(${this.btnData.defTex})`
      }
    },
    texActStyle: function () {
      return {
        'background-image': `url(${this.btnData.actTex})`
      }
    }
  },
  methods: {
    onDelete: function () {
      this.$emit('delete-btn')
    },
    onClick: function () {
      console.log('alskdfjlsdkfjl')
      this.$emit('select')
    }
  }
}
</script>

<style>

.UIButton_SP {
  width: auto;
  height: 5.7rem;

  margin-top: 0.71rem;
  margin-bottom: auto;

  margin-left: 1rem;
  margin-right: 1rem;

  min-width: 5.7rem;
  min-height: 5.7rem;

  position: relative;
  pointer-events: auto;
}

.UIButton_SP>div {
  width: auto;
  height: 100%;
  display: inline-flex;
  /*justify-content: center;*/

  position: relative;

  min-width: 5.7rem;

  user-select: none;
  cursor: pointer;
}

.UIButton_SP>div>span {
  position: absolute;
  z-index: 0;
  width: 5.7rem;
  height: 5.7rem;
  background-size: 5.7rem 5.7rem;
  background-repeat: no-repeat;
}

.UIButton_SP>div>span[role=texDef] {
  left: calc(50% - 2.85rem);
}

.UIButton_SP>div>span[role=texAct] {
  z-index: 1;
  left: calc(50% - 2.85rem);
}

.UIButton_SP>div>span[role=back] {
  position: absolute;
  z-index: 0;

  top: 0;
  left: calc(50% - 2.85rem);

  width: 5.7rem;
  height: 5.7rem;

}

.UIButton_SP>div>span[role=front] {
  z-index: 1;

  width: calc(5.4rem);
  height: calc(5.4rem);
  left: calc(50% - 2.85rem);

  /*top: 0.15rem;
  left: 0.15rem;*/
  transform: translate(0.14rem, 0.14rem);

  transition: background-color 0.3s;
}

.UIButton_SP>span[role=deleteBtn] {
  position: absolute;
  z-index: 2;
  width: 25%;
  height: 25%;
  background-color: rgba(255,255,255,0.50);
  border-radius: 50%;
  top: -12.5%;
  right: -14.5%;

  display: inline-flex;
  justify-content: center;
  transform: rotate(45deg);
  color: #ffffff;

  user-select: none;
  cursor: pointer;
}

.UIButton_SP>div>p {
  z-index: 2;
  display: inline-flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: transparent;
  transition: color 0.3s;
  text-align: center;
  word-wrap: break-word;
  margin: auto;
}

.UIButton_SP[role=circle]>div>span[role=front],
.UIButton_SP[role=circle]>div>span[role=back] {
  clip-path: circle(50% at 50% 50%);
}

.UIButton_SP[role=square]>span {}


.UIButton_SP[role=square_r]>div>span[role=front],
.UIButton_SP[role=square_r]>div>span[role=back] {
  border-radius: 0.32rem;
}


.UIButton_SP[role=square_rx]>div>span[role=front],
.UIButton_SP[role=square_rx]>div>span[role=back] {
  border-radius: 0.71rem;
}


.UIButton_SP[role=rhombus]>div>span[role=front],
.UIButton_SP[role=rhombus]>div>span[role=back] {
  clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
}

.UIButton_SP[role=hexagon]>div>span[role=back],
.UIButton_SP[role=hexagon]>div>span[role=front] {
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
}

</style>


