<template>
  <div class="navigation">
    <div role="left" @click.stop="onClick(true)">
      <span></span>
    </div>
    <div role="thumbnail">
      <!-- 页面提示列表 -->
      <span v-for="(item, index) in pages" :class="{'active': item.id === curActivePageId}"></span>
    </div>
    <div role="right" @click.stop="onClick(false)">
      <span></span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'navigation-component',
  props: {
    pages: Array
  },
  methods: {
    onClick: function (dir) {
      // true 上一页, false 下一页
      var index = 0
      if (dir) {
        // 当前是第一页，翻到最后一页
        if (this.pages[0].id === this.curActivePageId) this.$store.commit('update:curActivePageId', this.pages[this.pages.length - 1].id)
        else {
          // 当前不是第一页，往前翻页
          for (index in this.pages) {
            if (this.pages[index].id === this.curActivePageId) {
              this.$store.commit('update:curActivePageId', this.pages[parseInt(index) - 1].id)
              return
            }
          }
        }
      } else {
        // 当前是最后一页，翻到第一页
        if (this.pages[this.pages.length - 1].id === this.curActivePageId) this.$store.commit('update:curActivePageId', this.pages[0].id)
        else {
          // 当前不是最后一页，往后翻一页
          for (index in this.pages) {
            if (this.pages[index].id === this.curActivePageId) {
              this.$store.commit('update:curActivePageId', this.pages[parseInt(index) + 1].id)
              return
            }
          }
        }
      }
    }
  },
  computed: {
    curActivePageId: {
      get: function () {
        return this.$store.getters.curActivePageId
      },
      set: function (val) {
        this.$store.commit('update:curActivePageId', val)
      }
    }
  }
}
</script>


