<template>
<div class="window">
  <div :style="backgroundStyle">
    <canvas id="glCanvas" :style="canvasStyle"></canvas>
    <!-- 背景音乐图标 -->
    <div class="musicIcon">
      <img :role="bkgMusicState ? 'playing' : ''" src="static/default/images/musicIcon.png" @click="playMusic()">
      <!-- <audio id="bkgMusic" ref="bkgMusic" :src="$store.state.testBkgMusicSrc" loop="true" autoplay="true">你的浏览器不支持音乐控件</audio> -->
      <audio id="bkgMusic" ref="bkgMusic" :src="$store.state.sceneData.bkgMusicSrc" loop="true" autoplay="true">你的浏览器不支持音乐控件</audio>
    </div>
    <!-- 各个页面的UI空间 -->
    <page-vue :curActivePage="item" :disabled="item.pageInfo.id !== curActivePage.pageInfo.id" v-show="item.pageInfo.id === curActivePage.pageInfo.id" v-for="(item, index) in $store.state.pagesList" :key="index"></page-vue>
    </div>
  </div>
</div>
</template>

<script>
import Page from './subComponents/page.vue'

export default {
  name: 'window',
  components: {
    'page-vue': Page
  },
  props: {
    curActivePage: Object
  },
  mounted () {
    this.$refs.bkgMusic.play()
    document.addEventListener('WeixinJSBridgeReady', () => {
      this.$refs.bkgMusic.play()
    }, false)
  },
  data () {
    return {
      PageType: this.$store.state.PageType,
      TextureType: this.$store.state.TextureType,
      canvasStyle: {
        opacity: 1,
        transition: 'opacity 0.5s'
      },
      // 背景音乐播放状态 false: pause/ true: play
      bkgMusicState: true
    }
  },
  computed: {
    // 在跳转连接和加载页面没有开启画布的时候的背景
    backgroundStyle: function () {
      if (this.curActivePage.pageInfo.pageType === this.PageType.LOADING) {
        return {
          'background-size': '100% 100%',
          'background-image': this.curActivePage.pageContent.backgroundData.bkgType === this.TextureType.COLOR ? undefined : `url(${this.curActivePage.pageContent.backgroundData.bkgTex})`,
          'background-color': this.curActivePage.pageContent.backgroundData.bkgType === this.TextureType.COLOR ? this.curActivePage.pageContent.backgroundData.bkgColor : undefined
        }
      } else {
        // 返回一个空的样式(因为画布会覆盖掉背景所以任何背景样式都不会看见)
        return {}
      }
    },
    // 画布是否可见
    canvasValid: function () {
      return this.curActivePage.pageInfo.pageType !== this.PageType.LOADING && this.curActivePage.pageInfo.pageType !== this.PageType.LINK
    }
  },
  watch: {
    canvasValid: function (newValue) {
      if (newValue) {
        // 从false -> true
        this.canvasStyle.transition = 'none'
        this.canvasStyle.opacity = 1
      } else {
        // 从true -> false
        this.canvasStyle.transition = 'opacity 0.5s'
        this.canvasStyle.opacity = 0
      }
    },
    '$store.state.stopBkgMusic': function (newValue) {
      console.log(newValue)
      if (newValue) {
        this.$refs.bkgMusic.pause()
        this.bkgMusicState = false
      } else {
        this.$refs.bkgMusic.play()
        this.bkgMusicState = true
      }
    }
  },
  methods: {
    // 播放背景音乐
    playMusic: function () {
      if (this.$refs.bkgMusic.paused) {
        this.$refs.bkgMusic.play()
        this.bkgMusicState = true
      } else {
        this.$refs.bkgMusic.pause()
        this.bkgMusicState = false
      }
    }
  }
}
</script>

<style>
.window>div {
  position: relative;
  background-color: #313237;
}

.musicIcon {
  width: 3rem;
  height: 3rem;
  position:  absolute;
  z-index: 3501;
  right: 0.5rem;
  top: 0.5rem;
}

.musicIcon>img {
  width: 100%;
  cursor: pointer;
}

.musicIcon>img[role=playing] {
  animation: musicRotate 1s 0s cubic-bezier(.09, .57, .49, .9) infinite;
}

@keyframes musicRotate {
  0% {transform: rotate(0deg);}
  30% {transform: rotate(-18deg);}
  90% {transform: rotate(9deg);}
  100% {transform: rotate(0deg);}
}
</style>


