import { DataModelFactory, DataModelType } from '../core/DataModel.js'
import { ResourceManager } from '../managers/ResourceManager.js'
import { Billboard } from './Billboard.js'

function PageCamera (fov, aspect, near, far, cameraControl) {
  // Parent constructor //////////////////////////////////////////////
  THREE.PerspectiveCamera.call(this, fov, aspect, near, far)
  // Billboard.call(this);

  this.target = new THREE.Vector3(0, 0, 0)

  // 构造数据模型
  DataModelFactory.call(this, DataModelType.CAMERA)

  this.position.z = 5

  this.cameraControl = cameraControl

  this.billBoard = new Billboard(cameraControl)
  this.billBoard.camera = this
  this.billBoard.isCusReturn = true
  this.billBoard.cusReturn = function () {
    return this.camera
  }

  this.helper = new THREE.CameraHelper(this)

  this.update()

  this.dispatchEvent(this.changeEvent)
}

PageCamera.prototype = Object.assign(Object.create(THREE.PerspectiveCamera.prototype), Object.create(THREE.EventDispatcher.prototype), {
  constructor: PageCamera,

  isCustom: true,

  isNeedUpdate: true,

  changeEvent: { type: 'change' },

  update: function () {
    this.updateMatrixWorld()
    this.updateProjectionMatrix()

    this.billBoard.setRotationFromQuaternion(this.getWorldQuaternion().normalize())
    this.billBoard.position.setFromMatrixPosition(this.matrixWorld)
    this.billBoard.update()

    this.helper.update()
  },

  addToScene: function (scene) {
    this.scene = scene
    this.hideList = scene.pageHideList

    var resourceManager = new ResourceManager()

    this.billBoard.setTexture('static/default/images/camera.png')

    scene.add(this.billBoard, true)
    scene.add(this.helper)

    // 将镜头布告板和辅助器添加到隐藏列表中
    this.hideList.push(this.billBoard)
    this.hideList.push(this.helper)
  },

  removeFromScene: function () {
    scene.remove(this.billBoard, true)
    scene.remove(this.helper)

    var index = this.hideList.indexOf(this.billBoard)
    if (index > -1) { this.hideList.splice(index, 1) }

    index = this.hideList.indexOf(this.helper)
    if (index > -1) { this.hideList.splice(index, 1) }
  },

  copyFromCamera: function (camera) {
    this.dataModel.position = camera.position.clone()
    this.dataModel.quaternion = camera.quaternion.clone()
    this.dataModel.scale = camera.scale.clone()

    this.dataModel.target = this.cameraControl.target.clone()

    this.update()

    this.dispatchEvent(this.changeEvent)
  }
})

export { PageCamera }



