  // Private Method //////////////////////////////////
  // 渲染循环
  function animate () {
    if (isBrowser) { stats.begin() }
    // if (platform.os.family === 'Android') {
    //   currentTime = new Date().getTime()
    //   delta = currentTime - previousTime

    //   if (delta < 30) { return }
    // }

    curSelected = sceneManager.getCurSelected()
    // 当前选择对象不为空
    if (!checkNone(curSelected) && !checkNone(curSelected.dataModel)) {
      // 前一个选择对象为空 或者 选择的对象数据发生改变
      if (checkNone(lastDataModel) || !curSelected.dataModel.equal(lastDataModel)) {
        lastDataModel = curSelected.dataModel.clone()
        if (curSelected.isMesh) {
          engineInterface.curMeshData.update()

          var curAnimaChange = pageManager.getCurAnimaChange()
          if (!checkNone(curAnimaChange)) {
            engineInterface.curAniMeshData.update()

            var curKeyFrame = pageManager.getCurKeyFrame()
            if (!checkNone(curKeyFrame)) {
              curKeyFrame.setFromObj(curSelected)
              engineInterface.curAniMeshData.update()
            }
          }
        } else if (curSelected.isLight) {
          engineInterface.curLightData.update()
        } else if (curSelected.isMarkLine) {
          engineInterface.updateCurMarkLine()
        } else if (curSelected.isHotPoint) {
          engineInterface.updateCurHotPoint()
        }
        console.log('Notify!')
      }
    }
    // 如果当前选择为空
    else {
      // // 前一个数据对象不为空
      // if (!checkNone(curSelected) && !checkNone(lastDataModel)) {

      //   console.log(12313, curSelected)
      //   // if (curSelected.isMesh) {
      //   //   engineInterface.curMeshData.update()
      //   // }
      //   // else if (curSelected.isLight) {
      //   //   engineInterface.curLightData.update()
      //   // }

      //   console.log('Notify curSelect is none.')
      // }
      lastDataModel = null
    }

    // 由于要先隐藏元素，避免显示错误，所以先渲染页面窗口场景
    sceneManager.hideEditorObjects()

    if (isEditor) { sceneManager.findMaterialChangeObj() }

    if (isEditor) {
      // 渲染页面窗口场景
      pageRenderer.render(scene, pageCamera)
    } else if (isBrowser) {
      // 渲染编辑器窗口场景
      renderer.render(scene, camera)
    }

    if (isEditor) {
      sceneManager.showEditorObjects()
      sceneManager.updateMaterialChangeObj()
    }

    if (isEditor) {
      if (checkNone(curSelected)) { outlinePass.selectedObjects = [] } else {
        var tmpList = []
        tmpList.push(curSelected)
        outlinePass.selectedObjects = tmpList
      }
      // 渲染编辑器窗口场景
      composer.render()
      // renderer.render(scene, camera)
    }

    pageManager.update(pageRenderer)

    // 用于镜头平滑移动更新
    sceneManager.update()

    animationManager.update()

    if (isBrowser) {
      stats.end()
      engineInterface.fps = stats.fps
    }
    // console.log('fps', stats.fps)

    requestAnimationFrame(animate)
  }

