import {} from './Internal.js'
import {
  TextureType
} from '../core/Type.js'
import Widget from '../objects/Widget.js'

var ReleaseCode = {
  UPLOADING: 0,
  SUCCEED: 1
}

function EngineInterface () {
  // Singleton pattern ////////////////////////////////////////////////
  if (EngineInterface._instance !== undefined) {
    return EngineInterface._instance
  }
  EngineInterface._instance = this

  // Public variables ////////////////////////////////////////////////////
  this.debugInfo = ''

  if (isEditor) {
    this.hasSaved = true
    // errorCode :
    // - 0 : 默认值
    // - 100 : 打开成功
    // - 201 : 路径错误
    // - 202 : 目录不为空
    // - 203 : 加载失败
    this.loadingProjectData = {
      curProgress: 0,
      hasFinished: true,
      errorCode: 0
    }

    this.releaseData = {
      releasingState: ReleaseCode.UPLOADING,
      releasingInfo: ''
    }
  }

  this.pageList = [] // 页面列表

  this.imageList = [] // 图片资源列表
  this.modelList = [] // 模型资源列表
  this.meshList = [] // 场景模型列表
  this.lightList = [] // 场景灯光列表

  this.curSelectUI = []

  // 当前选择页面ID
  this.curPageId = -1

  // 当前选择场景模型的ID
  this.curMeshId = -1
  // 当前选择的场景模型的数据
  this.curMeshData = {
    position: { x: 0, y: 0, z: 0 }, // 位置
    rotation: { x: 0, y: 0, z: 0 }, // 旋转
    scale: { x: 0, y: 0, z: 0 }, // 缩放

    material: {
      type: 0
    }
  }

  // 当前选择灯光的ID
  this.curLightId = -1
  // 当前选择的场景灯光的数据
  this.curLightData = {
    position: { x: 0, y: 0, z: 0 }, // 位置
    rotation: { x: 0, y: 0, z: 0 }, // 旋转
    scale: { x: 0, y: 0, z: 0 } // 缩放
  }

  this.sceneData = {}

  // Public method /////////////////////////////////////////////
  // 导入资源到资源库中
  this.importResource = null

  // 删除资源库中的内容(不区分图片与模型)
  this.removeResource = null

  // 添加模型到场景中
  this.addMesh = null

  // 添加光源 0:平行光/ 1:点光源/ 2:聚光灯 3:矩形光
  this.addLight = null

  // 撤销操作
  this.undo = null

  // 设置当前的页面镜头
  this.setPageCamera = null

  // 材质页面关联物体
  this.attachMaterialToObj = null

  this.stopUpdate = false

  // 运行引擎
  this.runEngine = null

  if (isEditor) {
    this.projectList = []
    this.onlineProjectList = []
  }

  if (isBrowser) {
    var request = null
    if (window.XMLHttpRequest) {
      request = new XMLHttpRequest()
    } else if (window.ActiveXObject) {
      request = new ActiveXObject('Microsoft.XMLHTTP')
    }

    var jsonProject = null

    if (request) {
      // request.open("GET", projectFile, false)
      if (!isStatic) {
        var savePath = 'static/projects/' +
          window.location.href.match(/\/[^/]*?\/?$/)[0].split('/')[1].split('?')[0] +
          '/save.json'
      } else { var savePath = './static/projects/save.json' }

      request.open('GET', savePath, false)
      request.onreadystatechange = function () {
        if (request.readyState === 4) {
          if (request.status == 200 || request.status == 0) {
            jsonProject = JSON.parse(request.responseText)
          }
        }
      }
      request.send(null)
    }

    var jsonLoadPage = jsonProject.pageList[0]
    var jsonBackground = jsonLoadPage.background

    function convertToImgData (jsonImageList) {
      var imgsData = []
      jsonImageList.forEach(jsonImage => {
        var imgData = {}
        imgData.id = jsonImage.uiid
        imgData.width = jsonImage.width
        imgData.height = jsonImage.height
        imgData.dataX = jsonImage.dataX
        imgData.dataY = jsonImage.dataY
        imgData.rot = jsonImage.rot
        imgData.opacity = jsonImage.opacity
        imgData.zIndex = jsonImage.zIndex

        imgData.widget = new Widget()
        imgData.widget.marginTop = jsonImage.marginTop
        imgData.widget.marginBottom = jsonImage.marginBottom
        imgData.widget.marginLeft = jsonImage.marginLeft
        imgData.widget.marginRight = jsonImage.marginRight
        imgData.widget.widgetWidth = jsonImage.widgetWidth
        imgData.widget.widgetHeight = jsonImage.widgetHeight

        imgData.src = jsonImage.src

        imgsData.push(imgData)
      })

      return imgsData
    }

    function convertToTextData (jsonTextList) {
      var textsData = []
      jsonTextList.forEach(jsonText => {
        var textData = {}
        textData.id = jsonText.uiid
        textData.width = jsonText.width
        textData.height = jsonText.height
        textData.dataX = jsonText.dataX
        textData.dataY = jsonText.dataY
        textData.rot = jsonText.rot
        textData.opacity = jsonText.opacity
        textData.zIndex = jsonText.zIndex

        textData.widget = new Widget()
        textData.widget.marginTop = jsonText.marginTop
        textData.widget.marginBottom = jsonText.marginBottom
        textData.widget.marginLeft = jsonText.marginLeft
        textData.widget.marginRight = jsonText.marginRight
        textData.widget.widgetWidth = jsonText.widgetWidth
        textData.widget.widgetHeight = jsonText.widgetHeight

        textData.text = jsonText.text
        textData.fontSize = jsonText.fontSize
        textData.fontColor = jsonText.fontColor
        textData.textAlign = jsonText.textAlign

        textsData.push(textData)
      })

      return textsData
    }

    this.loadingPageData = {
      loadPageData: {
        loadingIcon: {
          rot: jsonLoadPage.rot,
          style: jsonLoadPage.style,
          color: jsonLoadPage.color,
          opacity: jsonLoadPage.opacity
        }

      },
      backgroundData: {
        bkgType: jsonBackground.type,
        bkgTex: jsonBackground.textureUrl,
        bkgColor: jsonBackground.color
      },
      curProgress: 0,
      hasFinished: false,
      pageUI: {
        imgsData: convertToImgData(jsonLoadPage.imgUIList),
        textData: convertToTextData(jsonLoadPage.textUIList)
      }
    }
  }

  if (isBrowser) { this.fps = 0 }
}

var engineInterface = new EngineInterface()

export { engineInterface }



