var setting = new Setting()

function SceneManager () {
  // Singleton pattern ////////////////////////////////////////////////
  if (SceneManager._instance !== undefined) {
    return SceneManager._instance
  }
  SceneManager._instance = this

  // Public variables ///////////////////////////////////////////////

  // Private variables //////////////////////////////////////////////////
  var scope = this

  // 获取用于GL渲染的画布
  var glCanvas = document.getElementById('glCanvas')

  // 唯一的场景
  var scene = new Scene()
  // 获取场景镜头
  var camera = scene.getCamera()

  // 资源管理器
  var resourceManager = new ResourceManager()

  // 存储管理器
  var storageManager = new StorageManager()

  // 页面管理器
  var pageManager = new PageManager()

  // 当前选择对象
  var curSelected = null
  // // 上一个选择对象的数据模型
  // var lastDataModel = null;

  // 镜头控制器
  // 镜头平移、旋转和缩放操作
  var cameraControl = new THREE.OrbitControls(camera, glCanvas)

  // 获取页面场景镜头
  var pageCamera = new PageCamera(75,
    glCanvas.clientWidth / glCanvas.clientHeight,
    0.01, 1000, cameraControl)

  // 操作控制器
  // 对象平移、旋转和缩放操作
  var transformControl = new THREE.TransformControls(camera, glCanvas)

  // 选择控制器，实现选择对象（物体、灯光等）功能
  var selectControl = new SelectControl(camera, glCanvas)

  var hideIndexList = []

  var materialUpdateList = []

  var background = new Background(camera, scene)

  // sid分配列表
  var sidList = {}

  // sid范围
  var sceneBeginId = 100
  var sceneEndId = 1000000

  var space = 'world'

  // Public methods //////////////////////////////////////////////////////////
  this.clear = function () {
    scope.removeAllObj()

    for (var arg in sidList) {
      delete sidList[arg]
    }

    scene.clear()

    scope.setCameraFov(75)
    scope.setEnv(0)
    scope.setEnvBlur(0)
    scope.setAmbientIntensity(0)
    scope.setHemisphereIntensity(0)
    // console.log(12123123, scene.dataModel)
    engineInterface.sceneData.update()
    // console.log(777777, scene.sceneObjList)
  }

  this.update = function () {
    cameraControl.update()

    // if (!checkNone(curSelected) && !checkNone(curSelected.dataModel)) {
    // 	if (checkNone(lastDataModel) || !curSelected.dataModel.equal(lastDataModel)) {
    // 		lastDa taModel = curSelected.dataModel.clone();
    // 		console.log("Notify!");
    // 	}
    // }
    // else {
    // 	if (!checkNone(lastDataModel)) {
    // 		console.log("Notify!");
    // 	}
    // 	lastDataModel = null;
    // }
  }

  // 获取当前场景
  this.getScene = function () {
    return scene
  }

  this.setCurSelected = function (sid) {
    var sceneObj = scene.sceneObjList[sid]

    // 取消选择上一个场景对象
    if (!checkNone(curSelected)) {
      // 用于关闭灯光辅助器
      if ('onDeselect' in curSelected) {
        curSelected.onDeselect()
      }
    }

    if (isEditor) { transformControl.detach() }

    if (sid <= 0) { engineInterface.clearCurSelectInternal() }

    curSelected = sceneObj

    if (checkNone(sceneObj)) {
      return false
    }

    if (isEditor)
    // 选择当前场景对象
    { transformControl.attach(curSelected) }

    selectControl.setCurSelected(curSelected)

    // 用于开启灯光辅助器
    if ('onSelect' in curSelected) {
      curSelected.onSelect()
    }
    if ('onClick' in curSelected) {
      curSelected.onClick()
    }
  }

  this.getCurSelected = function () {
    return curSelected
  }

  this.getCameraControl = function () {
    return cameraControl
  }

  this.getTransformControl = function () {
    return transformControl
  }

  // 获取页面镜头
  this.getPageCamera = function () {
    return pageCamera
  }

  this.getObjBySid = function (sid) {
    return scene.sceneObjList[sid]
  }

  this.showEditorObjects = function () {
    if (!checkNone(transformControl.object)) { transformControl.visible = true }
    // if (!checkNone(scene.pageHideList))
    for (var index = 0; index < scene.pageHideList.length; ++index) {
      scene.pageHideList[index].visible = true
    }

    for (var index = 0; index < hideIndexList.length; ++index) {
      scene.pageHideList[hideIndexList[index]].visible = false
    }
    hideIndexList = []

    cameraControl.changeCamera(camera)
  }

  this.hideEditorObjects = function () {
    transformControl.visible = false
    // if (!checkNone(scene.pageHideList))
    for (var index = 0; index < scene.pageHideList.length; ++index) {
      if (scene.pageHideList[index].visible === false) {
        hideIndexList.push(index)
      }
      scene.pageHideList[index].visible = false
    }

    if (isEditor) { cameraControl.changeCamera(pageCamera) }
  }

  this.findMaterialChangeObj = function () {
    for (var index = 0; index < scene.objIntersectList.length; ++index) {
      var sceneObj = scene.objIntersectList[index]
      if (!checkNone(sceneObj.material)) {
        if (sceneObj.material.needsUpdate) { materialUpdateList.push(sceneObj) }
      }
    }
  }

  this.updateMaterialChangeObj = function () {
    for (var index = 0; index < scene.objIntersectList.length; ++index) {
      var sceneObj = scene.objIntersectList[index]
      sceneObj.material.needsUpdate = true
    }
  }

  this.setCameraFov = function (nFov) {
    scene.setCameraFov(nFov)

    pageCamera.fov = nFov
    pageCamera.update()

    // pageManager.curPage.dataModel.scene.fov = nFov;
  }

  // 场景全部显示
  this.setWholeView = function () {
    var sceneObjList = scene.sceneObjList
    var box3 = new THREE.Box3()
    var vMin = new THREE.Vector3()
    var vMax = new THREE.Vector3()
    var spherical = cameraControl.getSpherical().clone()

    var bFirst = true

    for (var sid in sceneObjList) {
      var sceneObj = sceneObjList[sid]
      if (sceneObj.isMesh) {
        var box3 = box3.setFromObject(sceneObj)
        if (bFirst) {
          vMin.copy(box3.min)
          vMax.copy(box3.max)
          bFirst = false
        } else {
          vMin.min(box3.min)
          vMax.max(box3.max)
        }
      }
    }

    if (bFirst) { return }

    var targetPos = new THREE.Vector3().addVectors(vMin, vMax).divideScalar(2)
    // var distanceToOrigin = targetPos.distanceTo(new THREE.Vector3())
    cameraControl.target = targetPos

    // var nearPlane = new THREE.Plane(new THREE.Vector3(0, 0, -1));
    // nearPlane.translate(new THREE.Vector3(0, 0, -camera.near))
    // nearPlane.applyMatrix4(camera.matrixWorld)

    var horizontalPlane = new THREE.Plane(new THREE.Vector3(0, 1, 0))
    horizontalPlane.applyMatrix4(camera.matrixWorld)
    horizontalPlane.constant = 0
    horizontalPlane.translate(targetPos)

    var verticalPlane = new THREE.Plane(new THREE.Vector3(1, 0, 0))
    verticalPlane.applyMatrix4(camera.matrixWorld)
    verticalPlane.constant = 0
    verticalPlane.translate(targetPos)

    var targetPlane = new THREE.Plane(new THREE.Vector3(0, 0, -1))
    targetPlane.applyMatrix4(camera.matrixWorld)
    targetPlane.translate(cameraControl.target.clone().sub(camera.position))

    function getPoints (vMin, vMax) {
      var pointArr = []
      var vTmp = null
      pointArr.push(vMin)
      pointArr.push(vMax)

      vTmp = new THREE.Vector3().copy(vMin)
      vTmp.x = vMax.x
      pointArr.push(vTmp)
      vTmp = new THREE.Vector3().copy(vMin)
      vTmp.y = vMax.y
      pointArr.push(vTmp)
      vTmp = new THREE.Vector3().copy(vMin)
      vTmp.z = vMax.z
      pointArr.push(vTmp)
      vTmp = new THREE.Vector3().copy(vMax)
      vTmp.x = vMin.x
      pointArr.push(vTmp)
      vTmp = new THREE.Vector3().copy(vMax)
      vTmp.y = vMin.y
      pointArr.push(vTmp)
      vTmp = new THREE.Vector3().copy(vMax)
      vTmp.z = vMin.z
      pointArr.push(vTmp)

      return pointArr
    }

    function calRadius (point) {
      var farVal = 0
      var nearVal = 0

      var horiz = verticalPlane.distanceToPoint(point)
      var vert = horizontalPlane.distanceToPoint(point)

      horiz = Math.abs(horiz)
      vert = Math.abs(vert)

      var height = camera.near * Math.tan(camera.fov / 2 / 180 * Math.PI)
      var width = camera.aspect * height
      var depth = targetPlane.distanceToPoint(point)
      // var depth = nearPlane.distanceToPoint(point)

      if (horiz < vert * camera.aspect) {
        farVal = vert
        nearVal = height
      } else {
        farVal = horiz
        nearVal = width
      }

      var radius = camera.near / nearVal * farVal

      if (depth < 0) { radius -= depth }
      // console.log('dr', depth, radius)
      // var radius = depth - radius
      // console.log(depth)

      // if (radius < 0)
      //   radius = spherical.radius - radius
      // else {

      //   return spherical.radius
      // }
      return radius
    }

    var points = getPoints(vMin, vMax)

    var radius = 0
    points.forEach(point => {
      var res = calRadius(point)
      if (res > radius) { radius = res }
    })
    // var radius = calRadius(vMax)

    spherical.radius = radius

    var cameraPos = new THREE.Vector3()

    cameraPos.setFromSpherical(spherical).add(cameraControl.target)

    // cameraControl.moveTo(cameraControl.target, cameraPos)
    camera.position.copy(cameraPos)
    // console.log('v', vMin, vMax)
    // console.log('dist', distMin, distMax)
    // console.log('horizontal', horiMin, horiMax)
    // console.log('vert', vertMin, vertMax)
    // console.log('asp', camera.aspect)

    // calPoint(horiMin, vertMin, distMin)

    // var helper = new THREE.PlaneHelper( nearPlane, 3, 0xffff00 );
    // var vhelper = new THREE.PlaneHelper( verticalPlane, 3, 0xffff00 );
    // var hhelper = new THREE.PlaneHelper( horizontalPlane, 3, 0xffff00 );
    // var thelper = new THREE.PlaneHelper( targetPlane, 3, 0xffff00 );
    // scene.add( helper );
    // scene.add( vhelper );
    // scene.add( hhelper );
    // scene.add( thelper );
  }

  // 加载环境贴图
  this.loadEnv = function (envUrl, callback) {
    scene.loadEnv(envUrl, callback)

    // pageManager.curPage.dataModel.scene.envUrl = envUrl;
  }

  this.setEnv = function (envIdx, callback) {
    scene.setEnv(envIdx, callback)
  }

  // blur是模糊半径
  this.setEnvBlur = function (blur) {
    scene.setEnvBlur(blur)
    // pageManager.curPage.dataModel.scene.blur = blur;
  }

  // 设置控制模式。（平移、旋转、缩放）
  this.setControlMode = function (mode) {
    if (typeof mode !== 'string') {
      console.log("SceneManager, setControlMode(), mode: \"Mode should be a \'string\'.\"")
      return
    }

    var restoreSpace = false
    switch (mode) {
      case 'translate':
      case 'rotate':
      case 'scale':
        scope.setPivotMode(false)
        transformControl.setSpace(space)
        break
      default:
        console.log('SceneManager, setControlMode(), space: "Invalid mode."')
        return
    }
    transformControl.setMode(mode)
  }

  // 设置transform控制器的操作的空间坐标系
  this.setSpace = function (changeSpace) {
    if (changeSpace !== 'world' && changeSpace !== 'local') {
      console.log('SceneManager, setSpace(), changeSpace: "Invalid space."')
      return
    }
    transformControl.setSpace(changeSpace)
    space = changeSpace
  }

  // 设置场景的默认环境光亮度
  this.setAmbientIntensity = function (intensity) {
    scene.setAmbientIntensity(intensity)
  }

  this.setHemisphereIntensity = function (intensity) {
    scene.setHemisphereIntensity(intensity)
  }

  // 设置当前对象名字
  this.setObjName = function (name) {
    // 检查当前对象和名字是否合法
    if (checkNone(curSelected)) {
      console.log('SceneManager, setObjName(), curSelected: "No scene object selected."')
    } else if (typeof name !== 'string') {
      console.log("SceneManager, setObjName(), name: \"Name should be a \'string\'.\"")
    } else {
      curSelected.name = name
      console.log(curSelected)
    }
  }

  // 根据Url向场景中添加物体
  this.addMesh = function (modelUrl, name, sid) {
    // 检查url是否为字符串
    if (typeof modelUrl !== 'string') {
      console.log("SceneManager, addMesh(), modelUrl: \"Url should be a \'string\'.\"")
      return false
    }

    var mesh = resourceManager.getModel(modelUrl, name)

    if (checkNone(mesh)) {
      console.log('SceneManager, addMesh(), mesh: "getMesh(modelUrl) failed."')
    } else {
      if (!checkNone(mesh.dataModel)) {
        mesh.dataModel.modelUrl = modelUrl
      }

      if (checkNone(sid)) {
        scope.addSid(mesh)
        // if (setting.loadAllPageObj){
        // 	scene.intersectList.push(mesh);
        // 	scene.add(mesh);
        // 	pageManager.addSceneObj(mesh);
        // }
      } else {
        scope.setId(mesh, sid)
      }

      // scene.objIntersectList.push(mesh);
      scene.add(mesh)

      if (isEditor && !checkNone(mesh.orgMaterialUrl)) {
        scope.setTexture(mesh.orgMaterialUrl, mesh)
      }

      // if (!setting.loadAllPageObj) {
      // 	scene.intersectList.push(mesh);
      // 	scene.add(mesh);
      // 	pageManager.addSceneObj(mesh);
      // }

      var curPage = pageManager.getCurPage()
      if (checkNone(sid) && !checkNone(curPage)) {
        if (curPage.pageType === PageType.MATERIAL) {
          if (!checkNone(pageManager.jsonMaterialList)) {
            // pageManager.jsonMaterialList = {}
            pageManager.jsonMaterialList[mesh.sid] = mesh.dataModel.material.clone()
          }
          // console.log(pageManager.jsonMaterialList)
        } else if (curPage.pageType === PageType.ANIMATION) {
          if (!checkNone(pageManager.jsonModelChangeList)) {
            var modelChange = new ModelChangeDataModel()
            modelChange.setFrom(mesh)
            pageManager.jsonModelChangeList[mesh.sid] = modelChange
            console.log(modelChange)
          }
        }
      }

      return mesh
    }
  }

  this.setColor = function (color, sceneObj, fixDataModel) {
    if (checkNone(sceneObj)) { sceneObj = curSelected }
    scene.setColor(sceneObj, color, fixDataModel)
  }

  // 为场景物体设置贴图
  this.setTexture = function (imgUrl, sceneObj, fixDataModel) {
    // 检查url是否为字符串
    if (typeof imgUrl !== 'string') {
      console.log("SceneManager, setTexture(), imgUrl: \"Url should be a \'string\'.\"")
      return
    }

    if (checkNone(sceneObj)) { sceneObj = curSelected }

    // 当前选择对象为空
    if (checkNone(sceneObj)) {
      console.log('SceneManager, setTexture(), curSelected: "No scene object selected."')
      return
    }

    if (checkNone(imgUrl) || imgUrl === '') {
      scene.setTexture(sceneObj, null, fixDataModel)
      return
    }

    var texture = resourceManager.getTexture(imgUrl)

    // if (checkNone(texture)) {
    //   // console.log('SceneManager, setTexture(), texture: "getTexture(imgUrl) failed."')
    //   scene.setTexture(sceneObj, null, fixDataModel)
    // }
    if (!checkNone(texture)) {
      texture.wrapS = THREE.RepeatWrapping
      texture.wrapT = THREE.RepeatWrapping
    }

    scene.setTexture(sceneObj, texture, fixDataModel)

    if (!checkNone(sceneObj.dataModel) &&
      !checkNone(sceneObj.dataModel.material) &&
      !fixDataModel) {
      if (checkNone(texture)) { sceneObj.dataModel.material.textureUrl = '' } else { sceneObj.dataModel.material.textureUrl = imgUrl }
    }
  }

  this.setUVTranslate = function (tx, ty, sceneObj) {
    if (checkNone(sceneObj)) { sceneObj = curSelected }

    // 当前选择对象为空
    if (checkNone(sceneObj)) {
      console.log('SceneManager, setUVTranslate(), curSelected: "No scene object selected."')
      return
    }

    var texture = sceneObj.material.map
    if (!checkNone(texture)) {
      texture.offset.x = tx
      texture.offset.y = ty

      sceneObj.dataModel.material.uvData.tx = tx
      sceneObj.dataModel.material.uvData.ty = ty
      console.log(sceneObj.dataModel.material.uvData)
    }
  }

  this.setUVScale = function (sx, sy, sceneObj) {
    if (checkNone(sceneObj)) { sceneObj = curSelected }

    // 当前选择对象为空
    if (checkNone(sceneObj)) {
      console.log('SceneManager, setUVTranslate(), curSelected: "No scene object selected."')
      return
    }

    var texture = sceneObj.material.map
    if (!checkNone(texture)) {
      texture.repeat.set(sx, sy)
      sceneObj.dataModel.material.uvData.sx = sx
      sceneObj.dataModel.material.uvData.sy = sy
    }
  }

  // 添加灯光
  this.addLight = function (lightType, sid) {
    // 检查url是否为字符串
    if (typeof lightType !== 'string') {
      console.log("SceneManager, addLight(), lightType: \"lightType should be a \'string\'.\"")
      return
    }

    var light = null

    switch (lightType) {
      case 'Spot':
        light = new SpotLight(0xffffff)
        light.name = '聚光灯'
        break
      case 'Direction':
        light = new DirectionalLight(0xffffff)
        light.name = '平行光'
        break
      case 'Point':
        light = new PointLight(0xffffff)
        light.name = '点光'
        break
      case 'RectArea':
        light = new RectAreaLight(0xffffff)
        light.name = '矩形光'
        break
      default:
        console.log('SceneManager, addLight(), lightType: "Invalid light type."')
        return
    }

    if (checkNone(sid)) {
      scope.addSid(light)
    } else {
      scope.setId(light, sid)
    }

    scene.add(light)

    if (checkNone(sid)) {
      engineInterface.curLightId = light.sid
    }

    return light

    // console.log(scene.lightList);
  }

  // 场景添加标注线，类型为x,y,z;align为文本的对齐方式
  this.addMarkLine = function (text, textType, align, fontUrl, sid) {
    var pText = text || 'default'
    var pType = textType || TextDirectionType.HORIZONTAL
    var pFontUrl = fontUrl || 'static/default/fonts/helvetiker_bold.typeface'
    // var pFontUrl = fontUrl || 'static/default/fonts/SimHei_Regular.typeface'
    var pFont = resourceManager.getFont(pFontUrl)

    var pAlign = align || TextAlignType.MIDDLE

    var markLine = new MarkLine(pText, pType, pAlign, pFont, curSelected)

    if (checkNone(sid)) {
      scope.addSid(markLine)
    } else {
      scope.setId(markLine, sid)
    }

    if (!checkNone(markLine.dataModel)) {
      markLine.dataModel.fontUrl = pFontUrl
    }

    scene.add(markLine)

    console.log(markLine)

    // console.log(nMark);
    return markLine
  }

  this.addAutoMarkLine = function () {
    var sceneObjList = scene.sceneObjList
    var box3 = new THREE.Box3()
    var vMin = new THREE.Vector3()
    var vMax = new THREE.Vector3()

    var bFirst = true

    for (var sid in sceneObjList) {
      var sceneObj = sceneObjList[sid]
      if (sceneObj.isMesh) {
        var box3 = box3.setFromObject(sceneObj)
        if (bFirst) {
          vMin.copy(box3.min)
          vMax.copy(box3.max)
          bFirst = false
        } else {
          vMin.min(box3.min)
          vMax.max(box3.max)
        }
      }
    }

    if (bFirst) { return }

    var xMarkLine = scope.addMarkLine()
    var yMarkLine = scope.addMarkLine()
    var zMarkLine = scope.addMarkLine()

    scene.setObjPos(xMarkLine, transformControl.space,
      (vMax.x + vMin.x) / 2,
      vMin.y,
      vMax.z)
    scene.setObjPos(yMarkLine, transformControl.space,
      vMin.x,
      (vMax.y + vMin.y) / 2,
      vMax.z)
    scene.setObjPos(zMarkLine, transformControl.space,
      vMax.x,
      vMin.y,
      (vMax.z + vMin.z) / 2)

    // scene.setObjRotate(xMarkLine, transformControl.space,
    //   0, 0, 0)
    scene.setObjRotate(yMarkLine, transformControl.space,
      0, 0, 90 / 180 * Math.PI)
    scene.setObjRotate(zMarkLine, transformControl.space,
      0, 90 / 180 * Math.PI, 0)

    scope.setMarkLine({
      markLength: vMax.x - vMin.x,
      lineRadius: 0.002,
      fontSize: 4
    }, xMarkLine)

    scope.setMarkLine({
      markLength: vMax.y - vMin.y,
      lineRadius: 0.002,
      fontSize: 4
    }, yMarkLine)

    scope.setMarkLine({
      markLength: vMax.z - vMin.z,
      lineRadius: 0.002,
      fontSize: 4
    }, zMarkLine)

    var markLineList = []
    markLineList.push(xMarkLine)
    markLineList.push(yMarkLine)
    markLineList.push(zMarkLine)

    return markLineList
  }

  // 增加热点标注
  this.addHotPoint = function (position, sid) {
    var hotPoint = new HotPoint(cameraControl, position)

    if (checkNone(sid)) {
      scope.addSid(hotPoint)
    } else {
      scope.setId(hotPoint, sid)
    }

    scene.add(hotPoint)

    return hotPoint
  }

  // 删除选定对象
  this.removeObj = function (sceneObj) {
    var removedObj = null
    // 检查当前对象是否合法
    if (checkNone(sceneObj)) {
      if (checkNone(curSelected)) {
        console.log('SceneManager, removeObj(), curSelected: "No scene object selected."')
        return
      }
      removedObj = curSelected
      selectControl.clearCurselected()
    } else {
      removedObj = sceneObj
    }

    var res = true

    // 删除物体本身
    scope.removeSid(removedObj)
    // pageManager.curPage.remove(curSelected);
    res = res & scene.remove(removedObj)

    if (res) {
      // 如果删除的是当前选择的对象
      if (removedObj === curSelected) {
        // 对象删除成功，断开transform控制器的连接
        curSelected = null
        transformControl.detach()
      }
      console.log('Remove success.')
    } else {
      console.log('SceneManager, removeObj(), ?: "Remove failed, need debug."')
    }

    return res
  }

  this.removeAllObj = function () {
    var sceneObjList = scene.sceneObjList
    var res = true

    for (var sid in sceneObjList) {
      var sceneObj = sceneObjList[sid]

      if (checkNone(sceneObj)) { continue }

      res = res & scope.removeObj(sceneObj)

      if (res) { console.log('Remove success.') } else { break }
    }

    if (res) {
      // 对象删除成功，断开transform控制器的连接
      curSelected = null
      transformControl.detach()
      console.log('Remove all obj success.')
    } else {
      console.log('SceneManager, removeAllObj(), ?: "Remove failed, need debug."')
    }
    return res
  }

  // 设置物体的位置，当控制器的空间为world，即全局时，则设置的是世界坐标
  // 若为Local则设置的是局部坐标
  this.setObjPos = function (x, y, z) {
    var res = scene.setObjPos(curSelected, transformControl.space, x, y, z)
    if (res) {
      // 更新控制器位置
      transformControl.update()
    }
  }

  // 设置对象的旋转角度，x,y,z代表绕这几个轴旋转的角度，单位是radian
  // 若移动控制器的space是world，则x,y,z代表的是世界空间的轴
  // 若space是local，则x,y,z代表的是局部坐标下的轴
  this.setObjRotate = function (x, y, z) {
    console.log('space:', transformControl.space)
    var res = scene.setObjRotate(curSelected, transformControl.space, x, y, z)
    if (res) {
      // 更新控制器位置
      transformControl.update()
    }
  }

  // 只对局部坐标系进行缩放，毕竟针对全局进行缩放会造成模型变形
  this.setObjScale = function (x, y, z) {
    scene.setObjScale(curSelected, x, y, z)
  }

  this.setPivotMode = function (isPivotMode) {
    transformControl.pivotMode = isPivotMode
  }

  this.setObjPivotPos = function (x, y, z) {
    var res = scene.setObjPivotPos(curSelected, transformControl.space, x, y, z)
    if (res) {
      // 更新控制器位置
      transformControl.update()
    }
  }

  // 获得当前选中物体的坐标，space表示坐标空间
  this.getObjPos = function (space) {
    if (curSelected === null || curSelected === undefined) return
    if (!(curSelected instanceof THREE.Object3D)) return

    if (space === 'world') { return curSelected.getWorldPosition() } else { return curSelected.position.clone() }
  }

  // 获得当前物体的旋转角(欧拉角)，space代表坐标空间
  this.getObjRotate = function (space) {
    if (curSelected === null || curSelected === undefined) return
    if (!(curSelected instanceof THREE.Object3D)) return

    if (space === 'world') { return curSelected.getWorldRotation() } else { return curSelected.rotation.clone() }
  }

  // 获得当前选中物体的缩放比例，space代表坐标空间
  this.getObjScale = function (space) {
    if (curSelected === null || curSelected === undefined) return
    if (!(curSelected instanceof THREE.Object3D)) return

    if (space === 'world') { return curSelected.getWorldScale() } else { return curSelected.scale.clone() }
  }

  this.setLightCastShadow = function (castShadow) {
    scene.setLightCastShadow(curSelected, castShadow)
  }

  // 设置灯光的颜色
  this.setLightColor = function (color) {
    scene.setLightColor(curSelected, color)
  }

  // 设置灯光的强度
  this.setLightIntensity = function (intensity) {
    scene.setLightIntensity(curSelected, intensity)
  }

  // //设置物体为透明物体
  // this.setToTransparent = function(ifToggle){
  // 	scene.setToTransparent(curSelected, ifToggle);
  // };

  // 设置当前物体的透明度
  this.setObjOpacity = function (opacity) {
    scene.setObjOpacity(curSelected, opacity)
  }

  // 设置当前物体的光泽度
  // this.setShininess = function (shininess) {
  //   scene.setShininess(curSelected, shininess)
  // }

  this.setRoughness = function (roughness) {
    scene.setRoughness(curSelected, roughness)
  }

  this.setMetalness = function (metalness) {
    scene.setMetalness(curSelected, metalness)
  }

  this.setClearCoat = function (clearCoat) {
    scene.setClearCoat(curSelected, clearCoat)
  }

  this.setClearCoatRoughness = function (clearCoat) {
    scene.setClearCoatRoughness(curSelected, clearCoat)
  }

  this.setReflectivity = function (reflectivity) {
    scene.setReflectivity(curSelected, reflectivity)
  }

  // 设置标注线相关属性
  this.setMarkLine = function (parameter, sceneObj) {
    var markLine = null
    // 如果没有传入对象，则对当前选定对象进行操作
    if (checkNone(sceneObj)) {
      if (checkNone(curSelected)) {
        console.log('SceneManager, setMarkLine(), curSelected: "No scene object selected."')
        return
      }
      if (!curSelected.isMarkLine) { return false }
      markLine = curSelected
    } else {
      markLine = sceneObj
    }

    if (parameter === null || parameter === undefined) return
    if (parameter.font) {
      var fontUrl = parameter.font
      var nFont = resourceManager.getFont(parameter.font)
      parameter.font = nFont

      markLine.dataModel.fontUrl = fontUrl
    }

    scene.setMarkLine(markLine, parameter)
  }

  // 设置热点标注颜色
  this.setHotPointColor = function (color) {
    if (curSelected === null || curSelected === undefined) return
    if (!(curSelected.isHotPoint)) return
    curSelected.color = color
  }

  // 设置热点的尺寸,单位为屏幕像素
  this.setHotPointSize = function (size) {
    if (curSelected === null || curSelected === undefined) return
    if (!(curSelected.isHotPoint)) return
    if (size < 0) size = 0
    curSelected.size = size
  }

  // 设置热点控制的摄像机位置
  this.setHotPointCameraPos = function () {
    if (curSelected === null || curSelected === undefined || !(curSelected.isHotPoint)) return
    curSelected.setCameraPos()
  }

  // 设置当前是否为增加热点模式
  this.setHotPointMode = function (ifToggle) {
    glCanvas.addEventListener('clickIntersect', onMouseClickAndGenerateHotPoint)

    // if(ifToggle){
    // 	//进入热点生成模式
    // 	glCanvas.removeEventListener('deselect',onMouseDeselected);
    // 	glCanvas.removeEventListener('select',onMouseSelected);
    // 	glCanvas.addEventListener('clickIntersect',onMouseClickAndGenerateHotPoint);

    // 	if(curSelected !== null && 'onDeselect' in curSelected)
    // 		curSelected.onDeselect();
    // 	transformControl.detach();
    // 	curSelected = null;

    // }else{
    // 	//退出
    // 	glCanvas.addEventListener('deselect',onMouseDeselected);
    // 	glCanvas.addEventListener('select',onMouseSelected);
    // 	glCanvas.removeEventListener('clickIntersect',onMouseClickAndGenerateHotPoint);
    // }
  }

  this.getBackground = function () {
    return background
  }

  this.setBackgroundColor = function (color, ifNone) {
    // 设置侧边栏显示的背景颜色
    background.setColor(color, ifNone)
    // 设置存储中的背景数据
    pageManager.getCurPage().setBackground(background)
  }

  this.setBackgroundTexture = function (texUrl) {
    // 设置侧边栏显示的背景图片
    background.setTexture(texUrl)
    // 设置存储中的背景数据
    pageManager.getCurPage().setBackground(background)
  }

  // 锁定镜头
  this.lockCamera = function () {
    if (isBrowser) {
      cameraControl.needOperateListener = false
      cameraControl.dispose()
    }

    var pageManager = new PageManager()
    var curPage = pageManager.getCurPage()

    if (!checkNone(curPage) && curPage.pageType === PageType.MATERIAL) {
      curPage.isCameraLock = true
    }
  }

  // 解锁镜头
  this.unlockCamera = function (ifChangeDataModel) {
    if (isBrowser) {
      cameraControl.needOperateListener = true
      cameraControl.addListener()
    }

    if (!checkNone(ifChangeDataModel)) {
      var pageManager = new PageManager()
      var curPage = pageManager.getCurPage()

      if (!checkNone(curPage) && curPage.pageType === PageType.MATERIAL) {
        curPage.isCameraLock = false
      }
    }
  }

  // 新添加具有数据模型的对象到列表中
  this.addSid = function (sceneObj) {
    var sid = getId(sceneBeginId, sceneEndId)

    if (sid === 0) {
      console.log('SceneManager, addSid(), sid: "Failed to get storage id."')
      return false
    }
    if (!checkNone(sceneObj.dataModel)) {
      sceneObj.sid = sid
      sidList[sid] = true
    }
    return true
  }

  // 从模型列表中删除对象的数据模型
  this.removeSid = function (sceneObj) {
    if (checkNone(sceneObj.dataModel)) {
      return
    }
    var sid = sceneObj.sid
    if (checkId(sid)) {
      delete sidList[sid]
    }
  }

  // 设置对象的sid
  this.setId = function (sceneObj, sid) {
    if (!checkId(sid)) {
      sidList[sid] = true
      sceneObj.sid = sid
    } else {
      scope.addSid(sceneObj)
    }
  }

  this.addListener = function () {
    // 鼠标滚轮事件，用来滚轮事件更新transformControl
    glCanvas.addEventListener('wheel', onMouseScroll)

    // 选择对象事件
    glCanvas.addEventListener('select', onMouseSelected)

    // 取消选择对象事件
    glCanvas.addEventListener('deselect', onMouseDeselected)

    if (isEditor) {
      // 在操作Transform时，屏蔽掉镜头控制器
      transformControl.addEventListener('transStart', onTransStart)

      // 在操作Transform时，屏蔽掉镜头控制器
      transformControl.addEventListener('transEnd', onTransEnd)

      transformControl.addEventListener('objectChange', onObjectChange)
    }

    cameraControl.addEventListener('autoTransEnd', onAutoTransEnd)

    cameraControl.addListener()
  }

  this.disposeListener = function () {
    // 鼠标滚轮事件，用来滚轮事件更新transformControl
    glCanvas.removeEventListener('wheel', onMouseScroll)

    // 选择对象事件
    glCanvas.removeEventListener('select', onMouseSelected)

    // 取消选择对象事件
    glCanvas.removeEventListener('deselect', onMouseDeselected)

    if (isEditor) {
      // 在操作Transform时，屏蔽掉镜头控制器
      transformControl.removeEventListener('transStart', onTransStart)

      // 在操作Transform时，屏蔽掉镜头控制器
      transformControl.removeEventListener('transEnd', onTransEnd)

      transformControl.removeEventListener('objectChange', onObjectChange)
    }

    cameraControl.removeEventListener('autoTransEnd', onAutoTransEnd)

    cameraControl.dispose()
  }

  // Private methods ////////////////////////////////////////////////////

  function init () {
    // 初始化监听事件
    scope.addListener()

    // 设置可选择物体
    selectControl.spriteIntersectList = scene.spriteIntersectList
    selectControl.objIntersectList = scene.objIntersectList
    cameraControl.objIntersectList = scene.objIntersectList

    if (isEditor) {
      // 添加transform控制器到场景中以供显示
      scene.add(transformControl)

      scene.add(pageCamera)

      scope.setSpace('world')

      scope.setHotPointMode(true)
    }

    // window.addEventListener('resize', function (event) {
    //   var camera = scope.getScene().getCamera()
    //   camera.aspect = glCanvas.clientWidth / glCanvas.clientHeight
    //   camera.updateProjectionMatrix()

    //   var curPage = pageManager.getCurPage()
    //   if (isEditor && !checkNone(curPage)) {
    //     var canvas = document.getElementById('glPageCanvas' + curPage.pid)
    //     var pageCamera = scope.getPageCamera()
    //     pageCamera.aspect = canvas.clientWidth / canvas.clientHeight
    //     pageCamera.updateProjectionMatrix()
    //   }
    // })
  }

  // 检查sid是否已经被用，已经使用返回true，否则返回false
  function checkId (sid) {
    if (checkNone(sidList[sid])) {
      return false
    }
    return sidList[sid]
  }

  // 获取当前可用的最小id,如果为0,获取失败
  function getId (begin, end) {
    if (begin >= end) {
      return 0
    }

    for (var indexId = begin; indexId < end; ++indexId) {
      if (!checkId(indexId)) {
        return indexId
      }
    }
    return 0
  }

  function onMouseScroll (event) {
    event.preventDefault()
    transformControl.update()
  }

  function onMouseSelected (event) {
    transformControl.attach(event.object)
    curSelected = event.object

    // 释放event的指针
    event.object = null
    // 用于开启灯光辅助器
    if ('onSelect' in curSelected) {
      curSelected.onSelect()
    }

    // 用于响应页面布告板点击
    if ('onClick' in curSelected) {
      curSelected.onClick()
    }

    // engineInterface.clearCurSelect()

    var pageManager = new PageManager()
    var curPage = pageManager.getCurPage()

    if (!checkNone(curPage)) {
      if (curPage.pageType === PageType.ANIMATION) {
        var curAnimation = curPage.getCurButton()

        if (!checkNone(curAnimation)) {
          curAnimation.setCurAnimationChangeFromSelected()
        }
      }

      if (curPage.pageType === PageType.MATERIAL) {
        var curMaterial = curPage.getCurButton()

        if (!checkNone(curMaterial)) {
          curMaterial.setCurMaterialChangeFromSelected()
        }
      }
    }

    if (curSelected.isMesh) {
      engineInterface.curMeshId = curSelected.sid
    } else if (curSelected.isLight) {
      engineInterface.curLightId = curSelected.sid
    } else if (curSelected.isMarkLine) {
      engineInterface.curMarkLineId = curSelected.sid
    } else if (curSelected.isHotPoint) {
      engineInterface.curHotPointId = curSelected.sid

      pageCamera.position.copy(curSelected.cameraPos)
      pageCamera.lookAt(curSelected.cameraTarget)
      pageCamera.update()
    }
  }

  function onMouseDeselected (event) {
    var lastSelected = event.object

    // 释放event的指针
    event.object = null

    if (!checkNone(lastSelected)) {
      // 用于关闭灯光辅助器
      if ('onDeselect' in lastSelected) {
        lastSelected.onDeselect()
      }

      if (lastSelected.isHotPoint) {
        var curPage = pageManager.getCurPage()
        if (!checkNone(curPage)) {
          pageCamera.position.copy(curPage.pageCamera.position)
          pageCamera.lookAt(curPage.pageCamera.target)
        } else {
          pageCamera.position.set(0, 0, 5)
          pageCamera.lookAt(new THREE.Vector3(0, 0, 0))
        }
        pageCamera.update()
      }

      engineInterface.updateAnimationChangeIndex(-1)
      engineInterface.clearCurSelect()
    }

    transformControl.detach()
    curSelected = null
  }

  function onMouseClickAndGenerateHotPoint (event) {
    if (event === null || event === undefined) return
    if (event.object === null || event.object === undefined) return

    var curPage = pageManager.getCurPage()
    console.log(curPage)
    if (!checkNone(curPage) && curPage.pageType === PageType.HOTPOINT) {
      storageManager.begindo()

      curPage.addHotPoint(event.object.point)
      engineInterface.updatePageContent()

      storageManager.enddo()
    }

    if (curSelected !== null && 'onDeselect' in curSelected) { curSelected.onDeselect() }
    transformControl.detach()
    curSelected = null
  }

  function onTransStart (event) {
    cameraControl.disposeDocListener()
    // console.log("Transform Start.");
    storageManager.begindoInterface()

    engineInterface.stopUpdate = false
  }

  function onTransEnd (event) {
    // console.log("Transform End.");
    console.log(transformControl.object.rotation)
    storageManager.enddoInterface()
  }

  function onObjectChange (event) {
    // 当物体Transform发生改变时进行自身的更新
    var obj = event.target.object

    if (obj.isNeedUpdate) {
      // 假如是光源，则更新光源自身的组件(控制器，目标等)
      obj.update()
    }
  }

  function onAutoTransEnd (event) {
  }

  init()
}

export { SceneManager }



