<template>
  <div class="UIButton" :data-x="btnData.dataX" :data-y="btnData.dataY" :style="style" @click="redirect()">
    <p :style="fontStyle">{{ btnData.text }}</p>
  </div>
</template>

<script>
export default {
  name: 'ui-button-component',
  props: {
    btnData: Object
  },
  computed: {
    style: function () {
      var align = 'flex-start'
      switch (this.btnData.textAlign) {
        case 0 :
          break
        case 1 :
          align = 'center'
          break
        case 2 :
          align = 'flex-end'
          break
      }
      var re = this.btnData.widget.calc()
      return {
        width: `${re.width}px`,
        height: `${re.height}px`,
        left: `${re.left}px`,
        top: `${re.top}px`,
        opacity: `${this.btnData.opacity}`,
        'z-index': `${this.btnData.zIndex}`,
        'justify-content': align,
        transform: `rotate(${this.btnData.rot}deg)`,
        'background-color': this.btnData.btnColor
      }
    },
    fontStyle: function () {
      return {
        'color': `${this.btnData.fontColor}`,
        'font-size': `${this.fontSize / 14}rem`
        // 'font-family': this.$store.state.fontFamily[this.btnData.fontType].value
      }
    },
    fontSize: function () {
      return parseInt(this.$store.state.fontSizes[this.btnData.fontSize].name)
    }
  },
  methods: {
    // 按钮点击后跳转到当前按钮设定的页面
    redirect: function () {
      // 处理网址，主要是检测是否带有http/https开头的信息
      if (/http\b|https\b/.exec(this.btnData.url)) window.open(`${this.btnData.url}`)
      else window.open(`http://${this.btnData.url}`)
    }
  }
}
</script>

<style>
.UIButton {
  width: 3rem;
  height: 3rem;
  position: absolute;
  z-index: 3;
  top: 0rem;
  left: 0rem;

  display: flex;

  cursor: pointer;
  background-color: #0999ff;
  border-radius: 4px;
  transition: box-shadow 0.2s;
  user-select: none;
  pointer-events: auto;
}

.UIButton>p {
  margin-top: auto;
  margin-bottom: auto;
}
</style>




