function Renderer (parameters) {
  // Private variables //////////////////////////////////////////////////////
  THREE.WebGLRenderer.call(this, parameters)
  // var webGLRenderer = new THREE.WebGLRenderer(parameters);

  var scope = this
  var canvas = parameters.canvas

  this.bUseComposer = false

  // Public methods ///////////////////////////////////////////////////
  this.renderOrigin = this.render
  this.render = function (scene, camera, renderTarget, forceClear) {
    if (!this.bUseComposer) {
      // 由于关闭了自动清空缓冲，需要手动调用
      this.clear()
    }

    // 渲染天空盒
    if (isEditor && 'skyBox' in scene && this.bRenderSkyBox) {
      scene.skyBox.renderBy(this, renderTarget)
    }

    if ('background' in this) {
      var width = canvas.clientWidth
      var height = canvas.clientHeight

      this.background.renderBy(this)
    }

    this.renderOrigin(scene, camera, renderTarget, forceClear)
  }

  // Private methods //////////////////////////////////////////////////////
  function init () {
    // threejs渲染器设置
    // 设置GL渲染窗口大小
    scope.setSize(canvas.clientWidth, canvas.clientHeight)
    // 设置背景颜色
    scope.setClearColor(0xbbbbbb)

    // 确定使用阴影图的方式设置影子
    if (platform.os.family === 'Android') {
      scope.shadowMap.enabled = false
    } else {
      scope.shadowMap.enabled = true
    }
    // 使用软阴影
    scope.shadowMap.type = THREE.PCFSoftShadowMap
    // 为了使用环境贴图进行多次渲染，需要关闭自动清楚，否则每次调用render都会把
    // 之前的渲染结果清除
    scope.autoClear = false

    scope.physicallyCorrectLights = true
    // console.log(platform)
    if (isBrowser) {
      if (platform.os.family === 'iOS') { scope.setPixelRatio(2) }
      // if (platform.os.family === 'Android') {
      //   scope.setPixelRatio(0.8)
      // }
    }

    // scope.gammaInput = true;
    // scope.gammaOutput = true;
    // scope.toneMapping = THREE.ReinhardToneMapping;
    // scope.toneMappingExposure = 2.2;
    // scope.gammaFactor = 0.45
  }

  init()
}

Renderer.prototype = Object.assign(Object.create(THREE.WebGLRenderer.prototype), {
  constructor: Renderer
})

export { Renderer }



