<template>
<div v-if="isActive" class="UIButtonList" :style="listStyle">
  <!-- 需要添加的固定按钮 -->
  <ui-button-sp-component v-for="(item, index) in btnList" :key="item.id" :btnData="item.btnData" @select="select(item.id)" :isOn="item.id === curSelectBtnId" :canDelete="false"></ui-button-sp-component>
</div>
</template>

<script>
import UIButtonSPComponent from './UIButton_sp.vue'

export default {
  name: 'ui-button-sp-list',
  components: {
    'ui-button-sp-component': UIButtonSPComponent
  },
  props: {
    isActive: {
      default: false,
      type: Boolean
    },
    btnList: Array,
    curSelectBtnId: {
      default: -1,
      type: Number
    }
  },
  data () {
    return {
      TextureType: this.$store.state.TextureType
    }
  },
  computed: {
    listStyle: function () {
      return {
        'height': `${this.listHeight}rem`
      }
    },
    listHeight: function () {
      var biggestFont = 0
      this.btnList.forEach(ele => {
        if (ele.btnData.type === this.TextureType.TEXTURE) {
          if (ele.btnData.fontSize > biggestFont) biggestFont = ele.btnData.fontSize
        }
      })
      biggestFont = parseInt(this.$store.state.fontSizes[biggestFont].name) / 14
      return biggestFont + 7.05
    }
  },
  methods: {
    select: function (id) {
      this.$emit('update:curSelectBtnId', id)
    }
  }
}
</script>

<style>
.UIButtonList {
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 9.2rem;
  bottom: 5.4rem;
  background: transparent;
  border-radius: 3.2px;

  pointer-events: none;
  display: flex;
  justify-content: center;
}
</style>


