function Setting () {
  // Singleton pattern ////////////////////////////////////////////////
  if (Setting._instance !== undefined) {
    return Setting._instance
  }
  Setting._instance = this

  // Public variables ///////////////////////////////////////////////

  // 是否读取所有页面的对象
  // 如果是，在加载项目时会加载所有对象，
  // 如果不是，只有在加载页面时会加载相应的对象
  this.loadAllPageObj = true
}

var setting = new Setting()

export { Setting }



