// 一个动画中的一个模型在时间上的变化数据
// 保存了多个帧数据
function AnimationChangeDataModel () {
  // Parent constructor ////////////////////////////////////////////////////////////
  DataModel.call(this)

  // Public variables //////////////////////////////////////////////////
  this.sid = 0
  this.loop = false

  // this.orgData = new ModelChangeDataModel()

  this.keyFrameList = []
}

AnimationChangeDataModel.prototype = Object.assign(Object.create(DataModel), {
  constructor: AnimationChangeDataModel,
  setFrom: function (jsonObj) {
    DataModel.prototype.setFrom.call(this, jsonObj)

    this.sid = jsonObj.sid
    if (!checkNone(jsonObj.loop)) { this.loop = jsonObj.loop }

    // this.orgData.setFrom(jsonObj.orgData)
  },

  copy: function (srcModel) {
    DataModel.prototype.copy.call(this, srcModel)

    this.sid = srcModel.sid

    // this.orgData.copy(srcModel.orgData)
    if (!checkNone(srcModel.loop)) { this.loop = srcModel.loop }

    this.keyFrameList.splice(0, this.keyFrameList.length)
    srcModel.keyFrameList.forEach(keyFrame => {
      this.keyFrameList.push(keyFrame.clone())
    })
  },

  clone: function (srcModel) {
    var dataModel = new AnimationChangeDataModel()
    dataModel.copy(this)
    return dataModel
  },

  equal: function (srcModel) {
    var res = DataModel.prototype.equal.call(this, srcModel)

    if (!res) { return res }

    res = res & (this.sid === srcModel.sid)

    if (!checkNone(srcModel.loop)) { res = res & (this.loop === srcModel.loop) }

    // res = res & (this.orgData.equal(srcModel.orgData))

    return res
  }
})

