// 场景物体的数据模型
function MeshDataModel () {
  // Parent constructor ////////////////////////////////////////////////////////////
  SceneObjDataModel.call(this)

  // Public variables //////////////////////////////////////////////////
  this.type = DataModelType.MESH
  this.orgName = ''
  this.modelUrl = ''

  this.material = new MaterialDataModel()

  // this.textureUrl = ''
  // this.ifTransparent = false;
  this.opacity = 1.0
  this.roughness = 0.5
  this.metalness = 0.5

  this.clearCoat = 0.0
  this.clearCoatRoughness = 0.0
  this.reflectivity = 0.5

  this.pivotPos = new THREE.Vector3()
}

MeshDataModel.prototype = Object.assign(Object.create(SceneObjDataModel), {
  constructor: MeshDataModel,
  setFrom: function (jsonObj) {
    SceneObjDataModel.prototype.setFrom.call(this, jsonObj)

    this.orgName = jsonObj.orgName
    this.modelUrl = jsonObj.modelUrl
    this.material.setFrom(jsonObj.material)
    // this.textureUrl = jsonObj.textureUrl
    // this.ifTransparent = jsonObj.ifTransparent;
    this.opacity = jsonObj.opacity
    // this.shininess = jsonObj.shininess
    this.metalness = jsonObj.metalness
    this.roughness = jsonObj.roughness

    if (!checkNone(jsonObj.clearCoat)) { this.clearCoat = jsonObj.clearCoat }

    if (!checkNone(jsonObj.clearCoatRoughness)) { this.clearCoatRoughness = jsonObj.clearCoatRoughness }

    if (!checkNone(jsonObj.reflectivity)) { this.reflectivity = jsonObj.reflectivity }

    if (!checkNone(jsonObj.pivotPos)) {
      this.pivotPos.x = jsonObj.pivotPos.x
      this.pivotPos.y = jsonObj.pivotPos.y
      this.pivotPos.z = jsonObj.pivotPos.z
    }
  },

  copy: function (srcModel) {
    SceneObjDataModel.prototype.copy.call(this, srcModel)

    this.orgName = srcModel.orgName
    this.modelUrl = srcModel.modelUrl
    this.material.copy(srcModel.material)
    // this.textureUrl = srcModel.textureUrl
    // this.ifTransparent = srcModel.ifTransparent;
    this.opacity = srcModel.opacity
    // this.shininess = srcModel.shininess
    this.metalness = srcModel.metalness
    this.roughness = srcModel.roughness

    if (!checkNone(srcModel.clearCoat)) { this.clearCoat = srcModel.clearCoat }

    if (!checkNone(srcModel.clearCoatRoughness)) { this.clearCoatRoughness = srcModel.clearCoatRoughness }

    if (!checkNone(srcModel.reflectivity)) { this.reflectivity = srcModel.reflectivity }

    if (!checkNone(srcModel.pivotPos)) {
      this.pivotPos.x = srcModel.pivotPos.x
      this.pivotPos.y = srcModel.pivotPos.y
      this.pivotPos.z = srcModel.pivotPos.z
    }
  },

  clone: function (srcModel) {
    var dataModel = new MeshDataModel()
    dataModel.copy(this)
    return dataModel
  },

  equal: function (srcModel) {
    var res = SceneObjDataModel.prototype.equal.call(this, srcModel)

    if (!res) { return res }

    res = res & (this.orgName === srcModel.orgName)
    res = res & (this.modelUrl === srcModel.modelUrl)
    res = res & (this.material.equal(srcModel.material))
    // res = res & (this.textureUrl === srcModel.textureUrl)
    res = res & (this.opacity === srcModel.opacity)
    // res = res & (this.shininess === srcModel.shininess)
    res = res & (this.metalness === srcModel.metalness)
    res = res & (this.roughness === srcModel.roughness)
    res = res & (this.clearCoat === srcModel.clearCoat)
    res = res & (this.clearCoatRoughness === srcModel.clearCoatRoughness)
    res = res & (this.reflectivity === srcModel.reflectivity)

    if (!checkNone(srcModel.pivotPos)) {
      res = res & (this.pivotPos.x === srcModel.pivotPos.x)
      res = res & (this.pivotPos.y === srcModel.pivotPos.y)
      res = res & (this.pivotPos.z === srcModel.pivotPos.z)
    }

    return res
  }
})

