// 标注线物体的数据模型
function MarkLineDataModel () {
  // Parent constructor ////////////////////////////////////////////////////////////
  SceneObjDataModel.call(this)

  // Public variables //////////////////////////////////////////////////
  this.type = DataModelType.MARKLINE

  // 保存当前的字体Url
  this.fontUrl = ''

  // 字体大小
  this.fontSize = 0
  // 文本字符串
  this.text = ''
  // 颜色
  this.color = new THREE.Color(0x006699)

  // 当前的文本对齐方式 mid/left/right
  this.textAlign = TextAlignType.MIDDLE
  // 文本绕标注线旋转角度，步长为90度
  this.textRot = 0
  // 当前文本类型为水平还是垂直与标注线，horizontal/vertical
  this.textType = TextDirectionType.HORIZONTAL

  // 标注线总体长度
  this.markLength = 5

  // 标注线样式
  this.lineShape = 'solid'
  // 线段半径
  this.lineRadius = 0.05

  // 标注线两端箭头形状
  this.headShape = MarkLineType.NONE

  // 标注线为虚线时每一段的宽度
  this.subLength = 0.3

  // 标注线透明度
  this.opacity = 1.0
}

