function ProjectDataModel () {
  this.cover = 'static/default/images/project.png'
  this.title = ''
  this.describe = 'None'
  this.date = ''
  this.filePath = ''
}

ProjectDataModel.prototype.setFrom = function (jsonObj) {
  this.cover = jsonObj.cover
  this.title = jsonObj.title
  this.describe = jsonObj.describe
  this.date = jsonObj.date
  this.filePath = jsonObj.filePath
}

ProjectDataModel.prototype.copy = function (srcModel) {
  this.cover = srcModel.cover
  this.title = srcModel.title
  this.describe = srcModel.describe
  this.date = srcModel.date
  this.filePath = srcModel.filePath
}

ProjectDataModel.prototype.clone = function (srcModel) {
  var dataModel = new ProjectDataModel()
  dataModel.copy(this)
  return dataModel
}

ProjectDataModel.prototype.equal = function (srcModel) {
  var res = true

  res = res & (this.cover === srcModel.cover)
  res = res & (this.title === srcModel.title)
  res = res & (this.describe === srcModel.describe)
  res = res & (this.date === srcModel.date)
  res = res & (this.filePath === srcModel.filePath)

  return res
}

/// /////////////////////////////////////////////////////////////////////////////

// 定义绑定函数
function bindFunc (modelFactory, bindDefineFunc, fstParam, sndParam) {
  var metaBindFunc = bindDefineFunc.bind(modelFactory)
  metaBindFunc(modelFactory.dataModel, fstParam, sndParam)
}

function bindName (dataModel) {
  Object.defineProperties(this, {
    name: {
      get: function () {
        return dataModel.objName
      },
      set: function (value) {
        dataModel.objName = value
      }
    }
  })
}

function bindParam (dataModel, fstParam, sndParam) {
  var paramSetGet = null

  // 定义set/get函数
  // 如果只传入了一个参数
  if (checkNone(sndParam)) {
    paramSetGet = {
      get: function () {
        return dataModel[fstParam]
      },
      set: function (value) {
        dataModel[fstParam] = value
      }
    }
  }
  // 传入两个参数
  else {
    paramSetGet = {
      get: function () {
        return dataModel[fstParam][sndParam]
      },
      set: function (value) {
        dataModel[fstParam][sndParam] = value
      }
    }
  }

  // 定义描述器
  var descriptors = {}
  if (checkNone(sndParam)) {
    descriptors[fstParam] = paramSetGet
  } else {
    descriptors[sndParam] = paramSetGet
  }

  // 定义属性
  if (checkNone(sndParam)) {
    Object.defineProperties(this, descriptors)
  } else {
    if (dataModel instanceof MeshDataModel) {
      if (fstParam === 'position' || fstParam === 'quaternion' || fstParam === 'scale'
      ) { Object.defineProperties(this.group[fstParam], descriptors) } else if (fstParam === 'pivotPos') { Object.defineProperties(this.position, descriptors) }
    } else { Object.defineProperties(this[fstParam], descriptors) }
  }
}

