// 关键属性
/*
PointLight
	distance:描述照明距离
	intensity:照明的强度
	THREE.Object3D的position,rotaion
	Material的颜色
	controlball & helper:只需要在设置完上述参数后调用Update函数即可

SpotLight
	angle:椎体的张角
	distance:照明距离
	THREE.Object3D的position,rotation
	Material的颜色
	controlball & targetBox & helper 只需要在设置完以上参数后调用update函数即可

DirectionalLight
	THREE.Object3D的position,rotation
	controlball & targetBox & helper只需要在设置完以上参数后调用update函数即可

*/
/// //////////////////////////////////////////////////////////////////////

import { DataModelFactory, DataModelType } from '../core/DataModel.js'
import { ResourceManager } from '../managers/ResourceManager.js'
import { Billboard } from '../objects/Billboard.js'

THREE.Light.prototype.addToScene = function (scene) {
  // 添加对场景和点击列表的引用，方便日后进行删除工作
  // 不保证是否造成循环引用，导致之后的垃圾清除失败
  this.scene = scene
  this.intersectList = scene.objIntersectList
  this.hideList = scene.pageHideList

  // 将控制盒和灯光辅助器添加在场景下
  // 从而使灯光父节点的物体无法影响到它们
  scene.add(this.controlBall)
  scene.add(this.helper)
  // 点光源是没有targetBox的
  if ('targetBox' in this) { scene.add(this.targetBox) }
  // 将控制盒添加到列表中，以用于选择到该控制盒
  this.intersectList.push(this.controlBall)

  // 将控制盒和辅助器添加到隐藏列表中
  // console.log(scene.pageHideList);
  this.hideList.push(this.controlBall)
  this.hideList.push(this.helper)
}

THREE.Light.prototype.removeFromScene = function () {
  // 获得控制器的索引，从点击列表中删除控制器
  var index = this.intersectList.indexOf(this.controlBall)
  if (index > -1) { this.intersectList.splice(index, 1) }

  this.scene.remove(this.controlBall)
  this.scene.remove(this.helper)
  if ('targetBox' in this) { this.scene.remove(this.targetBox) }

  index = this.hideList.indexOf(this.controlBall)
  if (index > -1) { this.hideList.splice(index, 1) }

  index = this.hideList.indexOf(this.helper)
  if (index > -1) { this.hideList.splice(index, 1) }
}

// 灯光选中后显示辅助器
THREE.Light.prototype.onSelect = function () {
  this.helper.visible = true
}

// 灯光取消选中时隐藏辅助器
THREE.Light.prototype.onDeselect = function () {
  this.helper.visible = false
}

// 判断是否为自定义后的光源
THREE.Light.prototype.isCustom = false

/// ///////////////////////////////////////////////////////////////////////

