import { ResourceManager } from '../managers/ResourceManager.js'
import { PageManager } from '../managers/PageManager.js'

import { PageType, TextureType } from '../core/Type.js'

function Background (camera, scene) {
  var scope = this
  // Public variables ////////////////////////////////////////////
  this.type = TextureType.NONE
  this.color = '#ffffff'
  this.textureUrl = ''

  this.needsUpdate = false

  // Private variables ////////////////////////////////////////////////
  var resourceManager = new ResourceManager()
  var pageManager = new PageManager()

  var cameraBackground = new THREE.PerspectiveCamera(camera.fov, camera.aspect, camera.near, camera.far)
  var sceneBackground = new THREE.Scene()

  var height = 2.0

  var materialCanvas = new THREE.MeshBasicMaterial({color: 0xbbbbbb, depthTest: false})
  var geometry = new THREE.PlaneBufferGeometry(camera.aspect * height, height)
  var meshCanvas = new THREE.Mesh(geometry, materialCanvas)

  cameraBackground.position.z = height / 2 / Math.tan(camera.fov / 2 / 180 * Math.PI)

  // console.log(cameraBackground.position.z)

  sceneBackground.add(meshCanvas)

  // Public methods ////////////////////////////////////////////////
  // 调用渲染器渲染背景
  this.renderBy = function (renderer) {
    renderer.renderOrigin(sceneBackground, cameraBackground)
  }

  this.setColor = function (color, ifNone) {
    meshCanvas.material.map = null

    if (ifNone) {
      scope.type = TextureType.NONE
    } else { scope.type = TextureType.COLOR }

    scope.color = color

    if (typeof (color) === 'string') {
		  meshCanvas.material.color.setStyle(color)
    } else if (typeof (color) === 'number') {
		  meshCanvas.material.color.setHex(color)
    }

    meshCanvas.material.needsUpdate = true
  }

  this.setTexture = function (imgUrl) {
    // 检查url是否为字符串
    if (typeof imgUrl !== 'string') {
      console.log("Background, setTexture(), imgUrl: \"Url should be a \'string\'.\"")
      return
    }

    scope.type = TextureType.TEXTURE
    scope.textureUrl = imgUrl

    if (imgUrl === '') {
      meshCanvas.material.map = null
	    meshCanvas.material.needsUpdate = true
      return
    }

    var texture = resourceManager.getTexture(imgUrl)
    if (checkNone(texture)) {
      console.log('Background, setTexture(), texture: "getTexture(imgUrl) failed."')
    } else {
  		meshCanvas.material.color.setHex(0xffffff)

      meshCanvas.material.map = texture
      meshCanvas.material.needsUpdate = true

      scope.needsUpdate = true
    }
  }
}

export { Background }



