<template>
  <div class="UIVideo" :data-x="videoData.dataX" :data-y="videoData.dataY" :style="style">
    <video ref="video" preload="auto" controls :src="videoData.src" :style="videoStyle">你的浏览器不支持video标签</video>
  </div>
</template>

<script>
export default {
  name: 'ui-video-component',
  props: {
    videoData: Object,
    disabled: {
      default: false,
      type: Boolean
    }
  },
  mounted () {
    this.$refs.video.addEventListener('play', () => { this.$store.state.stopBkgMusic = true }, false)
    this.$refs.video.addEventListener('pause', () => { this.$store.state.stopBkgMusic = false }, false)
    this.$refs.video.addEventListener('ended', () => { this.$store.state.stopBkgMusic = false }, false)
  },
  computed: {
    style: function () {
      var re = this.videoData.widget.calc()
      return {
        width: `${re.width}px`,
        height: `${re.height}px`,
        left: `${re.left}px`,
        top: `${re.top}px`,
        opacity: `${this.videoData.opacity}`,
        'z-index': `${this.videoData.zIndex}`,
        transform: `rotate(${this.videoData.rot}deg)`,
        'pointer-events': this.disabled ? 'none' : 'auto'
      }
    },
    videoStyle: function () {
      return {
        'pointer-events': this.disabled ? 'none' : 'auto'
      }
    }
  }
}
</script>

<style>
.UIVideo {
  width: 3rem;
  height: 3rem;
  position: absolute;
  z-index: 3;
  top: 0rem;
  left: 0rem;

  transition: box-shadow 0.2s;
  user-select: none;
  pointer-events: auto;
}

.UIVideo>video {
  width: 100%;
  height: 100%;
  background-color: #fff;
}
</style>


