// param
// font 字体资源
// text 初始化的文本
// targets 目标物体群，支持多选的情况下，这个参数应该是一个物体数组，求包围盒
// type 决定标注线是X/Y/Z方向的
// align 决定文本的对齐方式

// 对象属性
/*
font 				当前正在使用的字体资源的引用，应从资源管理器获取
textAlign			标注线内字体的对齐方式，mid/left/right，在标注线类型为x,z时left,right也可以是上下
headHelperAlign		标注线两端用于对准的辅助线，mid/left/right表示辅助线倾向于哪一边
fontSize			文本的大小
text 				文本内容
lineRadius			标注线处文字外的图案的粗细
length 				标注线长度
headShape 			标注线两端箭头的形状 sphere/box/cone
lineShape 			标注线本身的线条类型 dash/solid(虚线/实线)
helperLineShape 	标注线辅助线的线条类型，与lineShape相似
helpersegmentLength	标注线辅助线的长度
helperLineRot		标注线辅助线绕标注线本身的旋转角度，步长是90度
textRot 			文本绕标注线旋转角度，步长为90度
type				标注线类型 x/y/z
isChangeReturn		改变返回的目标
*/
