MarkLine.prototype = Object.assign(Object.create(THREE.Object3D.prototype), {

  constructor: MarkLine,

  isMarkLine: true,

  isCustom: true,

  // 添加到场景
  addToScene: function (scene) {
    this.intersectList = scene.objIntersectList
    this.intersectList.push(this.textBox)
    this.intersectList.push(this.lineBox)
    // this.intersectList.push(this.lineBody.children[0]);
  },

  // 删除该标注线
  removeFromScene: function () {
    this.removeHead()
    this.removeLine()
    this.removeText()
    this.removeMarkLineBox()
    this.textMaterial.dispose()
    this.textMaterial = null
    this.lineMaterial.dispose()
    this.lineMaterial = null
    this.remove(this.line)
    this.intersectList = null
  },

  update: function () {
    if (this.updateText) { this.setText() }
    if (this.updateHead) { this.setHeadShape() }
    if (this.updateLine) { this.setLineShape() }
    if (this.updateBox) { this.setMarkLineBox() }
    if (this.updateColor) {
      this.setTextColor(this.color)
      this.setLineColor(this.color)
    }
    if (this.updateTextRot) {
      this.setTextRot()
    }

    if (this.updateOpacity) { this.setOpacity() }

    this.updateText = false
    this.updateHead = false
    this.updateLine = false
    this.updateBox = false
    this.updateColor = false
    this.updateTextRot = false
    this.updateOpacity = false
  },

  setLength: function (length) {
    this.markLength = length
  },

  setTextColor: function (color) {
    this.textMaterial.color.set(color)
    this.textMaterial.needsUpdate = true
  },

  setLineColor: function (color) {
    this.lineMaterial.color.set(color)
    this.lineMaterial.needsUpdate = true
  },

  setToTransparent: function (ifToggle) {
    this.textMaterial.transparent = ifToggle
    this.lineMaterial.transparent = ifToggle
  },

  setOpacity: function () {
    if (this.opacity > 1.0) {
      this.opacity = 1.0
    } else if (this.opacity < 0.0) {
      this.opacity = 0.0
    }

    if (this.opacity > 0.99) {
      this.setToTransparent(false)
    } else {
      this.setToTransparent(true)
    }

    this.lineMaterial.opacity = this.opacity
    this.textMaterial.opacity = this.opacity

    this.lineMaterial.needsUpdate = true
    this.textMaterial.needsUpdate = true
  },

  // 改变头部标志物样式
  setHeadShape: function () {
    switch (this.headShape) {
      case MarkLineType.NONE:
      case MarkLineType.ARROW:
      case MarkLineType.CONE:
      case MarkLineType.BOX:
      case MarkLineType.SPHERE:
      case MarkLineType.RECTANGLE:
        break
      default:
        console.log('invalid head shape')
        return
    }

    this.removeHead()

    var leftHead = null
    var rightHead = null

    this.segmentLength = this.markLength
    // 根据不同形状要求生成不同的geometry，要特别注意radius和lineRadius参数
    // 关系设置不好会使得标注线的线条“穿出”箭头
    if (this.headShape === MarkLineType.NONE) {
      this.headLeft = null
      this.headRight = null
      return
    } else if (this.headShape === MarkLineType.ARROW) {
      var width = this.lineRadius * 6
      var height = this.lineRadius * 2
      var depth = this.lineRadius * 2

      leftHead = this.generateArrow(width, height, depth)
      leftHead.rotateX(THREE.Math.degToRad(90))
      leftHead.translate(-this.markLength / 2, 0, 0)
      rightHead = this.generateArrow(width, height, depth)
      rightHead.rotateY(THREE.Math.degToRad(180))
      rightHead.rotateX(THREE.Math.degToRad(90))
      rightHead.translate(this.markLength / 2, 0, 0)

      this.segmentLength -= this.lineRadius * 4
    } else if (this.headShape === MarkLineType.CONE) {
      var radius = this.lineRadius * 2.3
      var height = this.lineRadius * 7.25 // this.markLength * 0.2;
      leftHead = new THREE.ConeBufferGeometry(radius, height, 32)
      leftHead.translate(0, this.markLength / 2 - height / 2, 0)
      leftHead.rotateZ(THREE.Math.degToRad(90))
      rightHead = new THREE.ConeBufferGeometry(radius, height, 32)
      rightHead.translate(0, this.markLength / 2 - height / 2, 0)
      rightHead.rotateZ(THREE.Math.degToRad(-90))

      this.segmentLength -= height * 2
    } else if (this.headShape === MarkLineType.BOX) {
      var size = this.lineRadius * 2.8
      leftHead = new THREE.BoxBufferGeometry(size, size, size)
      leftHead.translate(-this.markLength / 2, 0, 0)
      rightHead = new THREE.BoxBufferGeometry(size, size, size)
      rightHead.translate(this.markLength / 2, 0, 0)
    } else if (this.headShape === MarkLineType.SPHERE) {
      var radius = this.lineRadius * 2.8
      leftHead = new THREE.SphereBufferGeometry(radius, 32, 32)
      leftHead.translate(-this.markLength / 2, 0, 0)
      rightHead = new THREE.SphereBufferGeometry(radius, 32, 32)
      rightHead.translate(this.markLength / 2, 0, 0)
    } else if (this.headShape === MarkLineType.RECTANGLE) {
      var width = this.lineRadius * 6
      var height = this.lineRadius * 2
      var depth = this.lineRadius * 2
      leftHead = new THREE.BoxBufferGeometry(width, height, depth)
      leftHead.rotateY(THREE.Math.degToRad(55))
      leftHead.rotateX(THREE.Math.degToRad(90))
      leftHead.translate(-this.markLength / 2, 0, 0)
      rightHead = new THREE.BoxBufferGeometry(width, height, depth)
      rightHead.rotateY(THREE.Math.degToRad(55))
      rightHead.rotateX(THREE.Math.degToRad(90))
      rightHead.translate(this.markLength / 2, 0, 0)
    }

    // this.headLeft = new THREE.Mesh(leftHead,this.lineMaterial);
    // this.headRight = new THREE.Mesh(rightHead,this.lineMaterial);
    this.headLeft = new THREE.Mesh(leftHead, this.lineMaterial)
    this.headRight = new THREE.Mesh(rightHead, this.lineMaterial)

    this.line.add(this.headLeft)
    this.line.add(this.headRight)
  },

  // 改变线条样式
  setLineShape: function () {
    if (this.lineShape !== 'solid' &&
   this.lineShape !== 'dash') { return }

    this.removeLine()

    // 假如当前文字不是居中
    if (this.textAlign !== TextAlignType.MIDDLE) {
      if (this.lineShape === 'solid') {
        var tLine = null
        // if (this.headShape === MarkLineType.NONE) {
        //   tLine = new THREE.PlaneBufferGeometry(this.lineRadius * 2, this.segmentLength)
        // }
        // else {
        tLine = new THREE.CylinderBufferGeometry(this.lineRadius, this.lineRadius, this.segmentLength, 32, 1, true)
        // }
        tLine.rotateZ(THREE.Math.degToRad(90))
        this.lineBody = new THREE.Mesh(tLine, this.lineMaterial)
      } else if (this.lineShape === 'dash') {
        var subLength = this.subLength// 每一段的宽度
        var dashGroup = new THREE.Group()
        var eNum = Math.floor(this.segmentLength / (subLength * 2))// 总共有多少段
        var tLine = null

        if (this.headShape === MarkLineType.NONE) {
          tLine = new THREE.PlaneBufferGeometry(this.lineRadius * 2, subLength)
        } else {
          tLine = new THREE.CylinderBufferGeometry(this.lineRadius, this.lineRadius, subLength, 32, 1, true)
        }

        tLine.rotateZ(THREE.Math.degToRad(90))

        for (var i = 0; i < eNum; ++i) {
          var temp = new THREE.Mesh(tLine, this.lineMaterial)
          temp.translateX(-this.segmentLength / 2 + subLength + 2 * subLength * i)
          dashGroup.add(temp)
        }

        this.lineBody = dashGroup
      }
    }
    // 当前文字是居中，需要“挖空”线中间
    else {
      // 考虑“挖空”的大小
      var middleGap = 0
      if (this.textType === TextDirectionType.HORIZONTAL) {
        middleGap = this.textWidth
      } else {
        middleGap = this.textHeight
      }
      // if(this.type === 'y')
      // 	middleGap = this.textHeight;
      // else
      // 	middleGap = this.textWidth;

      if (this.lineShape === 'solid') {
        var tLine = null
        // if (this.headShape === MarkLineType.NONE) {
        //   tLine = new THREE.PlaneBufferGeometry(this.lineRadius * 2, (this.segmentLength - middleGap) / 2)
        // }
        // else {
        tLine = new THREE.CylinderBufferGeometry(this.lineRadius, this.lineRadius, (this.segmentLength - middleGap) / 2, 32, 1, true)
        // }

        tLine.rotateZ(THREE.Math.degToRad(90))

        var lineLeft = new THREE.Mesh(tLine, this.lineMaterial)
        var lineRight = new THREE.Mesh(tLine, this.lineMaterial)

        lineLeft.translateX(-(this.segmentLength + middleGap) / 4, 0, 0)
        lineRight.translateX((this.segmentLength + middleGap) / 4, 0, 0)

        this.lineBody = new THREE.Group()
        this.lineBody.add(lineLeft)
        this.lineBody.add(lineRight)
      } else if (this.lineShape === 'dash') {
        this.lineBody = new THREE.Group()

        var subLength = this.subLength
        var eNum = Math.floor(((this.segmentLength - middleGap) / 2) / (subLength * 2))// 到底有多少段曲线
        var tLine = null
        if (this.headShape === MarkLineType.NONE) {
          tLine = new THREE.PlaneBufferGeometry(this.lineRadius * 2, subLength)
        } else {
          tLine = new THREE.CylinderBufferGeometry(this.lineRadius, this.lineRadius, subLength, 32, 1, true)
        }

        tLine.rotateZ(THREE.Math.degToRad(90))

        for (var i = 0; i < eNum; ++i) {
          var tempL = new THREE.Mesh(tLine, this.lineMaterial)
          var tempR = new THREE.Mesh(tLine, this.lineMaterial)

          // tempL.translateX(-(this.lineLength / 2) + 2 * subLength + 2 * subLength * i);
          tempL.translateX(-(middleGap / 2 + subLength + 2 * subLength * i))
          tempR.translateX(middleGap / 2 + subLength + 2 * subLength * i)

          this.lineBody.add(tempL)
          this.lineBody.add(tempR)
        }
      }
    }

    this.lineBody.name = 'lineBody'
    this.line.add(this.lineBody)
  },

  // 改变对准线样式
  // shape(虚线/实线)，align(左中右)，length(长度)，rot(旋转角度，步长90度)，gap(虚线长度与间隔)
  setHelperLineShape: function () {
    if (this.helperLineRight !== null) {
      this.helperLineRight.dispose()
      this.line.remove(this.helperLineRight)
    }

    if (this.helperLineLeft !== null) {
      this.helperLineLeft.dispose()
      this.line.remove(this.helperLineLeft)
    }

    var vLength = this.helpersegmentLength// 标定线的长度
    var vAlign = this.headHelperAlign// 标定线的布局，左/右/居中
    var vRot = this.helperLineRot// 旋转角度，步长为90度，最后的旋转角度为vROT * 90

    if (this.helperLineShape === 'solid') {
      // 默认就有居中的效果
      var l = new THREE.CylinderBufferGeometry(this.lineRadius, this.lineRadius, vLength, 32, 1, false)
      if (vAlign === 'left') {
        l.translate(0, vLength / 2, 0)
      } else if (vAlign === 'right') {
        l.translate(0, -vLength / 2, 0)
      }
      this.helperLineLeft = new THREE.Mesh(l, this.lineMaterial)
      this.helperLineRight = new THREE.Mesh(l, this.lineMaterial)
    } else if (this.helperLineShape === 'dash') {
      this.helperLineRight = new THREE.Group()
      this.helperLineLeft = new THREE.Group()

      // 虚线之间的间隔,也是虚线的长度
      var subLength = this.subLength
      // 有多少段虚线
      var eNum = Math.floor(vLength / (subLength * 2))
      eNum = eNum + 1
      // 虚线开始产生的位置，与布局有关
      var startPos = subLength / 2
      if (vAlign === 'mid') { startPos = -vLength / 2 + subLength } else if (vAlign === 'right') { startPos = -vLength + subLength }
      var l = new THREE.CylinderBufferGeometry(this.lineRadius, this.lineRadius, subLength, 32, 1, false)

      for (var i = 0; i < eNum; ++i) {
        var tempL = new THREE.Mesh(l, this.lineMaterial)
        var tempR = new THREE.Mesh(l, this.lineMaterial)

        tempL.translateY(startPos + subLength * 2 * i)
        tempR.translateY(startPos + subLength * 2 * i)

        this.helperLineLeft.add(tempL)
        this.helperLineRight.add(tempR)
      }
    }

    this.helperLineRight.rotateX(THREE.Math.degToRad(90 * vRot))
    this.helperLineLeft.rotateX(THREE.Math.degToRad(90 * vRot))

    this.helperLineLeft.translateX(-this.markLength / 2)
    this.helperLineRight.translateX(this.markLength / 2)

    this.line.add(this.helperLineRight)
    this.line.add(this.helperLineLeft)

    this.helperLineRight.name = 'helperLineRight'
    this.helperLineLeft.name = 'helperLineLeft'
  },

  // 改变字体大小,样式(小标索引),文本内容(涉及到字体模型的生成)
  setText: function () {
    this.removeText()

    var offset = (this.fontSize + 1) / FONT_SIZE_FACTOR / 3
    var shapes = this.font.generateShapes(this.text, (this.fontSize + 1) / FONT_SIZE_FACTOR, 5)
    var geometry = new THREE.ShapeBufferGeometry(shapes)

    // var geometry = new THREE.BoxGeometry(1, 1, 1);
    this.textMesh = new THREE.Mesh(geometry, this.textMaterial)
    this.textMesh.geometry.computeBoundingBox()
    var box = this.textMesh.geometry.boundingBox
    this.textWidth = box.max.x - box.min.x
    this.textHeight = box.max.y - box.min.y

    this.textMesh.geometry.center()

    if (this.textType === TextDirectionType.VERTICAL) {
      this.textMesh.rotateZ(THREE.Math.degToRad(90))
    }

    if (this.textAlign === TextAlignType.RIGHT) {
      if (this.textType === TextDirectionType.VERTICAL) {
        this.textMesh.geometry.translate(this.textWidth / 2 + offset, 0, 0)
      } else {
        this.textMesh.geometry.translate(0, this.textHeight / 2 + offset, 0)
      }
    } else if (this.textAlign === TextAlignType.LEFT) {
      if (this.textType === TextDirectionType.VERTICAL) {
        this.textMesh.geometry.translate(-this.textWidth / 2 - offset, 0, 0)
      }
      // this.textMesh.geometry.translate(0, this.textWidth / 2, 0);
      else {
        this.textMesh.geometry.translate(0, -this.textHeight / 2 - offset, 0)
      }
    }

    // this.text.isText = true;

    this.add(this.textMesh)

    this.textMesh.name = 'textMesh'
  },

  // 改变字体角度(步长90度)
  setTextRot: function () {
    if (this.textType === TextDirectionType.HORIZONTAL) {
      this.textMesh.rotateX(THREE.Math.degToRad(90) * this.textRot)
      this.textBox.rotateX(THREE.Math.degToRad(90) * this.textRot)
    } else if (this.textType === TextDirectionType.VERTICAL) {
      this.textMesh.rotateY(-THREE.Math.degToRad(90) * this.textRot)
      this.textBox.rotateY(-THREE.Math.degToRad(90) * this.textRot)
    }
  },

  // 设置标注线包围盒
  setMarkLineBox: function () {
    this.removeMarkLineBox()

    var width = 0
    var height = 0
    var offset = (this.fontSize + 1) / FONT_SIZE_FACTOR / 3

    if (this.textType === TextDirectionType.HORIZONTAL) {
      width = this.textWidth
      height = this.textHeight
    } else {
      width = this.textHeight
      height = this.textWidth
    }

    if (this.textAlign !== TextAlignType.MIDDLE) {
      height += this.lineRadius * 2
    }

    var textGeometry = new THREE.BoxGeometry(width, height, 0.05)

    var lineGeometry = null
    // if (this.headShape === MarkLineType.NONE) {
    //   lineGeometry = new THREE.BoxGeometry(this.markLength, this.lineRadius * 2, 0.05)
    // }
    // else {
    lineGeometry = new THREE.BoxGeometry(this.markLength, this.lineRadius * 2, this.lineRadius * 2)
    // }

    // if (this.textAlign === 'left') {
    // 	textGeometry.translate(0, height / 2 + this.lineRadius, 0);
    // }
    // else if (this.textAlign === 'right') {
    // 	textGeometry.translate(0, - height / 2 - this.lineRadius, 0);
    // }

    if (this.textType === TextDirectionType.VERTICAL) {
      // textGeometry.rotateY(THREE.Math.degToRad(90))
      // textGeometry.rotateZ(THREE.Math.degToRad(90))
    }

    if (this.textAlign === TextAlignType.RIGHT) {
      if (this.textType === TextDirectionType.VERTICAL) {
        textGeometry.translate(0, this.textWidth / 2 + offset, 0)
      } else {
        textGeometry.translate(0, this.textHeight / 2 + offset, 0)
      }
    } else if (this.textAlign === TextAlignType.LEFT) {
      if (this.textType === TextDirectionType.VERTICAL) {
        textGeometry.translate(0, -this.textWidth / 2 - offset, 0)
      } else {
        textGeometry.translate(0, -this.textHeight / 2 - offset, 0)
      }
    }

    // 用于碰撞盒子的材质，材质设置为不可见
    // 可以使得盒子被检测到但不显示
    var boxMaterial = this.lineMaterial.clone()
    boxMaterial.visible = false
    this.textBox = new THREE.Mesh(textGeometry, boxMaterial)
    this.lineBox = new THREE.Mesh(lineGeometry, boxMaterial)

    // boxMaterial.transparent = true
    // boxMaterial.opacity = 1
    // boxMaterial.needsUpdate = true
    // this.textBox.visible = false
    // this.lineBox.visible = false

    this.textBox.isCusReturn = true
    this.textBox.cusReturn = function () {
      return this.parent
    }

    this.lineBox.isCusReturn = true
    this.lineBox.cusReturn = function () {
      return this.parent
    }

    if (!checkNone(this.intersectList)) {
      this.intersectList.push(this.textBox)
    }
    if (!checkNone(this.intersectList)) {
      this.intersectList.push(this.lineBox)
    }

    this.add(this.textBox)
    this.add(this.lineBox)
  },

  hide: function () {
    this.visible = false
    this.removeMarkLineBox()
  },

  show: function () {
    this.visible = true

    this.setMarkLineBox()
  },

  removeHead: function () {
    // 将原来的两个头去掉
    if (!checkNone(this.headLeft)) {
      this.headLeft.dispose()
      this.line.remove(this.headLeft)
    }
    if (!checkNone(this.headRight)) {
      this.headRight.dispose()
      this.line.remove(this.headRight)
    }
  },

  removeLine: function () {
    // 删除原有的线
    if (!checkNone(this.lineBody)) {
      if (this.lineBody.isMesh) {
        this.lineBody.dispose()
        this.line.remove(this.lineBody)
      } else if (this.lineBody.type === 'Group') {
        // 由于遍历时this指针会变，需要存为临时变量
        var lineBody = this.lineBody
        var removeList = []
        this.lineBody.traverse(function (child) {
          if (child.uuid !== lineBody.uuid) {
            removeList.push(child)
          }
        })

        for (var index = 0; index < removeList.markLength; ++index) {
          removeList[index].dispose()
          this.lineBody.remove(removeList[index])
        }
        removeList = []
        this.line.remove(this.lineBody)
      }
    }
  },

  removeText: function () {
    if (!checkNone(this.textMesh)) {
      this.textMesh.dispose()
      this.remove(this.textMesh)
    }
  },

  removeMarkLineBox: function () {
    if (!(checkNone(this.lineBox) || checkNone(this.intersectList))) {
      var index = this.intersectList.indexOf(this.textBox)
      if (index > -1) {
        this.intersectList.splice(index, 1)
      }

      index = this.intersectList.indexOf(this.lineBox)
      if (index > -1) {
        this.intersectList.splice(index, 1)
      }

      this.textBox.dispose()
      this.lineBox.dispose()

      this.remove(this.textBox)
      this.remove(this.lineBox)
    }
  },

  generateHalfArrow: function (width, height, depth) {
    var halfArrow = new THREE.BoxGeometry(width, height, depth)
    halfArrow.translate(width / 2, 0, -depth / 2)
    var triGeometry = new THREE.Geometry()
    var vertices = [
      -depth * Math.sqrt(3), -height / 2, -depth / 2, // 0
      0, -height / 2, -depth / 2, // 1
      0, -height / 2, depth / 2, // 2
      -depth * Math.sqrt(3), height / 2, -depth / 2, // 3
      0, height / 2, -depth / 2, // 4
      0, height / 2, depth / 2 // 5
    ]
    /* 三棱柱
			 3**********4  3,4,5为上面的三角形
				* *      *
				*    *   *
				*       **5
				*
			 0**********1  0,1,2为下面的三角形
			    *      *
			       *   *
			          **2
			*/

    var faces = [
      0, 1, 2,
      3, 5, 4,

      0, 2, 5,
      5, 3, 0,

      5, 2, 1,
      1, 4, 5,

      1, 0, 4,
      3, 4, 0
    ]

    var index = 0
    for (index = 0; index < vertices.length / 3; ++index) {
      triGeometry.vertices.push(
        new THREE.Vector3(vertices[index * 3], vertices[index * 3 + 1], vertices[index * 3 + 2])
      )
    }
    for (index = 0; index < faces.length / 3; ++index) {
      triGeometry.faces.push(
        new THREE.Face3(faces[index * 3], faces[index * 3 + 1], faces[index * 3 + 2])
      )
      triGeometry.faceVertexUvs[0].push([
		        new THREE.Vector2(0, 0),
		        new THREE.Vector2(0, 0),
		        new THREE.Vector2(0, 0)
		    ])
    }
    triGeometry.translate(0, 0, -depth / 2)

    halfArrow.merge(triGeometry)

    triGeometry.dispose()
    return halfArrow
  },

  generateArrow: function (width, height, depth) {
    var arrow = this.generateHalfArrow(width, height, depth)
    arrow.rotateY(THREE.Math.degToRad(30))
    var halfArrow = this.generateHalfArrow(width, height, depth)
    halfArrow.rotateY(THREE.Math.degToRad(30))
    halfArrow.rotateX(THREE.Math.degToRad(180))

    arrow.merge(halfArrow)
    arrow.translate(depth * 2, 0, 0)

    halfArrow.dispose()

    return arrow
  }
})

export { MarkLine }



