function ImageUI () {
  // Parent constructor //////////////////////////////////////////////

  Widget.call(this)

  // 构造数据模型
  DataModelFactory.call(this, DataModelType.IMAGE_UI)

  // Public variables ///////////////////////////////////////////////
  // this.src = ''

  // Private variables ///////////////////////////////////////////////
}

ImageUI.prototype.clone = function () {
  var ui = new ImageUI()
  ui.dataModel.copy(this.dataModel)
  return ui
}

ImageUI.prototype.toJSON = function () {
  return this.dataModel
}

