import { engineInterface } from '../core/EngineInterface.js'
import { PageType, TextureType } from '../core/Type.js'
import { PageFactory } from '../objects/Page.js'
import { SceneManager } from '../managers/SceneManager.js'
import { StorageManager } from '../managers/StorageManager.js'
import { ResourceManager } from '../managers/ResourceManager.js'
import { MaterialChangeData } from '../objects/MaterialChangeData.js'

function PageManager () {
  // Singleton pattern
  if (PageManager._instance !== undefined) {
    return PageManager._instance
  }
  PageManager._instance = this

  var scope = this

  // Public variables ////////////////////////////////////////////////
  // 页面列表
  this.pageList = []

  this.jsonMaterialList = null
  this.jsonModelChangeList = null

  // Private variables ////////////////////////////////////////////////
  var sceneManager = new SceneManager()
  var storageManager = new StorageManager()
  var resourceManager = new ResourceManager()

  var pidList = {}

  // 当前页面
  var curPage = null

  var pageBeginId = 100
  var pageEndId = 1000000

  var backImg = new Image()

  // Public methods ////////////////////////////////////////////////
  this.clear = function () {
    scope.removeAllPage(true)

    curPage = null

    for (var arg in pidList) {
      delete pidList[arg]
    }

    scope.jsonMaterialList = null
    scope.jsonModelChangeList = null

    init()
  }

  // 材质页面接口 ////////////////////////////////////////
  this.addMaterialButton = function () {
    if (!checkNone(curPage) &&
      curPage.pageType === PageType.MATERIAL) {
      curPage.addButton()
    }
  }

  this.removeMaterialButton = function (uiid) {
    if (!checkNone(curPage) &&
      curPage.pageType === PageType.MATERIAL) {
      curPage.removeButton(uiid)
    }
  }

  this.addMaterialChange = function () {
    var curButton = scope.getCurButton()

    console.log(curButton)
    if (!checkNone(curPage) &&
      curPage.pageType === PageType.MATERIAL) {
      curButton.addMaterialChange()
    }
  }

  this.removeMaterialChange = function (index) {
    var curButton = scope.getCurButton()

    if (!checkNone(curPage) && !checkNone(curButton) &&
      curPage.pageType === PageType.MATERIAL) {
      curButton.removeMaterialChange(index)
    }
  }

  // 标注页面接口 /////////////////////////////////////////////////
  this.addMarkLine = function () {
    if (!checkNone(curPage) &&
      curPage.pageType === PageType.MARKLINE) {
      curPage.addMarkLine()
    }
  }

  this.removeMarkLine = function (sid) {
    if (!checkNone(curPage) &&
      curPage.pageType === PageType.MARKLINE) {
      curPage.removeMarkLine(sid)
    }
  }

  this.addAutoMarkLine = function () {
    if (!checkNone(curPage) &&
      curPage.pageType === PageType.MARKLINE) {
      curPage.addAutoMarkLine()
    }
  }

  // 热点页面接口 /////////////////////////////////////////////////
  this.addHotPoint = function () {
    if (!checkNone(curPage) &&
      curPage.pageType === PageType.HOTPOINT) {
      curPage.addHotPoint()
    }
  }

  this.removeHotPoint = function (sid) {
    if (!checkNone(curPage) &&
      curPage.pageType === PageType.HOTPOINT) {
      curPage.removeHotPoint(sid)
    }
  }

  this.addHotPointHiddenUI = function (id) {
    if (!checkNone(curPage)) {
      curPage.addHotPointHiddenUI(id)
    }
  }

  this.removeHotPointHiddenUI = function () {
    if (!checkNone(curPage)) {
      curPage.removeHotPointHiddenUI()
    }
  }

  this.addHotPointImageUI = function () {
    if (!checkNone(curPage)) {
      curPage.addHotPointImageUI()
    }
  }

  this.removeHotPointImageUI = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.removeHotPointImageUI(null, uiid)
    }
  }

  this.copyHotPointImageUI = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.copyHotPointImageUI(null, uiid)
    }
  }

  this.addHotPointTextUI = function () {
    if (!checkNone(curPage)) {
      curPage.addHotPointTextUI()
    }
  }

  this.removeHotPointTextUI = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.removeHotPointTextUI(null, uiid)
    }
  }

  this.copyHotPointTextUI = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.copyHotPointTextUI(null, uiid)
    }
  }

  this.addHotPointVideoUI = function () {
    if (!checkNone(curPage)) {
      curPage.addHotPointVideoUI()
    }
  }

  this.removeHotPointVideoUI = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.removeHotPointVideoUI(null, uiid)
    }
  }

  this.copyHotPointVideoUI = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.copyHotPointVideoUI(null, uiid)
    }
  }

  this.setHotPointCamera = function () {
    if (!checkNone(curPage)) {
      curPage.setHotPointCamera()
    }
  }

  this.setAnimationCamera = function () {
    if (!checkNone(curPage)) {
      curPage.setAnimationCamera()
    }
  }

  // 动画页面接口 /////////////////////////////////////////////////
  // this.playAnimation = function () {
  //   if (!checkNone(curPage)) {
  //     curPage.playAnimation()
  //   }
  // }

  this.addAnimation = function () {
    if (!checkNone(curPage)) {
      curPage.addAnimation()
    }
  }

  this.removeAnimation = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.removeAnimation(uiid)
    }
  }

  this.addAnimationChange = function () {
    if (!checkNone(curPage)) {
      var curAnimation = curPage.getCurButton()
      if (!checkNone(curAnimation)) {
        curAnimation.addAnimationChange()
      }
    }
  }

  this.removeAnimationChange = function (index) {
    if (!checkNone(curPage)) {
      var curAnimation = curPage.getCurButton()
      if (!checkNone(curAnimation)) {
        curAnimation.removeAnimationChange(index)
      }
    }
  }

  this.addKeyFrame = function () {
    if (!checkNone(curPage)) {
      var curAnimation = curPage.getCurButton()
      if (!checkNone(curAnimation)) {
        var curAnimaChange = curAnimation.getCurAnimationChange()
        if (!checkNone(curAnimaChange)) {
          curAnimaChange.addKeyFrame(curAnimation.curTimePoint)
        }
      }
    }
  }

  this.removeKeyFrame = function (timePoint) {
    if (!checkNone(curPage)) {
      var curAnimation = curPage.getCurButton()
      if (!checkNone(curAnimation)) {
        var curAnimaChange = curAnimation.getCurAnimationChange()
        if (!checkNone(curAnimaChange)) {
          curAnimaChange.removeKeyFrame(timePoint)
        }
      }
    }
  }

  this.getCurAnimaChange = function () {
    if (!checkNone(curPage)) {
      if (curPage.pageType === PageType.ANIMATION) {
        var curAnimation = curPage.getCurButton()
        if (!checkNone(curAnimation)) {
          var curAnimaChange = curAnimation.getCurAnimationChange()
          return curAnimaChange
        }
      }
    }
    return null
  }

  this.getCurKeyFrame = function () {
    if (!checkNone(curPage)) {
      if (curPage.pageType !== PageType.ANIMATION) {
        return null
      }
      var curAnimation = curPage.getCurButton()
      if (!checkNone(curAnimation)) {
        console.log(curAnimation)
        var curAnimaChange = curAnimation.getCurAnimationChange()
        if (!checkNone(curAnimaChange)) {
          var curKeyFrame = curAnimaChange.getCurKeyFrame()
          return curKeyFrame
        }
      }
    }
    return null
  }

  // 链接页面接口 /////////////////////////////////////////////////
  this.addImageHyperLink = function () {
    if (!checkNone(curPage)) {
      curPage.addImageHyperLink()
    }
  }

  this.removeImageHyperLink = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.removeImageHyperLink(uiid)
    }
  }

  this.copyImageHyperLink = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.copyImageHyperLink(uiid)
    }
  }

  this.addButtonHyperLink = function () {
    if (!checkNone(curPage)) {
      curPage.addButtonHyperLink()
    }
  }

  this.removeButtonHyperLink = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.removeButtonHyperLink(uiid)
    }
  }

  this.copyButtonHyperLink = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.copyButtonHyperLink(uiid)
    }
  }

  // 页面UI接口 /////////////////////////////////////////////////
  this.addImageUI = function () {
    if (!checkNone(curPage)) {
      curPage.addImageUI()
    }
  }

  this.removeImageUI = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.removeImageUI(uiid)
    }
  }

  this.copyImageUI = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.copyImageUI(uiid)
    }
  }

  this.addTextUI = function () {
    if (!checkNone(curPage)) {
      curPage.addTextUI()
    }
  }

  this.removeTextUI = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.removeTextUI(uiid)
    }
  }

  this.copyTextUI = function (uiid) {
    if (!checkNone(curPage)) {
      curPage.copyTextUI(uiid)
    }
  }

  // 公用接口 ///////////////////////////////////////////
  this.update = function (pageRenderer) {
    if (isEditor) {
      if (!checkNone(curPage)) {
        var canvas = document.getElementById('glPageCanvas' + curPage.pid)
        canvas.width = canvas.clientWidth
        canvas.height = canvas.clientHeight

        var ctx = canvas.getContext('2d')
        pageRenderer.setSize(canvas.clientWidth, canvas.clientHeight)

        if (curPage.pageType === PageType.LINK ||
        curPage.pageType === PageType.LOADING) {
          var background = pageRenderer.background

          switch (background.type) {
            case TextureType.COLOR:
              ctx.fillStyle = background.color
              ctx.fillRect(0, 0, canvas.clientWidth, canvas.clientHeight)
              break
            case TextureType.TEXTURE:
              ctx.fillStyle = '#333333'
              ctx.fillRect(0, 0, canvas.clientWidth, canvas.clientHeight)

              ctx.drawImage(backImg, 0, 0, canvas.clientWidth, canvas.clientHeight)

              var texUrl = resourceManager.getImage(background.textureUrl)
              if (backImg.src !== texUrl) {
                backImg.src = texUrl
              }
              break
            default:
              ctx.fillStyle = '#333333'
              ctx.fillRect(0, 0, canvas.clientWidth, canvas.clientHeight)
              break
          }
        } else {
          ctx.drawImage(pageRenderer.domElement, 0, 0, canvas.clientWidth, canvas.clientHeight)
          // img.src = pageRenderer.domElement.toDataURL()
        }
      }
    }

    // if (!checkNone(curPage) &&
    //   curPage.pageType === PageType.ANIMATION) {
    //   curPage.update()
    // }
  }

  this.setPageCamera = function () {
    if (!checkNone(curPage)) {
      if (!checkNone(curPage.setPageCamera)) {
        curPage.setPageCamera()
      }
    }
  }

  this.loadPageCamera = function (changePage) {
    if (!checkNone(curPage)) {
      if (!checkNone(curPage.loadPageCamera)) {
        curPage.loadPageCamera(changePage)
      }
    }
  }

  this.addPage = function (pageType, pid) {
    var page = PageFactory(pageType)
    if (!checkNone(page)) {
      scope.pageList.push(page)
      if (checkNone(pid)) { scope.addPid(page) } else { scope.setId(page, pid) }
    }
    return page
  }

  // 如果不指定pid，则默认删除当前页面
  this.removePage = function (pid) {
    var removePage = null
    if (checkNone(pid)) {
      removePage = scope.getPageById(pid)
    } else {
      removePage = curPage
    }

    if (checkNone(removePage)) { return false }

    scope.removePid(removePage)

    removePage.removeContent()

    var index = scope.pageList.indexOf(removePage)
    scope.pageList.splice(index, 1)

    if (curPage.pid === pid) { curPage = null }
  }

  this.copyPage = function (pid) {
    var orgPage = null
    if (!checkNone(pid)) {
      orgPage = scope.getPageById(pid)
    } else {
      orgPage = curPage
    }

    if (checkNone(orgPage)) { return false }

    storageManager.loadPage(orgPage.dataModel)
  }

  this.removeAllPage = function (loading) {
    for (var index = 0; index < scope.pageList.length; ++index) {
      var page = scope.pageList[index]
      scope.removePid(page)
      page.removeContent(loading)
    }
    scope.pageList.splice(0, scope.pageList.length)

    curPage = null

    engineInterface.updatePageList()
  }

  this.setPageName = function (pid, pageName) {
    var page = scope.getPageById(pid)
    if (checkNone(page)) { return false }
    page.pageName = pageName
  }

  this.setCurPage = function (pid) {
    var foundPage = scope.getPageById(pid)

    var sceneManager = new SceneManager()
    sceneManager.getTransformControl().detach()

    if (!checkNone(foundPage)) {
      if (!checkNone(curPage)) { curPage.removePageSceneObj() }

      if (isEditor) {
        curPage = foundPage
        scope.loadPageCamera()
      } else if (isBrowser) {
        var changePage = false
        if (!checkNone(curPage)) { changePage = true }

        curPage = foundPage
        scope.loadPageCamera(changePage)
      }

      curPage.addPageSceneObj()

      engineInterface.clearCurSelect()

      return true
    }

    if (!checkNone(curPage)) { curPage.removePageSceneObj() }

    curPage = null
    return false
  }

  this.setCurPageIndex = function (index) {
    if (index < scope.pageList.length) { curPage = scope.pageList[index] }
  }

  this.getCurPage = function () {
    return curPage
  }

  this.getCurButton = function () {
    if (!checkNone(curPage)) {
      return curPage.getCurButton()
    }
    return null
  }

  this.getCurMaterialChange = function () {
    if (!checkNone(curPage)) {
      var button = curPage.getCurButton()
      if (!checkNone(button)) {
        return button.getCurMaterialChange()
      }
    }

    return null
  }

  // 新添加具有数据模型的对象到列表中
  this.addPid = function (page) {
    var pid = getId(pageBeginId, pageEndId)

    if (pid === 0) {
      console.log('PageManager, addModel(), pid: "Failed to get page id."')
      return false
    }

    if (!checkNone(page.dataModel)) {
      page.dataModel.pid = pid
      pidList[pid] = true
    }

    return true
  }

  // 从模型列表中删除对象的数据模型
  this.removePid = function (page) {
    if (checkNone(page.dataModel)) {
      return
    }
    var pid = page.dataModel.pid
    if (checkId(pid)) {
      pidList[pid] = false
    }
  }

  // 设置对象的pid
  this.setId = function (page, pid) {
    if (!checkId(pid)) {
      pidList[pid] = true
      page.pid = pid
    } else {
      scope.addPid(page)
    }
  }

  this.getPageById = function (pid) {
    for (var index = 0; index < scope.pageList.length; ++index) {
      if (pid === scope.pageList[index].pid) {
        return scope.pageList[index]
      }
    }
    return null
  }

  // Private methods ////////////////////////////////////////////////////
  function init () {
    scope.addPage(PageType.LOADING)
    engineInterface.updatePageList()
  }

  // 用于切换页面时存储镜头数据到页面对象中
  function saveCameraToObj () {
    var pageCamera = sceneManager.getPageCamera()
    var curPage = scope.pageList[curPageIndex]

    curPage.getCameraModel().copy(pageCamera.dataModel)
  }

  // 用于切换页面时从页面对象中读取镜头数据
  function loadCameraFromObj () {
    var pageCamera = sceneManager.getPageCamera()
    var curPage = scope.pageList[curPageIndex]

    pageCamera.dataModel.setFrom(curPage.getCameraModel())
    pageCamera.update()
  }

  // 检查pid是否已经被用，已经使用返回true，否则返回false
  function checkId (pid) {
    if (checkNone(pidList[pid])) {
      return false
    }
    return pidList[pid]
  }

  // 获取当前可用的最小id,如果为0,获取失败
  function getId (begin, end) {
    if (begin >= end) {
      return 0
    }

    for (var indexId = begin; indexId < end; ++indexId) {
      if (!checkId(indexId)) {
        return indexId
      }
    }
    return 0
  }

  init()
}

export { PageManager, PageType }



