import { engineInterface } from '../core/EngineInterface.js'

import { DataModelFactory, DataModelType } from '../core/DataModel.js'
import { SceneManager } from '../managers/SceneManager.js'
import { MaterialChangeData } from './MaterialChangeData.js'

function MaterialButton () {
  // Parent constructor //////////////////////////////////////////////
  // 构造数据模型
  DataModelFactory.call(this, DataModelType.MATERIALBUTTON)

  var scope = this

  // Public variables ///////////////////////////////////////////////
  // this.uiid = 0
  // this.btnName = ''
  // // 表明是文字按钮还是图片按钮
  // this.btnType = 0

  // this.shapeType = 'square'

  // this.actShapeColor = '#0999ff'
  // this.defShapeColor = '#333333'

  // this.actTextureUrl = ''
  // this.defTextureUrl = ''

  // this.actTextColor = '#ffffff'
  // this.defTextColor = '#333333'

  // this.fontSize = 14
  // this.opacity = 1.0

  // this.text = 'default'
  // this.materialChangeList = []
  // background

  // Private variables ///////////////////////////////////////////////
  var curMaterialChange = null

  var sceneManager = new SceneManager()

  // Public methods ///////////////////////////////////////////////////////
  this.addMaterialChange = function (setMatChange) {
    if (checkNone(setMatChange)) {
      var sceneObj = sceneManager.getCurSelected()
      var sid = sceneObj.sid
      var materialChange = scope.getMaterialChangeById(sid)
      if (checkNone(materialChange) && !checkNone(sceneObj)) {
        var matData = new MaterialChangeData(scope)
        var material = sceneObj.dataModel.material
        var tex = sceneObj.material.map

        matData.sid = sid
        matData.material.copy(material)
        if (!checkNone(tex)) {
          matData.uvData.tx = tex.offset.x
          matData.uvData.ty = tex.offset.y
          matData.uvData.sx = tex.repeat.x
          matData.uvData.sy = tex.repeat.y
        }

        curMaterialChange = matData

        scope.materialChangeList.push(matData)

        console.log(11, scope.materialChangeList)
        sceneObj.disposeList.push(matData)

        return matData
      }

      return null
    } else {
      var sceneObj = sceneManager.getObjBySid(setMatChange.sid)
      var matData = new MaterialChangeData(scope)

      matData.sid = setMatChange.sid
      matData.material.copy(setMatChange.material)
      matData.uvData.setFrom(setMatChange.uvData)

      scope.materialChangeList.push(matData)
      if (!checkNone(sceneObj)) { sceneObj.disposeList.push(matData) }

      return matData
    }
  }

  this.removeMaterialChange = function (index) {
    if (index >= 0 && index < scope.materialChangeList.length) {
      scope.materialChangeList[index].cancel()
      scope.materialChangeList.splice(index, 1)

      // // 更新接口数据
      // if (!checkNone(scope.displayData)) {
      //   scope.displayData.update(this)
      // }
      return true
    }

    return false
  }

  this.removeAllMaterialChange = function (cancelMatChange) {
    if (cancelMatChange) {
      for (var index = 0; index < scope.materialChangeList.length; ++index) {
        scope.materialChangeList[index].cancel()
      }
    }

    curMaterialChange = null

    scope.materialChangeList.splice(0, scope.materialChangeList.length)
  }

  this.removeMaterialChangeByObj = function (matChange) {
    var changeList = scope.materialChangeList
    var index = changeList.indexOf(matChange)
    if (index > -1) {
      scope.removeMaterialChange(index)
    }
  }

  this.setCurMaterialChange = function (index) {
    // var foundMaterialChange = scope.getMaterialChangeById(sid)

    // console.log(sid)
    // console.log(scope.materialChangeList)
    // if (!checkNone(foundMaterialChange)) {
    //   curMaterialChange = foundMaterialChange
    //   return true
    // }
    // return false

    if (index < 0 || index > scope.materialChangeList.length) {
      curMaterialChange = null
      return false
    }

    curMaterialChange = scope.materialChangeList[index]
    return true
  }

  this.setCurMaterialChangeFromSelected = function () {
    var sceneObj = sceneManager.getCurSelected()
    if (!checkNone(sceneObj) && sceneObj.isMesh) {
      for (var index = 0; index < scope.materialChangeList.length; ++index) {
        if (sceneObj.sid === scope.materialChangeList[index].sid) {
          scope.setCurMaterialChange(index)
          engineInterface.updateMaterialChangeIndex(index)

          return
        }
      }

      scope.setCurMaterialChange(-1)
      engineInterface.updateMaterialChangeIndex(-1)
    }
  }

  this.getCurMaterialChange = function () {
    return curMaterialChange
  }

  this.getMaterialChangeById = function (sid) {
    for (var index = 0; index < scope.materialChangeList.length; ++index) {
      if (sid === scope.materialChangeList[index].sid) {
        return scope.materialChangeList[index]
      }
    }
    return null
  }

  // 点击材质按钮时设置背景颜色
  this.setBackgroundColor = function (color, ifNone) {
    var background = sceneManager.getBackground()
    // 设置侧边栏显示的背景颜色
    background.setColor(color, ifNone)
    // 设置存储中的背景数据
    this.background.setFrom(background)
  }

  // 点击材质按钮时设置背景图片
  this.setBackgroundTexture = function (texUrl) {
    var background = sceneManager.getBackground()
    // 设置侧边栏显示的背景图片
    background.setTexture(texUrl)
    // 设置存储中的背景数据
    this.background.setFrom(background)
  }
}

MaterialButton.prototype.clone = function () {
  var btn = new MaterialButton()
  btn.dataModel.copy(this)
  return btn
}

MaterialButton.prototype.toJSON = function () {
  return this.dataModel
}

export { MaterialButton }



