<template>
  <div id="app" :style="style">
    <textarea class="debugInfo" disabled="disabled" readonly="readonly" v-show="debugInfo || debugInfo !== ''">{{ debugInfo }}</textarea>
    <div class="infoBoard" :style="loadingPageBackgroundStyle">
      <div class="windowUIContainer">
        <ui-image-component v-for="(item, index) in loadingPageData.pageUI.imgsData" :key="index" :imgData="item"></ui-image-component>
        <ui-text-component v-for="(item, index) in loadingPageData.pageUI.textData" :key="index" :textData="item"></ui-text-component>
        <ui-loading-component :loadingPageData="loadingPageData"></ui-loading-component>
      </div>
    </div>
    <window :curActivePage="curActivePage"></window>
    <!-- <window v-show="true" :curActivePage="curActivePage"></window> -->
    <navigation :pages="pagesData" v-show="loadingPageData.hasFinished"></navigation>
    <!-- <navigation :pages="pagesData" v-show="true"></navigation> -->
    <!-- 引擎不应该渲染页面预览页 -->
    <canvas id="glPageCanvas" v-show="false"></canvas>
  </div>
</template>

<script>
import { engineInterface } from './engine/core/Engine.js'
import window from './components/window.vue'
import navigation from './components/navigation.vue'
import uiLoadingComponent from './components/subComponents/ui/UILoading.vue'
import UIImageComponent from './components/subComponents/ui/UIImage.vue'
import UITextComponent from './components/subComponents/ui/UIText.vue'

export default {
  name: 'app',
  components: {
    'window': window,
    'navigation': navigation,
    'ui-loading-component': uiLoadingComponent,
    'ui-image-component': UIImageComponent,
    'ui-text-component': UITextComponent
  },
  mounted () {
    this.$store.state.runLoad = true
    engineInterface.runEngine()
    var playFunc = () => {
      document.getElementById('bkgMusic').play()
      document.removeEventListener('touchstart', playFunc, false)
    }
    document.addEventListener('touchstart', playFunc, false)
  },
  data () {
    console.log('data running!')
    console.log(this.$store.state.loadingPageData)
    return {
      PageType: this.$store.state.PageType,
      TextureType: this.$store.state.TextureType,
      showInfo: false,
      warnInfo: '',
      defaultPageData: {
        pageInfo: {
          name: '',
          pageType: this.$store.state.PageType.DEFAULT,
          id: -1
        },
        pageUI: {
          imgsData: [],
          textData: []
        }
      },
      loadingPageData: this.$store.state.loadingPageData
    }
  },
  computed: {
    debugInfo: function () {
      return this.$store.state.interface.debugInfo
    },
    pagesData: function () {
      // 加载页面信息到数组中给导航栏使用
      var ar = []
      this.$store.state.pagesList.forEach(ele => {
        if (ele.pageInfo.pageType !== this.PageType.LOADING) ar.push(ele.pageInfo)
      })
      return ar
    },
    curActivePage: function () {
      for (var index in this.$store.state.pagesList) {
        if (this.$store.state.pagesList[index].pageInfo.id === this.curActivePageId) {
          this.showInfo = false
          return this.$store.state.pagesList[index]
        }
      }
      console.warn(`App.vue: 找不到页面，页面ID: , ${this.curActivePageId}`)
      this.showInfo = true
      // this.warnInfo = '没有任何页面!'
      this.warnInfo = `${document.documentElement.clientHeight},${innerHeight}`
      return this.defaultPageData
    },
    curActivePageId: function () {
      return this.$store.getters.curActivePageId
    },
    loadingPageBackgroundStyle: function () {
      return {
        'background-size': '100% 100%',
        'background-image': this.loadingPageData.backgroundData.bkgType === this.TextureType.COLOR ? undefined : `url(${this.loadingPageData.backgroundData.bkgTex})`,
        'background-color': this.loadingPageData.backgroundData.bkgType === this.TextureType.COLOR ? this.loadingPageData.backgroundData.bkgColor : undefined,
        'opacity': this.loadingPageData.hasFinished ? 0 : 1
      }
    },
    style: function () {
      // var aspect = innerWidth / innerHeight
      // var width = 0
      // var height = 0
      // var marginTop = true
      // var margin = 0
      // var scale = 0
      // if (aspect > 640 / 1008) {
      //   // 更接近正方形
      //   scale = innerHeight / 1008
      //   height = innerHeight
      //   width = 640 * scale
      //   marginTop = false
      //   margin = (innerWidth - width) / 2
      // } else {
      //   // 更接近长方形
      //   scale = innerWidth / 640
      //   width = innerWidth
      //   height = 1008 * scale
      //   margin = (innerHeight - height) / 2
      // }
      return {
        width: `${innerWidth}px`,
        height: `${innerHeight}px`
        // 'margin-top': marginTop ? margin + 'px' : undefined,
        // 'margin-left': !marginTop ? margin + 'px' : undefined
      }
    }
  }
}
</script>

<style>
@import './assets/css/framework.css';
@import './assets/css/main.css';
@import './assets/css/navigation.css';
@import './assets/css/loading.css';

.infoBoard {
  position: absolute;
  z-index: 4000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  background-color: #ffffff;

  transition: opacity 0.3s;
  transition-delay: 2s;
  pointer-events: none;
}

.infoBoard>p {
  font-size: 2rem;
  margin: auto;
  color: red;
}

.testDIV {
  width: 45.72rem;
  height: 10rem;
  background-color: #0999ff;
}

.debugInfo {
  width: 50%;
  height: 50%;
  background-color: rgba(0,0,0, 0.35);
  pointer-events: none;
  overflow: none;

  position: absolute;
  z-index: 99999;
  color: white;

  resize: none;
}
</style>


