<template>
  <div class="hotPointUIContainer" :style="hotPointStyle">
    <ui-image-component v-for="item in curHotPointUI.imgsData" :key="item.id" :imgData="item"></ui-image-component>
    <ui-text-component v-for="item in curHotPointUI.textData" :key="item.id" :textData="item"></ui-text-component>
    <ui-video-component :disabled="disabled" v-for="item in curHotPointUI.videoData" :key="item.id" :videoData="item"></ui-video-component>
  </div>
</template>

<script>
import UIImageComponent from './ui/UIImage.vue'
import UITextComponent from './ui/UIText.vue'
import UIVideoComponent from './ui/UIVideo.vue'

export default {
  name: 'hotPointUI',
  components: {
    'ui-image-component': UIImageComponent,
    'ui-text-component': UITextComponent,
    'ui-video-component': UIVideoComponent
  },
  props: {
    curHotPointUI: Object,
    disabled: {
      default: true,
      type: Boolean
    }
  },
  data () {
    return {
      curOpacity: 0
    }
  },
  computed: {
    hotPointStyle: function () {
      return {
        'opacity': this.curOpacity,
        display: this.curOpacity > 0 ? 'block' : 'none'
      }
    }
  },
  watch: {
    disabled: function (newValue, oldValue) {
      oldValue ? this.curOpacity = 0 : this.curOpacity = 1
      var lastTime = performance.now()
      var ease = (timeStamp) => {
        var delta = timeStamp - lastTime
        if (newValue) {
          this.curOpacity -= 0.002 * delta
          if (this.curOpacity > 0) requestAnimationFrame(ease)
        } else {
          this.curOpacity += 0.002 * delta
          if (this.curOpacity < 1) requestAnimationFrame(ease)
        }
      }
      requestAnimationFrame(ease)
    }
  }
}
</script>

<style>
.hotPointUIContainer {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
}
</style>


