  this.clear = function () {
    scope.ifListenDo = false

    scope.loadCount = 0

    jsonProjectOrigin = {}
    undoList.splice(0, undoList.length)
    redoList.splice(0, redoList.length)

    doCount = -1
  }

  this.saveEditor = function () {
    var jsonEditor = {}
    var projectList = []

    var projectManager = new ProjectManager()

    var projectList = projectManager.projectList

    jsonEditor.projectList = projectList

    if (platform.name === 'Electron') {
      ipcRenderer.sendSync('SaveEditor',
        JSON.stringify(jsonEditor))
    }
  }

  this.loadEditor = function () {
    var jsonEditor = ipcRenderer.sendSync('LoadEditor')
    var projectManager = new ProjectManager()

    if (!checkNone(jsonEditor)) {
      projectManager.projectList.splice(0, projectManager.projectList.length)
      jsonEditor.projectList.forEach(proj => {
        var project = new Project()
        project.dataModel.setFrom(proj)
        projectManager.projectList.push(project)
      })
      engineInterface.updateProjectList()
    }
  }

