  // 存储项目
  this.saveProject = function (saveToFile) {
    var projectManager = new ProjectManager()

    var jsonProject = {}
    var jsonScene = saveScene()
    var jsonSceneObjList = saveSceneObj()
    var jsonPageList = savePageList()
    var jsonPageData = savePageData()
    var jsonCurSelect = saveCurSelect()

    var resourceList = scope.saveResourceList()

    if (isEditor) {
      jsonProject.projectInfo = projectManager.curProject.dataModel.clone()
    }
    jsonProject.resourceList = resourceList
    jsonProject.scene = jsonScene
    jsonProject.sceneObjList = jsonSceneObjList
    jsonProject.pageList = jsonPageList
    jsonProject.pageData = jsonPageData
    jsonProject.curSelect = jsonCurSelect

    if (platform.name === 'Electron' && saveToFile && isEditor) {
      // if (saveToFile) {
      if (checkNone(projectManager.curProject)) { return false }

      engineInterface.curSelectUI.splice(0, engineInterface.curSelectUI.length)

      if (isEditor) {
        var date = new Date()
        projectManager.curProject.date = date.format()

        engineInterface.updateProjectList()

        scope.saveEditor()
      }

      ipcRenderer.sendSync('SaveProject', projectManager.curProject.filePath,
        JSON.stringify(jsonProject))

      ipcRenderer.sendSync('CheckResource', projectManager.curProject.filePath, jsonProject.resourceList)

      engineInterface.hasSaved = true
    }

    if (isEditor && !saveToFile) {
      jsonProject.tmpAnimaScene = scope.saveModelChange()
    }

    // return JSON.stringify(jsonProject);
    return jsonProject
  }

  // 从项目文件读取项目
  this.loadProject = function (file, callback, projPath) {
    if (typeof file === 'string') {
      var animationManager = new AnimationManager()

      sceneManager.clear()
      pageManager.clear()
      resourceManager.clear()
      animationManager.clear()
      scope.clear()

      engineInterface.curSelectUI.splice(0, engineInterface.curSelectUI.length)

      undoList.splice(0, undoList.length)
      redoList.splice(0, redoList.length)

      // if (platform.name === 'Electron') {
      //   file = ipcRenderer.sendSync('LoadProject', file)
      // }

      var loader = new THREE.FileLoader()
      loader.load(file, function (data) {
        var jsonProject = JSON.parse(data)
        var jsonProjectInfo = jsonProject.projectInfo
        var jsonScene = jsonProject.scene
        var jsonSceneObjList = jsonProject.sceneObjList
        var jsonPageList = jsonProject.pageList
        var jsonPageData = jsonProject.pageData
        var jsonCurSelect = jsonProject.curSelect

        var jsonResourceList = jsonProject.resourceList

        if (isEditor) {
          loadProjectInfo(jsonProjectInfo, projPath)
        }

        if (checkNone(jsonResourceList)) {
          engineInterface.loadingProjectData.errorCode = 203
          resourceManager.loading = false
          return false
        }

        if (checkNone(jsonResourceList.urlList)) {
          loadAfterResource()
          return true
        }

        var loadPromise = loadResourceList(jsonResourceList.urlList)

        loadPromise.then(function () {
          loadAfterResource()
        }, function () {
          engineInterface.loadingProjectData.errorCode = 203
          resourceManager.loading = false
          return false
        })

        function loadAfterResource () {
          loadRemoveList(jsonResourceList.removeList)

          if (isBrowser) { engineInterface.loadingPageData.hasFinished = true }

          engineInterface.updateModelList()
          engineInterface.updateImageList()

          var res = true
          res = res & loadScene(jsonScene)
          res = res & loadSceneObj(jsonSceneObjList)
          res = res & loadPageData(jsonPageData)
          res = res & loadPageList(jsonPageList)
          if (isEditor) { res = res & loadCurSelect(jsonCurSelect) }

          if (!res) {
            engineInterface.loadingProjectData.errorCode = 203
          }

          if (!checkNone(callback)) { callback(res) }

          resourceManager.loading = false
        }

        // loadPageList(jsonPageList);
      })
    } else if (typeof file === 'object') {
      var jsonProject = file
      var jsonScene = jsonProject.scene
      var jsonSceneObjList = jsonProject.sceneObjList
      var jsonPageList = jsonProject.pageList
      var jsonPageData = jsonProject.pageData
      var jsonCurSelect = jsonProject.curSelect

      var jsonResourceList = jsonProject.resourceList

      var jsonTmpAnimaScene = jsonProject.tmpAnimaScene
      // var loadPromise = loadResourceList(jsonResourceList.urlList)

      // loadPromise.then(function () {
      // console.log(resourceManager.modelList)
      // loadRemoveList(jsonResourceList.removeList)

      // if (isBrowser)
      //   engineInterface.loadingPageData.hasFinished = true

      // engineInterface.updateModelList()
      // engineInterface.updateImageList()

      scope.ifListenDo = false

      // loadResourceListChange(jsonResourceList)

      loadScene(jsonScene)
      loadSceneObjChange(jsonSceneObjList)
      loadPageData(jsonPageData)
      loadPageList(jsonPageList)

      if (isEditor) {
        loadCurSelect(jsonCurSelect)
        loadTmpAnimaScene(jsonTmpAnimaScene)
      }

      scope.ifListenDo = true

      if (!checkNone(callback)) { callback() }

      // resourceManager.loading = false
      // })

      // if (!checkNone(jsonScene)) {
      //   loadScene(jsonScene)
      // }
      // if (!checkNone(jsonSceneObjList)) {
      //   loadSceneObjChange(jsonSceneObjList)
      // }
      // loadPageList(jsonPageList);
    }
  }

