  this.begindo = function () {
    if (isBrowser) { return }
    if (!this.ifListenDo) { return }

    ++doCount

    if (doCount > 0) {
      return
    }

    jsonProjectOrigin = scope.saveProject()
    // undoList.push(jsonProjectOrigin);
    // if (redoList.length !== 0) {
    //   redoList = []
    // }

    scope.dispatchEvent(begindoEvent)
  }

  this.begindoInterface = function () {
    if (isBrowser) { return }

    scope.ifListenDo = false

    jsonProjectOrigin = scope.saveProject()
    // undoList.push(jsonProjectOrigin);
    // if (redoList.length !== 0) {
    //   redoList = []
    // }

    scope.dispatchEvent(begindoEvent)
  }

  this.enddo = function () {
    if (isBrowser) { return }

    if (!this.ifListenDo) { return }

    if (doCount > 0) {
      --doCount
      return
    } else { --doCount }

    var jsonProjectEnd = scope.saveProject()
    var jsonChange = compareProject(jsonProjectOrigin, jsonProjectEnd)
    // console.log(jsonProjectOrigin, jsonProjectEnd);
    console.log('Compare Res: ', jsonChange)
    jsonProjectOrigin = {}
    // undoList.push(jsonProjectEnd);
    if (!checkNone(jsonChange)) {
      if (redoList.length !== 0) {
        redoList.splice(0, redoList.length)
      }

      undoList.push(jsonChange)

      engineInterface.hasSaved = false

      if (undoList.length > MAX_DO_COUNT) {
        undoList.splice(0, 1)
      }
    }

    scope.dispatchEvent(enddoEvent)
  }

  this.enddoInterface = function () {
    if (isBrowser) { return }

    var jsonProjectEnd = scope.saveProject()
    var jsonChange = compareProject(jsonProjectOrigin, jsonProjectEnd)
    // console.log(jsonProjectOrigin, jsonProjectEnd);
    console.log('Compare Res: ', jsonChange)
    jsonProjectOrigin = {}
    // undoList.push(jsonProjectEnd);
    if (!checkNone(jsonChange)) {
      if (redoList.length !== 0) {
        redoList.splice(0, redoList.length)
      }

      undoList.push(jsonChange)

      engineInterface.hasSaved = false

      if (undoList.length > MAX_DO_COUNT) {
        undoList.splice(0, 1)
      }
    }

    scope.dispatchEvent(enddoEvent)

    scope.ifListenDo = true
  }

  this.undo = function () {
    if (undoList.length > 0) {
      scope.ifListenDo = false

      console.log('undo')

      engineInterface.curSelectUI.splice(0, engineInterface.curSelectUI.length)

      var jsonChange = undoList.pop()
      console.log(jsonChange.startState)

      scope.loadProject(jsonChange.startState)

      redoList.push(jsonChange)

      scope.dispatchEvent(undoEvent)
      // var jsonEndState = undoList.pop();
      // var jsonStartState = undoList.pop();

      // scope.loadProject(jsonStartState);

      // redoList.push(jsonStartState);
      // redoList.push(jsonEndState);
      scope.ifListenDo = true
    }

    sceneManager.getTransformControl().update()
  }

  this.redo = function () {
    // console.log(redoList)
    if (redoList.length > 0) {
      scope.ifListenDo = false
      console.log('redo')
      engineInterface.curSelectUI.splice(0, engineInterface.curSelectUI.length)
      var jsonChange = redoList.pop()
      console.log(jsonChange)

      scope.loadProject(jsonChange.endState)

      undoList.push(jsonChange)

      scope.dispatchEvent(redoEvent)
      // var jsonEndState = redoList.pop();
      // var jsonStartState = redoList.pop();

      // scope.loadProject(jsonEndState);

      // undoList.push(jsonStartState);
      // undoList.push(jsonEndState);
      scope.ifListenDo = true
    }
    sceneManager.getTransformControl().update()
  }

