  this.saveResourceList = function () {
    // 资源列表
    var jsonResourceList = {}
    var urlList = []
    var removeList = {}
    var modelList = resourceManager.modelList

    for (var modelUrl in modelList) {
      urlList.push(modelUrl)
      removeList[modelUrl] = {}
      for (var modelName in modelList[modelUrl]) {
        var model = modelList[modelUrl][modelName]
        if (model.deleted) {
          removeList[modelUrl][modelName] = true
        }
      }
    }

    for (var imgUrl in resourceManager.imageList) {
      urlList.push(imgUrl)
    }

    jsonResourceList.urlList = urlList
    jsonResourceList.removeList = removeList

    return jsonResourceList
  }

  // Private methods ///////////////////////////////////////////////////

  function saveScene () {
    var scene = sceneManager.getScene()
    var dataModel = scene.dataModel.clone()
    return dataModel
  }

  // 保存场景对象，返回json数据列表
  function saveSceneObj () {
    var scene = sceneManager.getScene()
    var sceneObjList = scene.sceneObjList

    var dataModel = null
    var sceneObj = null
    var jsonObjList = {}

    for (var index in sceneObjList) {
      sceneObj = sceneObjList[index]
      // 如果场景对象已经失效，忽略
      if (checkNone(sceneObj)) {
        continue
      }

      dataModel = sceneObj.dataModel

      // 克隆模型从而使存储不受后续操作影响
      jsonObjList[dataModel.sid] = dataModel.clone()

      var pageManager = new PageManager()
      var jsonMaterialList = pageManager.jsonMaterialList
      if (!checkNone(jsonMaterialList)) {
        if (dataModel.sid in jsonMaterialList) {
          jsonObjList[dataModel.sid].material = jsonMaterialList[dataModel.sid]
        }
      }

      var jsonModelChangeList = pageManager.jsonModelChangeList
      if (!checkNone(jsonModelChangeList)) {
        // console.log(jsonModelChangeList)
        if (dataModel.sid in jsonModelChangeList) {
          jsonObjList[dataModel.sid].position = jsonModelChangeList[dataModel.sid].position
          jsonObjList[dataModel.sid].quaternion = jsonModelChangeList[dataModel.sid].quaternion
          jsonObjList[dataModel.sid].scale = jsonModelChangeList[dataModel.sid].scale
          jsonObjList[dataModel.sid].opacity = jsonModelChangeList[dataModel.sid].opacity
          jsonObjList[dataModel.sid].pivotPos = jsonModelChangeList[dataModel.sid].pivotPos
        }
      }
    }

    return jsonObjList
  }
  this.saveSceneObj = saveSceneObj

  // 保存场景对象，返回json数据列表
  function saveLightList (filePath) {
    var scene = sceneManager.getScene()
    var sceneObjList = scene.sceneObjList

    var dataModel = null
    var sceneObj = null
    var jsonObjList = {}

    for (var index in sceneObjList) {
      sceneObj = sceneObjList[index]
      // 如果场景对象已经失效，忽略
      if (checkNone(sceneObj) || !sceneObj.isLight) {
        continue
      }

      dataModel = sceneObj.dataModel

      // 克隆模型从而使存储不受后续操作影响
      jsonObjList[dataModel.sid] = dataModel.clone()

      var pageManager = new PageManager()
      var jsonMaterialList = pageManager.jsonMaterialList
      if (!checkNone(jsonMaterialList)) {
        if (dataModel.sid in jsonMaterialList) {
          jsonObjList[dataModel.sid].material = jsonMaterialList[dataModel.sid]
        }
      }

      var jsonModelChangeList = pageManager.jsonModelChangeList
      if (!checkNone(jsonModelChangeList)) {
        // console.log(jsonModelChangeList)
        if (dataModel.sid in jsonModelChangeList) {
          jsonObjList[dataModel.sid].position = jsonModelChangeList[dataModel.sid].position
          jsonObjList[dataModel.sid].quaternion = jsonModelChangeList[dataModel.sid].quaternion
          jsonObjList[dataModel.sid].scale = jsonModelChangeList[dataModel.sid].scale
          jsonObjList[dataModel.sid].opacity = jsonModelChangeList[dataModel.sid].opacity
          jsonObjList[dataModel.sid].pivotPos = jsonModelChangeList[dataModel.sid].pivotPos
        }
      }
    }

    if (!checkNone(filePath)) {
      ipcRenderer.sendSync('SaveLight',
        filePath,
        JSON.stringify(jsonObjList))
    }

    return jsonObjList
  }
  this.saveLightList = saveLightList

  // 保存场景对象，返回json数据列表
  function saveSceneObjMaterial () {
    var scene = sceneManager.getScene()
    var sceneObjList = scene.sceneObjList

    var dataModel = null
    var sceneObj = null
    var jsonObjList = {}

    for (var index in sceneObjList) {
      sceneObj = sceneObjList[index]
      // 如果场景对象已经失效，忽略
      if (checkNone(sceneObj) || !sceneObj.isMesh) {
        continue
      }

      dataModel = sceneObj.dataModel

      // 克隆模型从而使存储不受后续操作影响
      jsonObjList[dataModel.sid] = dataModel.material.clone()
    }

    return jsonObjList
  }
  this.saveSceneObjMaterial = saveSceneObjMaterial

  // 保存场景对象，返回json数据列表
  function saveModelChange () {
    var scene = sceneManager.getScene()
    var sceneObjList = scene.sceneObjList

    var dataModel = null
    var sceneObj = null
    var jsonObjList = {}

    for (var index in sceneObjList) {
      sceneObj = sceneObjList[index]
      // 如果场景对象已经失效，忽略
      if (checkNone(sceneObj) || !sceneObj.isMesh) {
        continue
      }

      dataModel = new ModelChangeDataModel()
      dataModel.setFrom(sceneObj.dataModel)

      jsonObjList[sceneObj.dataModel.sid] = dataModel
    }

    return jsonObjList
  }
  this.saveModelChange = saveModelChange

  function savePageList () {
    var pageList = pageManager.pageList

    var dataModel = null
    var page = null
    var jsonPageList = []

    for (var index = 0; index < pageList.length; ++index) {
      page = pageList[index]

      if (checkNone(page)) { continue }

      dataModel = page.dataModel.clone()
      console.log(dataModel)

      jsonPageList.push(dataModel)
    }

    return jsonPageList
  }
  this.savePageList = savePageList

  function savePageData () {
    var jsonMaterialList = JSON.parse(JSON.stringify(pageManager.jsonMaterialList))
    var jsonModelChangeList = JSON.parse(JSON.stringify(pageManager.jsonModelChangeList))

    var jsonPageData = {}

    jsonPageData.jsonMaterialList = jsonMaterialList
    jsonPageData.jsonModelChangeList = jsonModelChangeList

    return jsonPageData
  }

  function saveCurSelect () {
    var jsonCurSelect = {}

    jsonCurSelect.curPageId = engineInterface._curPageId

    var sceneObj = sceneManager.getCurSelected()
    if (!checkNone(sceneObj)) {
      jsonCurSelect.curSelectSid = sceneObj.sid
    } else {
      jsonCurSelect.curSelectSid = -1
    }

    var page = pageManager.getCurPage()
    if (!checkNone(page)) {
      var pageContent = engineInterface.getCurDisplayPage().pageContent
      switch (page.pageType) {
        case PageType.MATERIAL:
          jsonCurSelect.curBtnId = engineInterface.saveCurBtnId
          break

        case PageType.MARKLINE:
          break

        case PageType.ANIMATION:
          jsonCurSelect.curBtnId = engineInterface.saveCurBtnId
          jsonCurSelect.curFrame = engineInterface.saveCurFrame
          break

        case PageType.HOTPOINT:
          break

        case PageType.LINK:
          break
      }
    }

    return jsonCurSelect
  }

  function loadScene (jsonScene) {
    if (checkNone(jsonScene)) { return false }
    // if (!checkNone(jsonScene.envUrl)) {
    if (!checkNone(jsonScene.envIdx)) {
      // 读取当前页面的场景
      // sceneManager.loadEnv(jsonScene.envUrl, function () {
      sceneManager.setEnv(jsonScene.envIdx, function () {
        sceneManager.setEnvBlur(jsonScene.blur)
        engineInterface.sceneData.update()
      })
    } else if (!checkNone(jsonScene.blur)) {
      sceneManager.setEnvBlur(jsonScene.blur)
    }

    if (!checkNone(jsonScene.fov)) {
      sceneManager.setCameraFov(jsonScene.fov)
    }

    if (!checkNone(jsonScene.ambientIntensity)) {
      sceneManager.setAmbientIntensity(jsonScene.ambientIntensity)
    }

    if (!checkNone(jsonScene.hemisphereIntensity)) {
      sceneManager.setHemisphereIntensity(jsonScene.hemisphereIntensity)
    }

    if (!checkNone(jsonScene.bkgMusicSrc)) {
      sceneManager.getScene().bkgMusicSrc = jsonScene.bkgMusicSrc
    }

    engineInterface.sceneData.update()

    return true
  }

  // 读取场景对象，由于直接从文件读取，所以清空所有场景对象
  function loadSceneObj (jsonSceneObjList) {
    if (checkNone(jsonSceneObjList)) { return false }

    sceneManager.removeAllObj()

    var scene = sceneManager.getScene()

    var res = true

    for (var sid in jsonSceneObjList) {
      var jsonSceneObj = jsonSceneObjList[sid]
      res = res & !checkNone(addSceneObj(jsonSceneObj, parseInt(sid), scene))
    }

    engineInterface.updateMeshList()
    engineInterface.updateLightList()

    return res
  }
  this.loadSceneObj = loadSceneObj

  function loadLightList (file, callback) {
    var loader = new THREE.FileLoader()
    loader.load(file, function (data) {
      var jsonLightList = JSON.parse(data)
      var scene = sceneManager.getScene()

      for (var sid in jsonLightList) {
        var jsonSceneObj = jsonLightList[sid]
        addSceneObj(jsonSceneObj, parseInt(sid), scene)
      }

      engineInterface.updateMeshList()
      engineInterface.updateLightList()

      if (!checkNone(callback)) callback()
    })
  }
  this.loadLightList = loadLightList

  function loadSceneObjMaterial (jsonSceneObjList) {
    if (checkNone(jsonSceneObjList)) { return false }

    var scene = sceneManager.getScene()

    for (var sid in jsonSceneObjList) {
      var mesh = sceneManager.getObjBySid(sid)
      if (checkNone(mesh)) { continue }

      var jsonMaterial = jsonSceneObjList[sid]

      switch (jsonMaterial.type) {
        case TextureType.COLOR:
          scene.setColor(mesh, jsonMaterial.color)
          break
        case TextureType.TEXTURE:
          var texture = resourceManager.getTexture(jsonMaterial.textureUrl)
          scene.setTexture(mesh, texture)
          break
      }
      mesh.dataModel.material.textureUrl = jsonMaterial.textureUrl

      var uvData = jsonMaterial.uvData
      sceneManager.setUVTranslate(uvData.tx, uvData.ty, mesh)
      sceneManager.setUVScale(uvData.sx, uvData.sy, mesh)
    }

    engineInterface.updateMeshList()
  }
  this.loadSceneObjMaterial = loadSceneObjMaterial

  function loadModelChange (jsonChangeList) {
    if (checkNone(jsonChangeList)) { return false }

    var scene = sceneManager.getScene()

    for (var sid in jsonChangeList) {
      var mesh = sceneManager.getObjBySid(sid)
      if (checkNone(mesh)) { continue }

      var jsonModelChange = jsonChangeList[sid]
      var pos = jsonModelChange.position
      var quat = jsonModelChange.quaternion
      var scale = jsonModelChange.scale
      var pivotPos = jsonModelChange.pivotPos

      mesh.dataModel.position.x = pos.x
      mesh.dataModel.position.y = pos.y
      mesh.dataModel.position.z = pos.z
      mesh.dataModel.quaternion._x = quat._x
      mesh.dataModel.quaternion._y = quat._y
      mesh.dataModel.quaternion._z = quat._z
      mesh.dataModel.quaternion._w = quat._w
      mesh.dataModel.scale.x = scale.x
      mesh.dataModel.scale.y = scale.y
      mesh.dataModel.scale.z = scale.z
      mesh.dataModel.pivotPos.x = pivotPos.x
      mesh.dataModel.pivotPos.y = pivotPos.y
      mesh.dataModel.pivotPos.z = pivotPos.z

      scene.setObjOpacity(mesh, jsonModelChange.opacity)
    }

    engineInterface.updateMeshList()
  }
