  // 读取页面列表
  function loadPageList (jsonPageList) {
    if (checkNone(jsonPageList)) { return false }
    pageManager.removeAllPage(true)

    var index = 0
    if (isBrowser) { index = 1 }
    for (; index < jsonPageList.length; ++index) {
      loadPage(jsonPageList[index])
    }

    engineInterface.updatePageList()

    return true
  }

  function loadPageData (jsonPageData) {
    if (checkNone(jsonPageData)) { return false }
    if (isEditor) {
      pageManager.jsonMaterialList = jsonPageData.jsonMaterialList
    }
    pageManager.jsonModelChangeList = jsonPageData.jsonModelChangeList

    return true
  }

  function loadTmpAnimaScene (jsonTmpAnimaScene) {
    loadModelChange(jsonTmpAnimaScene)
  }

  function loadCurSelect (jsonCurSelect) {
    if (checkNone(jsonCurSelect)) { return false }

    engineInterface.curPageId = jsonCurSelect.curPageId

    sceneManager.setCurSelected(jsonCurSelect.curSelectSid)

    if (jsonCurSelect.curSelectSid >= 0) {
      var sceneObj = sceneManager.getObjBySid(jsonCurSelect.curSelectSid)
      if (sceneObj.isMesh) {
        engineInterface.curMeshId = sceneObj.sid
      }
      if (sceneObj.isLight) {
        engineInterface.curLightId = sceneObj.sid
      }
      if (sceneObj.isMarkLine) {
        engineInterface.curMarkLineId = sceneObj.sid
      }
      if (sceneObj.isHotPoint) {
        engineInterface.curHotPointId = sceneObj.sid
      }
    }

    var page = pageManager.getCurPage()
    if (!checkNone(page)) {
      var pageContent = engineInterface.getCurDisplayPage().pageContent
      switch (page.pageType) {
        case PageType.MATERIAL:
          pageContent.curSelectBtnId = jsonCurSelect.curBtnId
          break

        case PageType.MARKLINE:
          break

        case PageType.ANIMATION:
          pageContent.curSelectBtnId = jsonCurSelect.curBtnId
          var displayAnimation = engineInterface.getCurDisplayAnimation()
          if (!checkNone(displayAnimation)) { displayAnimation.curFrame = jsonCurSelect.curFrame }
          break

        case PageType.HOTPOINT:
          break

        case PageType.LINK:
          break
      }
    }

    return true
  }

  function loadPage (jsonPage) {
    var page = null

    page = pageManager.addPage(jsonPage.pageType, jsonPage.pid)

    page.dataModel.setFrom(jsonPage)

    // 页面图片UI列表
    var uiList = jsonPage.imgUIList
    for (var index = 0; index < uiList.length; ++index) {
      var uiData = uiList[index]

      var ui = page.addImageUI(uiData.uiid)

      ui.dataModel.setFrom(uiData)
      // console.log(ui)
    }

    // 页面文本UI列表
    uiList = jsonPage.textUIList
    for (var index = 0; index < uiList.length; ++index) {
      var uiData = uiList[index]

      var ui = page.addTextUI(uiData.uiid)

      ui.dataModel.setFrom(uiData)
    }

    if (!checkNone(page)) {
      switch (page.pageType) {
        case PageType.LOADING:
          if (isEditor) {
            var projectManager = new ProjectManager()
            if (!checkNone(projectManager.curProject)) {
              var bgUrl = page.dataModel.background.textureUrl
              if (bgUrl.length !== 0) {
                projectManager.curProject.cover = resourceManager.getImage(bgUrl)
                engineInterface.updateProjectList()
              } else {
                projectManager.curProject.cover = 'static/default/images/project.png'
              }
            }
          }
          break
        case PageType.BASEFUNC:
          break
        case PageType.MATERIAL:
          var btnList = jsonPage.matBtnList
          for (var index = 0; index < btnList.length; ++index) {
            var jsonButton = btnList[index]
            loadMaterialButton(page, jsonButton)
          }
          page.isCameraLock = jsonPage.isCameraLock
          break
        case PageType.MARKLINE:
          var markLineList = jsonPage.markLineList
          console.log(markLineList)
          for (var sid in markLineList) {
            page.markLineList[sid] = true
            var markLine = sceneManager.getObjBySid(sid)
            markLine.hide()
          }
          break
        case PageType.ANIMATION:
          var animationList = jsonPage.animationList
          for (var index = 0; index < animationList.length; ++index) {
            var jsonAnimation = animationList[index]
            loadAnimationButton(page, jsonAnimation)
          }
          break
        case PageType.HOTPOINT:
          var hotPointList = jsonPage.hotPointList
          for (var sid in hotPointList) {
            var hotPointUI = page.hotPointList[sid] = new HotPointUI()
            loadHotPointUI(jsonPage.hotPointList[sid], sid, page)
            var hotPoint = sceneManager.getObjBySid(sid)
            hotPoint.hide()
          }
          break
        case PageType.LINK:
          // 页面UI列表
          uiList = jsonPage.imgHyperLinkList
          for (var index = 0; index < uiList.length; ++index) {
            var uiData = uiList[index]

            var ui = page.addImageHyperLink(uiData.uiid)

            ui.dataModel.setFrom(uiData)
            console.log(ui)
          }

          // 页面UI列表
          uiList = jsonPage.btnHyperLinkList
          for (var index = 0; index < uiList.length; ++index) {
            var uiData = uiList[index]

            var ui = page.addButtonHyperLink(uiData.uiid)

            ui.dataModel.setFrom(uiData)
          }
          break
      }
    }
  }
  this.loadPage = loadPage

  function loadMaterialButton (page, jsonButton) {
    var button = page.addButton(jsonButton.uiid)

    if (!checkNone(button)) {
      button.dataModel.setFrom(jsonButton)

      var matChangeList = jsonButton.materialChangeList

      for (var index = 0; index < matChangeList.length; ++index) {
        var jsonMatChange = matChangeList[index]
        loadMaterialChange(button, jsonMatChange)
      }
    }
  }

