  // 用于从对象中读取场景对象数据，所以不清空场景
  function loadSceneObjChange (jsonSceneObjList) {
    if (checkNone(jsonSceneObjList)) { return false }

    var scene = sceneManager.getScene()
    var addList = jsonSceneObjList.addList
    var changeList = jsonSceneObjList.changeList
    var delList = jsonSceneObjList.delList
    var sceneObj = null
    var jsonSceneObj = null

    if (!checkNone(delList)) {
      for (var sid in delList) {
        var delSid = parseInt(sid)
        sceneObj = sceneManager.getObjBySid(parseInt(delSid))

        if (sceneObj.isMesh) {
          sceneManager.removeObj(sceneObj)
          engineInterface.updateMeshList()
          // engineInterface.updatePageContent()
        } else if (sceneObj.isLight) {
          sceneManager.removeObj(sceneObj)
          engineInterface.updateLightList()
        } else if (sceneObj.isMarkLine) {
          sceneManager.removeObj(sceneObj)
          // pageManager.removeMarkLine(sid)
          // scope.updatePageContent()
        } else if (sceneObj.isHotPoint) {
          sceneManager.removeObj(sceneObj)
          // pageManager.removeHotPoint(sid)
          // scope.updatePageContent()
        }
      }
    }

    if (!checkNone(addList)) {
      for (var sid in addList) {
        var addSid = parseInt(sid)
        jsonSceneObj = addList[addSid]

        var sceneObj = addSceneObj(jsonSceneObj, parseInt(addSid), scene)

        if (sceneObj.isMesh) {
          engineInterface.updateMeshList()
          // engineInterface.updatePageContent()
        } else if (sceneObj.isLight) {
          engineInterface.updateLightList()
        } else if (sceneObj.isMarkLine) {
          // var curPage = pageManager.getCurPage()
          // curPage.markLineList[sceneObj.sid] = true
          sceneObj.show()
          // engineInterface.updatePageContent()
        } else if (sceneObj.isHotPoint) {
          // var curPage = pageManager.getCurPage()
          // curPage.hotPointList[sceneObj.sid] = new HotPointUI()
          // scope.loadHotPointUI(curPage.hotPointList[addSid], sceneObj.sid, curPage)
          sceneObj.show()
          // engineInterface.updatePageContent()
        }
      }
    }

    if (!checkNone(changeList)) {
      for (var sid in changeList) {
        // sceneObj = sceneManager.getObjBySid(changeSid);
        // sceneManager.removeObj(sceneObj);
        // jsonSceneObj = changeList[changeSid];
        // addSceneObj(jsonSceneObj, changeSid, scene);
        var changeSid = parseInt(sid)

        sceneObj = sceneManager.getObjBySid(changeSid)
        jsonSceneObj = changeList[changeSid]
        addSceneObj(jsonSceneObj, changeSid, scene, sceneObj)

        if (sceneObj.isMesh) {
          engineInterface.stopUpdate = false
          engineInterface.curMeshData.update()
          engineInterface.updateMeshList()
          // engineInterface.updatePageContent()
        } else if (sceneObj.isLight) {
          engineInterface.stopUpdate = false
          engineInterface.curLightData.update()
          engineInterface.updateLightList()
        } else if (sceneObj.isMarkLine) {
          engineInterface.updateCurMarkLine()
          // var curPage = pageManager.getCurPage()
          // curPage.markLineList[sceneObj.sid] = true
          // sceneObj.show()
          // engineInterface.updatePageContent()
        } else if (sceneObj.isHotPoint) {
          engineInterface.updateCurHotPoint()
          // var curPage = pageManager.getCurPage()
          // curPage.hotPointList[sceneObj.sid] = new HotPointUI()
          // scope.loadHotPointUI(curPage.hotPointList[addSid], sceneObj.sid, curPage)
          // sceneObj.show()
          // engineInterface.updatePageContent()
        }
      }
    }
  }
  this.loadSceneObjChange = loadSceneObjChange

  function addSceneObj (jsonSceneObj, sid, scene, sceneObj) {
    var light = null
    var needUpdateUrl = false
    switch (jsonSceneObj.type) {
      case DataModelType.MESH:
        // 将mesh添加到场景中
        var mesh = null
        if (checkNone(sceneObj)) {
          mesh = sceneManager.addMesh(
            jsonSceneObj.modelUrl,
            jsonSceneObj.orgName,
            sid)
        } else {
          mesh = sceneObj
        }

        if (checkNone(mesh) || checkNone(mesh.dataModel)) {
          return null
        }

        mesh.dataModel.setFrom(jsonSceneObj)
        mesh.position.copy(mesh.dataModel.pivotPos)
        if (mesh.dataModel.scale.x < 0) {
          mesh.dataModel.scale.x *= -1
          mesh.scale.x = -1
        }
        if (mesh.dataModel.scale.y < 0) {
          mesh.dataModel.scale.y *= -1
          mesh.scale.y = -1
        }
        if (mesh.dataModel.scale.z < 0) {
          mesh.dataModel.scale.z *= -1
          mesh.scale.z = -1
        }

        // console.log(mesh);
        // page.add(mesh);

        console.log(mesh)

        var material = jsonSceneObj.material
        console.log(scene)
        switch (material.type) {
          case TextureType.COLOR:
            // scene.setColor(mesh, material.color)
            sceneManager.setColor(material.color, mesh)
            break
          case TextureType.TEXTURE:
            // var texture = resourceManager.getTexture(material.textureUrl)
            // scene.setTexture(mesh, texture)
            sceneManager.setTexture(material.textureUrl, mesh)
            break
        }

        var uvData = material.uvData
        sceneManager.setUVTranslate(uvData.tx, uvData.ty, mesh)
        sceneManager.setUVScale(uvData.sx, uvData.sy, mesh)

        // (!checkNone(sceneObj) && needUpdateUrl)) {
        //   var texture = resourceManager.getTexture(jsonSceneObj.textureUrl)
        //   scene.setTexture(mesh, texture)
        // }
        if (!checkNone(jsonSceneObj.opacity)) { scene.setObjOpacity(mesh, jsonSceneObj.opacity) }
        // if (!checkNone(jsonSceneObj.shininess)) { scene.setShininess(mesh, 1 - jsonSceneObj.shininess) }
        if (!checkNone(jsonSceneObj.metalness)) {
          scene.setMetalness(mesh, jsonSceneObj.metalness)
        }
        if (!checkNone(jsonSceneObj.roughness)) {
          scene.setRoughness(mesh, jsonSceneObj.roughness)
        }
        // break
        return mesh
      case DataModelType.POINT_LIGHT:
        console.log(sceneObj)
        if (checkNone(sceneObj)) {
          light = sceneManager.addLight('Point', sid)
        } else {
          light = sceneObj
        }

        if (checkNone(light) || checkNone(light.dataModel)) {
          return null
        }

        light.dataModel.setFrom(jsonSceneObj)
        // page.add(light);

        light.update()
        // break
        return light

      case DataModelType.SPOT_LIGHT:
        if (checkNone(sceneObj)) {
          light = sceneManager.addLight('Spot', sid)
        } else {
          light = sceneObj
        }

        if (checkNone(light) || checkNone(light.dataModel)) {
          return null
        }
        light.dataModel.setFrom(jsonSceneObj)
        // page.add(light);

        light.update()
        // break
        return light

      case DataModelType.DIRECT_LIGHT:
        if (checkNone(sceneObj)) {
          light = sceneManager.addLight('Direction', sid)
        } else {
          light = sceneObj
        }

        if (checkNone(light) || checkNone(light.dataModel)) {
          return null
        }
        light.dataModel.setFrom(jsonSceneObj)
        // page.add(light);

        light.update()
        // break
        return light

      case DataModelType.RECTAREA_LIGHT:
        if (checkNone(sceneObj)) {
          light = sceneManager.addLight('RectArea', sid)
        } else {
          light = sceneObj
        }

        if (checkNone(light) || checkNone(light.dataModel)) {
          return null
        }

        light.dataModel.setFrom(jsonSceneObj)

        light.update()
        // break
        return light
      case DataModelType.MARKLINE:
        var markLine = null
        if (checkNone(sceneObj)) {
          markLine = sceneManager.addMarkLine(null, null, null, null, sid)
        } else {
          markLine = sceneObj
        }

        if (checkNone(markLine) || checkNone(markLine.dataModel)) {
          return null
        }
        markLine.dataModel.setFrom(jsonSceneObj)
        markLine.updateText = true
        markLine.updateHead = true
        markLine.updateLine = true
        markLine.updateBox = true
        markLine.updateColor = true
        markLine.updateTextRot = true
        markLine.updateOpacity = true
        markLine.update()

        if (checkNone(sceneObj)) { markLine.visible = false }
        // break
        return markLine

      case DataModelType.HOTPOINT:
        var hotPoint = null
        if (checkNone(sceneObj)) {
          hotPoint = sceneManager.addHotPoint(new THREE.Vector3(0, 0, 0), sid)
        } else {
          hotPoint = sceneObj
        }

        if (checkNone(hotPoint) || checkNone(hotPoint.dataModel)) {
          return null
        }

        hotPoint.dataModel.setFrom(jsonSceneObj)
        hotPoint.updateFromDataModel()

        if (checkNone(sceneObj)) { hotPoint.hide() }

        // break
        return hotPoint

      default:
        break
    }
  }
  this.addSceneObj = addSceneObj

  function loadProjectInfo (projectInfo, projPath) {
    if (checkNone(projectInfo)) { return }
    var projectManager = new ProjectManager()
    var curProject = new Project()
    curProject.dataModel.setFrom(projectInfo)

    if (!checkNone(projPath)) {
      curProject.filePath = projPath
      curProject.dataModel.filePath = projPath
    }

    // 如果项目已经存在，删除原项目
    var projList = projectManager.projectList
    for (var index = 0; index < projList.length; ++index) {
      var project = projList[index]
      if (project.filePath === curProject.filePath) {
        projList.splice(index, 1)
        --index
      }
    }

    projList.push(curProject)
    projectManager.curProject = curProject

    engineInterface.updateProjectList()
  }

  // 读取保存的资源
  function loadResourceList (resourceList) {
    var promiseArray = []
    if (!checkNone(resourceList)) {
      resourceManager.loading = true
      resourceManager.loadTotal = resourceList.length
      for (var index = 0; index < resourceList.length; ++index) {
        var promise = new Promise(function (resolve, reject) {
          resourceManager.importResource(resourceList[index], resolve, reject)
        })
        promiseArray.push(promise)
      }
    }
    return Promise.all(promiseArray)
  }

  function loadRemoveList (removeList) {
    if (checkNone(removeList)) { return false }
    for (var modelUrl in removeList) {
      for (var name in removeList[modelUrl]) {
        resourceManager.modelList[modelUrl][name].deleted = true
      }
    }
  }

  function compareProject (jsonOrigin, jsonEnd) {
    // var jsonResourceCompRes = compareResourceList(jsonOrigin.resourceList, jsonEnd.resourceList)
    var jsonSceneCompRes = compareScene(jsonOrigin.scene, jsonEnd.scene)
    var jsonSceneObjListCompRes = compareSceneObjList(jsonOrigin.sceneObjList, jsonEnd.sceneObjList)
    // 比较存储的页面原始数据（材质，动画等）
    var jsonPageDataCompRes = comparePageData(jsonOrigin.pageData, jsonEnd.pageData)
    // 比较各个页面的数据
    var jsonPageListCompRes = comparePageList(jsonOrigin.pageList, jsonEnd.pageList)
    var jsonCurSelectCompRes = compareCurSelect(jsonOrigin.curSelect, jsonEnd.curSelect)
    var jsonTmpAnimaSceneCompRes = compareTmpAnimaScene(jsonOrigin.tmpAnimaScene, jsonEnd.tmpAnimaScene)

    var jsonStartState = {}
    var jsonEndState = {}
    var jsonCompRes = {}

    // if (!checkNone(jsonResourceCompRes)) {
    //   jsonStartState.resourceList = jsonResourceCompRes.startState
    //   jsonEndState.resourceList = jsonResourceCompRes.endState
    // }

    if (!checkNone(jsonSceneCompRes)) {
      jsonStartState.scene = jsonSceneCompRes.startState
      jsonEndState.scene = jsonSceneCompRes.endState
    }

    if (!checkNone(jsonSceneObjListCompRes)) {
      jsonStartState.sceneObjList = jsonSceneObjListCompRes.startState
      jsonEndState.sceneObjList = jsonSceneObjListCompRes.endState
    }

    if (!checkNone(jsonPageDataCompRes)) {
      jsonStartState.pageData = jsonPageDataCompRes.startState
      jsonEndState.pageData = jsonPageDataCompRes.endState
    }

    if (!checkNone(jsonPageListCompRes)) {
      jsonStartState.pageList = jsonPageListCompRes.startState
      jsonEndState.pageList = jsonPageListCompRes.endState
    }

    if (!checkNone(jsonCurSelectCompRes)) {
      jsonStartState.curSelect = jsonCurSelectCompRes.startState
      jsonEndState.curSelect = jsonCurSelectCompRes.endState
    }

    if (!checkNone(jsonTmpAnimaSceneCompRes)) {
      jsonStartState.tmpAnimaScene = jsonTmpAnimaSceneCompRes.startState
      jsonEndState.tmpAnimaScene = jsonTmpAnimaSceneCompRes.endState
    }

    if (checkNone(jsonSceneCompRes) &&
      checkNone(jsonSceneObjListCompRes) &&
      // checkNone(jsonPageDataCompRes) &&
      checkNone(jsonPageListCompRes) &&
      checkNone(jsonTmpAnimaSceneCompRes)) { return null }
    // !jsonCurSelectCompRes.res

    jsonCompRes.startState = jsonStartState
    jsonCompRes.endState = jsonEndState

    return jsonCompRes
  }

