<template>
  <div class="UILoad" :style="style">
    <div v-if="loadingIconData.style === 0" class="UILoading_1" :class="{'UILoadPaused': !$store.state.runLoad}">
      <div :style="{'background-color': loadingIconData.color}"></div>
      <div :style="{'background-color': loadingIconData.color}"></div>
      <div :style="{'background-color': loadingIconData.color}"></div>
      <div :style="{'background-color': loadingIconData.color}"></div>
      <div :style="{'background-color': loadingIconData.color}"></div>
    </div>
    <div v-if="loadingIconData.style === 1" class="UILoading_2" :class="{'UILoadPaused': !$store.state.runLoad}">
      <div :style="{'background-color': loadingIconData.color}"></div>
      <div :style="{'background-color': loadingIconData.color}"></div>
      <div :style="{'background-color': loadingIconData.color}"></div>
    </div>
    <div v-if="loadingIconData.style === 2" class="UILoading_3" :class="{'UILoadPaused': !$store.state.runLoad}">
      <div :style="{'background-color': loadingIconData.color}"></div>
      <div :style="{'border-color': `${loadingIconData.color} transparent ${loadingIconData.color} transparent`}"></div>
    </div>
    <p :style="fontStyle">{{ loadingProgress + '%' }}</p>
  </div>
</template>

<script>
export default {
  name: 'loading-component',
  props: {
    loadingPageData: Object
  },
  computed: {
    loadingProgress: function () {
      console.log('progress changed!!! curProgress is: ' + this.loadingPageData.curProgress)
      return this.loadingPageData.curProgress
    },
    loadingIconData: function () {
      return this.loadingPageData.loadPageData.loadingIcon
    },
    style: function () {
      var re = this.$store.state.loadingWidget.calc()
      return {
        width: `${re.width}px`,
        height: `${re.height}px`,

        left: `${re.left}px`,
        top: `${re.top}px`,
        opacity: `${this.loadingIconData.opacity}`,
        'z-index': `${this.loadingIconData.zIndex}`,
        transform: `rotate(${this.loadingIconData.rot}deg)`
      }
    },
    fontStyle: function () {
      return {
        color: `${this.loadingIconData.color}`,
        'font-size': `2rem`
      }
    }
  }
}
</script>

<style>
.UILoad {
  position: absolute;
  z-index: 3;

  transition: box-shadow 0.3s;

  user-select: none;

  display: flex;
  flex-direction: column;
}

.UILoad>div {
  width: 100%;
  height: 100%;
}

.UILoadPaused>div {
  animation: unset;
}

.UILoad>p {
  margin: auto;
  margin-bottom: -2.5rem;
  text-align: center;
}
</style>


