<template>
  <div class="UIText" :style="style">
    <div>
      <p :style="fontStyle">{{ textData.text }}</p>
    </div>
  </div>
</template>

<script>

export default {
  name: 'ui-text-component',
  props: {
    textData: Object
  },
  computed: {
    style: function () {
      var re = this.textData.widget.calc()
      return {
        width: `${re.width}px`,
        height: `${re.height}px`,
        left: `${re.left}px`,
        top: `${re.top}px`,
        transform: `rotate(${this.textData.rot}deg)`,
        'z-index': `${this.textData.zIndex}`
      }
    },
    fontStyle: function () {
      return {
        'font-size': `${this.fontSize / 14}rem`,
        'color': `${this.textData.fontColor}`,
        'text-align': this.textData.textAlign === 0 ? 'left' : this.textData.textAlign === 1 ? 'center' : 'right'
        // 'font-family': this.$store.state.fontFamily[this.textData.fontType].value
      }
    },
    fontSize: function () {
      return parseInt(this.$store.state.fontSizes[this.textData.fontSize].name)
    }
  }
}
</script>

<style>
.UIText {
  position: absolute;
  z-index: 3;

  transition: box-shadow 0.3s;

  pointer-events: none;
  user-select: none;
}

.UIText.active {
  box-shadow: 0 0 7px 2px #0999ff;
}

.UIText>div {
  width: 100%;
  height: 100%;
  display: flex;
}

.UIText>div>p {
  width: 100%;
  height: 100%;
  margin: 0px;
}

.UIText>div>textarea {
  width: 100%;
  height: auto;
  background-color: rgba(0,0,0,0);
  resize: none;
  border: none;
  margin: auto;
}

.UIText>div>textarea:focus {
  outline: none;
}
</style>


