import { PageType, TextureType } from '../core/Type.js'
import { DataModelFactory, DataModelType } from '../core/DataModel.js'
import { SkyBox } from '../objects/SkyBox.js'
import { Mesh } from '../objects/Mesh.js'
import { StorageManager } from '../managers/StorageManager.js'

function Scene () {
  // Parent constructor //////////////////////////////////////////////
  THREE.Scene.call(this)

  // 构造数据模型
  DataModelFactory.call(this, DataModelType.SCENE)

  // Private variables //////////////////////////////////////////////////
  var scope = this

  var canvas = document.getElementById('glCanvas')

  // 创建镜头
  var camera = new THREE.PerspectiveCamera(75,
    canvas.clientWidth / canvas.clientHeight, 0.01, 10000)

  // 场景默认环境光
  var ambientLight = new THREE.AmbientLight(0xffffff, 0.0)

  var hemiLight = new THREE.HemisphereLight(0xffffff, 0xffffff, 0.6)

  // var hemisphereLight = new THREE.HemisphereLight(0xffffff, 0xffffff, 1)

  // 网格辅助器, 创建辅助观察的网格
  var gridHelper = new THREE.GridHelper(100, 100)

  // Public variables ///////////////////////////////////////////////
  // 场景所有对象列表
  this.sceneObjList = {}

  // 场景所有可选择的布告板对象列表
  this.spriteIntersectList = []
  // 场景所有可选择的物体对象列表
  this.objIntersectList = []

  // 在页面窗口场景中不可见的对象列表
  this.pageHideList = []

  this.pageHideList.push(gridHelper)

  // 天空盒，由于用到镜头，所以要放在镜头创建后声明
  this.skyBox = new SkyBox(camera, scope)

  // Public methods //////////////////////////////////////////////////////////
  this.clear = function () {
    var storageManager = new StorageManager()

    for (var arg in scope.sceneObjList) {
      delete scope.sceneObjList[arg]
    }

    scope.objIntersectList.splice(0, scope.objIntersectList.length)
  }

  // 拷贝原有的添加函数，覆盖场景添加函数
  this.addOrigin = this.add
  this.add = function (sceneObj, shutSelect) {
    if (sceneObj instanceof Mesh) { scope.addOrigin(sceneObj.group) } else { scope.addOrigin(sceneObj) }

    // 如果存在有效sid，添加到对象列表中
    if (!checkNone(sceneObj.dataModel)) {
      var sid = sceneObj.sid
      if ((!checkNone(sid)) && sid !== 0) {
        scope.sceneObjList[sid] = sceneObj
      }
    }

    // console.log(scope.sceneObjList);
    // 如果为自定义的三种灯光，调用灯光本身的add函数来添加控制盒和辅助器
    // 自定义的标注线
    // 自定义的热点
    if (sceneObj.isCustom) {
      sceneObj.addToScene(scope, shutSelect)
    }

    scope.skyBox.setObjEnv(sceneObj)
  }

  // 拷贝原有的删除函数，覆盖场景删除函数
  this.removeOrigin = this.remove
  this.remove = function (sceneObj, shutSelect) {
    if (checkNone(sceneObj)) {
      console.log('Scene, remove(), sceneObj: "sceneObj is undefined or null."')
      return false
    } else if (typeof sceneObj !== 'object') {
      console.log("Scene, remove(), sceneObj: \"sceneObj should be a \'object\'.\"")
      return false
    } else {
      if (sceneObj instanceof Mesh) { scope.removeOrigin(sceneObj.group) } else { scope.removeOrigin(sceneObj) }

      // 如果为自定义的三种灯光，则调用灯光本身的remove函数来清空控制盒和辅助器
      // 自定义的标注线
      // 自定义的热点
      if (sceneObj.isCustom) {
        sceneObj.removeFromScene(shutSelect)
      }

      // 从对象列表中删除场景对象
      if (!checkNone(sceneObj.dataModel)) {
        var sid = sceneObj.sid
        if ((!checkNone(sid)) && sid !== 0) {
          delete scope.sceneObjList[sid]
        }
      }

      return true

      // var index = -1;
      // if (sceneObj.isBillboard) {
      // 	// 删除碰撞列表中的对象
      // 	index = scope.spriteIntersectList.indexOf(sceneObj);

      // 	if (index > -1)
      // 		scope.spriteIntersectList.splice(index, 1);
      // }
      // else {
      // // 删除碰撞列表中的对象
      // index = scope.objIntersectList.indexOf(sceneObj);

      // if (index > -1)
      // 	scope.objIntersectList.splice(index, 1);
      // }

      // console.log(sceneObj.parent);

      // // 当删除的对象不在Scene的直接子节点下，调用对象的父节点删除对象
      // if (sceneObj.parent.uuid !== scope.uuid) {
      // 	sceneObj.parent.remove(sceneObj);
      // }
      // // 如果对象为Scene的直接子节点，调用原有的remove函数来删除对象
      // else {
      // 	scope.removeOrigin(sceneObj);
      // }
    }
  }

  // 获取当前镜头
  this.getCamera = function () {
    return camera
  }

  // 设置默认环境光的亮度
  this.setAmbientIntensity = function (intensity) {
    intensity = Math.max(0, Math.min(1, intensity))
    ambientLight.intensity = intensity

    scope.dataModel.ambientIntensity = intensity
  }

  this.setHemisphereIntensity = function (intensity) {
    // intensity = Math.max(0, Math.min(1, intensity))
    hemiLight.intensity = intensity

    scope.dataModel.hemisphereIntensity = intensity
  }

  this.setCameraFov = function (nFov) {
    camera.fov = nFov
    camera.updateMatrixWorld()
    camera.updateProjectionMatrix()

    scope.dataModel.fov = nFov
  }

  // 加载环境贴图
  this.loadEnv = function (envUrl, callback) {
    scope.skyBox.loadEnv(envUrl, callback)

    scope.dataModel.envUrl = envUrl
  }

  this.setEnv = function (envIdx, callback) {
    if (envIdx !== scope.dataModel.envIdx) {
      switch (envIdx) {
        case 0:
          scope.loadEnv('')
          break
        case 1:
          scope.loadEnv('static/default/images/摄影棚.jpg', callback)
          break
        case 2:
          scope.loadEnv('static/default/images/房间.jpg', callback)
          break
        case 3:
          scope.loadEnv('static/default/images/室外.jpg', callback)
          break
        case 4:
          scope.loadEnv('static/default/images/阴天.jpg', callback)
          break
      }
      scope.dataModel.envIdx = envIdx
    }
  }

  // blur是模糊半径
  this.setEnvBlur = function (blur) {
    if (!checkNone(blur)) {
      scope.skyBox.setEnvBlur(blur)

      scope.dataModel.blur = blur
    }
  }

  this.setColor = function (sceneObj, color, fixDataModel) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setColor(), sceneObj: "sceneObj is undefined or null."')
    } else if (typeof sceneObj !== 'object') {
      console.log("Scene, setColor(), sceneObj: \"sceneObj should be a \'object\'.\"")
    } else if (sceneObj instanceof Array) {
      console.log("Scene, setColor(), sceneObj: \"sceneObj can\'t be Array.\"")
    } else if (!('material' in sceneObj)) {
      console.log('Scene, setColor(), sceneObj: "No material in sceneObj."')
    } else {
      sceneObj.material.map = null

      // console.log(sceneObj.material)

      var hexColor = color
      if (typeof (color) === 'string') {
        sceneObj.material.color.setStyle(color)
      } else if (typeof (color) === 'number') {
        sceneObj.material.color.setHex(color)
      }
      sceneObj.material.needsUpdate = true

      if (!checkNone(sceneObj.dataModel) && !fixDataModel) {
        sceneObj.dataModel.material.type = TextureType.COLOR
        sceneObj.dataModel.material.color = hexColor
      }
    }
  }

  this.setTexture = function (sceneObj, texture, fixDataModel) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setTexture(), sceneObj: "sceneObj is undefined or null."')
    } else if (typeof sceneObj !== 'object') {
      console.log("Scene, setTexture(), sceneObj: \"sceneObj should be a \'object\'.\"")
    } else if (sceneObj instanceof Array) {
      console.log("Scene, setTexture(), sceneObj: \"sceneObj can\'t be Array.\"")
    } else if (!('material' in sceneObj)) {
      console.log('Scene, setTexture(), sceneObj: "No material in sceneObj."')
    } else {
      if (checkNone(texture)) {
        if (!checkNone(sceneObj.dataModel) &&
          !checkNone(sceneObj.dataModel.material) &&
          !fixDataModel) {
          sceneObj.dataModel.material.type = TextureType.TEXTURE
        }
        return false
      }

      sceneObj.material.color.setHex(0xffffff)
      sceneObj.material.map = texture
      sceneObj.material.needsUpdate = true

      if (!checkNone(sceneObj.dataModel) &&
        !checkNone(sceneObj.dataModel.material) &&
        !fixDataModel) {
        sceneObj.dataModel.material.type = TextureType.TEXTURE
      }
    }
  }

  // 设置物体的位置
  this.setObjPos = function (sceneObj, space, x, y, z) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setObjPos(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setObjPos(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }

    if (sceneObj.hasPivot) { sceneObj = sceneObj.group }

    var newPos = null// 新位置

    if (space === 'world') {
      newPos = sceneObj.parent.worldToLocal(new THREE.Vector3(x, y, z))
    } else {
      newPos = new THREE.Vector3(x, y, z)
    }

    // 设置新的物体位置
    // sceneObj.position.copy(newPos);
    // sceneObj.position.x = newPos.x;
    sceneObj.position.copy(newPos)
    sceneObj.updateMatrixWorld()

    // 及时更新灯光控制盒和辅助器的位置
    if (sceneObj.isLight) { sceneObj.update() }

    return true
  }

  // 设置对象的旋转角度，x,y,z代表绕这几个轴旋转的角度，单位是radian
  // 若移动控制器的space是world，则x,y,z代表的是世界空间的轴
  // 若space是local，则x,y,z代表的是局部坐标下的轴
  this.setObjRotate = function (sceneObj, space, x, y, z) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setObjRotate(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setObjRotate(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }

    if (sceneObj.hasPivot) { sceneObj = sceneObj.group }

    var newRotateEuler = new THREE.Euler(x, y, z, 'XYZ')

    // if (space === 'world') {
    //   // 输入的是全局的旋转，而只能设置局部旋转
    //   // 以下代码将求出能够完成该全局旋转的局部旋转四元数
    //   // 注意，全局四元数=父节点全局四元数*局部四元数
    //   var newWorldRotateQuate = new THREE.Quaternion()
    //   newWorldRotateQuate.setFromEuler(newRotateEuler)

    //   var parentQuate = sceneObj.parent.getWorldQuaternion()
    //   var parentQuateConjugate = parentQuate.conjugate()

    //   newWorldRotateQuate.premultiply(parentQuateConjugate)

    //   sceneObj.setRotationFromQuaternion(newWorldRotateQuate)
    // } else {
    sceneObj.setRotationFromEuler(newRotateEuler)
    // }

    // sceneObj.rotation._y = y
    // sceneObj.updateMatrix()
    // console.log(sceneObj.rotation)

    sceneObj.traverse(function (child) {
      if (child.isLight) { child.update() }
    })

    return true
  }

  // 只对局部坐标系进行缩放，毕竟针对全局进行缩放会造成模型变形
  this.setObjScale = function (sceneObj, x, y, z) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setObjScale(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setObjScale(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }

    if (sceneObj.hasPivot) { sceneObj = sceneObj.group }

    // 防止矩阵变换出错，限制三个缩放的数值最小为0.01
    x = Math.max(x, 0.01)
    y = Math.max(y, 0.01)
    z = Math.max(z, 0.01)

    // if(sceneObj.isLight){
    // 	//对光源不能直接设置缩放变换，应设置光源属性
    // 	return;
    // }
    // else if(sceneObj.isMesh){
    if (sceneObj.isMesh || sceneObj instanceof THREE.Group) {
      sceneObj.scale.set(x, y, z)
      sceneObj.updateMatrix()
    } else {
      console.log("Scene, setObjScale(), sceneObj: \"sceneObj should be \'mesh\'.\"")
      return false
    }

    return true
  }

  this.setObjPivotPos = function (sceneObj, space, x, y, z) {
    if (!sceneObj.hasPivot) { return false }

    if (checkNone(sceneObj)) {
      console.log('Scene, setObjPos(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setObjPos(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }

    var newPos = null// 新位置

    newPos = new THREE.Vector3(x, y, z)

    var tmpPos = new THREE.Vector3().copy(newPos)
    console.log(1, sceneObj.group.position.x, sceneObj.group.position.y, sceneObj.group.position.z, tmpPos)
    tmpPos.sub(sceneObj.group.position)
    console.log(1, sceneObj.group.position.x, sceneObj.group.position.y, sceneObj.group.position.z, sceneObj.position, tmpPos)
    var rotMatrix = new THREE.Matrix4().extractRotation(sceneObj.group.matrixWorld)
    var tempMatrix = new THREE.Matrix4()
    tmpPos.applyMatrix4(tempMatrix.getInverse(rotMatrix))
    tmpPos.x /= sceneObj.group.scale.x
    tmpPos.y /= sceneObj.group.scale.y
    tmpPos.z /= sceneObj.group.scale.z
    sceneObj.position.sub(tmpPos)
    // sceneObj.dataModel.pivotPos.copy(sceneObj.position)
    sceneObj.group.position.copy(newPos)

    console.log(2, sceneObj.group.position, sceneObj.position)

    return true
  }

  this.setLightCastShadow = function (sceneObj, castShadow) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setLightColor(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setLightColor(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }

    if (sceneObj.isLight) {
      sceneObj.castShadow = castShadow
      // console.log(sceneObj)
    }

    return true
  }

  // 设置灯光的颜色
  this.setLightColor = function (sceneObj, color) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setLightColor(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setLightColor(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }

    if (sceneObj.isLight) {
      sceneObj.color.setStyle(color)
      // console.log(sceneObj)
      sceneObj.update()
    }

    return true
  }

  // 设置灯光的强度
  this.setLightIntensity = function (sceneObj, intensity) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setLightIntensity(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setLightIntensity(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }

    if (intensity < 0.0) { intensity = 0.0 }

    if (sceneObj.isLight) {
      sceneObj.intensity = intensity
      sceneObj.update()
    }
  }

  // 设置物体为透明物体
  this.setToTransparent = function (sceneObj, ifToggle) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setToTransparent(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setToTransparent(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }

    // 假如对象是标注线，则调用标注线本身的透明方法
    if (sceneObj.isCustom && 'setToTransparent' in sceneObj) {
      sceneObj.setToTransparent(ifToggle)
      return true
    }

    if ('material' in sceneObj) {
      sceneObj.material.transparent = ifToggle
      // 设置透明物体的渲染顺序(顺序越小越先渲染)
      // 以免遮挡了控制器
      sceneObj.renderOrder = -1

      // sceneObj.dataModel.ifTransparent = ifToggle;
    }

    return true
  }

  // 设置当前物体的透明度
  this.setObjOpacity = function (sceneObj, opacity) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setObjOpacity(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setObjOpacity(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }

    if (opacity > 1.0) { opacity = 1.0 } else if (opacity < 0.0) { opacity = 0.0 }

    if (opacity > 0.99) {
      scope.setToTransparent(sceneObj, false)
    } else {
      scope.setToTransparent(sceneObj, true)
    }

    // 假如是标注线，则调用标注线本身的透明度调节
    if (sceneObj.isCustom && 'setOpacity' in sceneObj) {
      sceneObj.setOpacity(opacity)
      return true
    }

    if ('material' in sceneObj) {
      sceneObj.material.opacity = opacity
      sceneObj.dataModel.opacity = opacity
    }

    return true
  }

  // // 设置物体的光泽度
  // this.setShininess = function (sceneObj, shininess) {
  //   if (checkNone(sceneObj)) {
  //     console.log('Scene, setShininess(), sceneObj: "sceneObj is undefined or null."')
  //     return false
  //   }
  //   if (typeof sceneObj !== 'object') {
  //     console.log("Scene, setShininess(), sceneObj: \"sceneObj should be \'object\'.\"")
  //     return false
  //   }
  //   if (sceneObj instanceof Array) {
  //     console.log("Scene, setShininess(), sceneObj: \"sceneObj can\'t be Array.\"")
  //     return false
  //   }
  //   if (!('material' in sceneObj)) {
  //     console.log('Scene, setShininess(), sceneObj: "No material in sceneObj."')
  //     return false
  //   }

  //   // 由于换了材质类型，使用roughness来代替光泽度
  //   sceneObj.material.roughness = 1 - shininess
  //   sceneObj.material.metalness = shininess
  //   // sceneObj.material.shininess = shininess;
  //   // sceneObj.material.reflectivity = shininess / 100;
  //   sceneObj.material.needsUpdate = true
  //   sceneObj.dataModel.shininess = 1 - shininess
  // }

  this.setRoughness = function (sceneObj, roughness) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setRoughness(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setRoughness(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }
    if (sceneObj instanceof Array) {
      console.log("Scene, setRoughness(), sceneObj: \"sceneObj can\'t be Array.\"")
      return false
    }
    if (!('material' in sceneObj)) {
      console.log('Scene, setRoughness(), sceneObj: "No material in sceneObj."')
      return false
    }

    sceneObj.material.roughness = roughness
    sceneObj.material.needsUpdate = true
    sceneObj.dataModel.roughness = roughness
  }

  this.setMetalness = function (sceneObj, metalness) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setMetalness(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setMetalness(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }
    if (sceneObj instanceof Array) {
      console.log("Scene, setMetalness(), sceneObj: \"sceneObj can\'t be Array.\"")
      return false
    }
    if (!('material' in sceneObj)) {
      console.log('Scene, setMetalness(), sceneObj: "No material in sceneObj."')
      return false
    }

    sceneObj.material.metalness = metalness
    sceneObj.material.needsUpdate = true
    sceneObj.dataModel.metalness = metalness
  }

  this.setClearCoat = function (sceneObj, clearCoat) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setClearCoat(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setClearCoat(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }
    if (sceneObj instanceof Array) {
      console.log("Scene, setClearCoat(), sceneObj: \"sceneObj can\'t be Array.\"")
      return false
    }
    if (!('material' in sceneObj)) {
      console.log('Scene, setClearCoat(), sceneObj: "No material in sceneObj."')
      return false
    }

    console.log('set clearcoat')
    sceneObj.material.clearCoat = clearCoat
    sceneObj.material.needsUpdate = true
    sceneObj.dataModel.clearCoat = clearCoat
  }

  this.setClearCoatRoughness = function (sceneObj, clearCoatRoughness) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setClearCoatRoughness(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setClearCoatRoughness(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }
    if (sceneObj instanceof Array) {
      console.log("Scene, setClearCoatRoughness(), sceneObj: \"sceneObj can\'t be Array.\"")
      return false
    }
    if (!('material' in sceneObj)) {
      console.log('Scene, setClearCoatRoughness(), sceneObj: "No material in sceneObj."')
      return false
    }

    sceneObj.material.clearCoatRoughness = clearCoatRoughness
    sceneObj.material.needsUpdate = true
    sceneObj.dataModel.clearCoatRoughness = clearCoatRoughness
  }

  this.setReflectivity = function (sceneObj, reflectivity) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setReflectivity(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setReflectivity(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }
    if (sceneObj instanceof Array) {
      console.log("Scene, setReflectivity(), sceneObj: \"sceneObj can\'t be Array.\"")
      return false
    }
    if (!('material' in sceneObj)) {
      console.log('Scene, setReflectivity(), sceneObj: "No material in sceneObj."')
      return false
    }

    sceneObj.material.reflectivity = reflectivity
    sceneObj.material.needsUpdate = true
    sceneObj.dataModel.reflectivity = reflectivity
  }

  this.set = function (sceneObj, metalness) {
    if (checkNone(sceneObj)) {
      console.log('Scene, setMetalness(), sceneObj: "sceneObj is undefined or null."')
      return false
    }
    if (typeof sceneObj !== 'object') {
      console.log("Scene, setMetalness(), sceneObj: \"sceneObj should be \'object\'.\"")
      return false
    }
    if (sceneObj instanceof Array) {
      console.log("Scene, setMetalness(), sceneObj: \"sceneObj can\'t be Array.\"")
      return false
    }
    if (!('material' in sceneObj)) {
      console.log('Scene, setMetalness(), sceneObj: "No material in sceneObj."')
      return false
    }

    sceneObj.material.metalness = metalness
    sceneObj.material.needsUpdate = true
    sceneObj.dataModel.metalness = metalness
  }

  // 设置标注线相关属性
  this.setMarkLine = function (sceneObj, parameterList) {
    if (!sceneObj.isMarkLine) return

    var accessList = ['font', 'fontSize', 'text', 'color',
      'textAlign', 'textRot', 'textType', 'markLength',
      'lineShape', 'lineRadius', 'headShape', 'subLength', 'opacity']

    for (var parameter in parameterList) {
      if (accessList.indexOf(parameter) > -1) { sceneObj[parameter] = parameterList[parameter] }
    }

    sceneObj.update()
  }

  // Private methods ////////////////////////////////////////////////////

  function init () {
    // 环境光初始化
    ambientLight.name = 'ambientLight'
    scope.add(ambientLight)

    hemiLight.intensity = 0.5
    hemiLight.color.setHSL(1.0, 1, 1.0)
    hemiLight.groundColor.setHSL(0.095, 1, 0.75)
    hemiLight.position.set(0, 50, 0)
    scope.add(hemiLight)

    // 网格辅助器初始化
    gridHelper.position.y = 0
    gridHelper.material.opacity = 0.25
    gridHelper.material.transparent = true
    scope.add(gridHelper)

    // 初始化相机位置
    camera.position.z = 5
  }

  init()
}

// Constructor
Scene.prototype = Object.assign(Object.create(THREE.Scene.prototype), {

  constructor: Scene,

  copy: function (source, recursive) {
    THREE.Scene.prototype.copy.call(this, source, recursive)

    return this
  }

})

export { Scene }



