<template>
  <div class="UIImage" :style="style" @click="redirect">
    <img :src="imgData.src" :style="imgStyle"></img>
  </div>
</template>

<script>
export default {
  name: 'ui-image-component',
  props: {
    imgData: Object
  },
  computed: {
    style: function () {
      var re = this.imgData.widget.calc()
      console.log(455464687, this.imgData.opacity)
      return {
        width: `${re.width}px`,
        height: `${re.height}px`,

        left: `${re.left}px`,
        top: `${re.top}px`,
        transform: `rotate(${this.imgData.rot}deg)`,
        'z-index': `${this.imgData.zIndex}`,
        opacity: `${this.imgData.opacity}`
      }
    },
    imgStyle: function () {
      var isBtn = (this.imgData.url !== undefined)
      return {
        'pointer-events': isBtn ? 'auto' : 'none'
      }
    }
  },
  methods: {
    redirect: function () {
      if (!this.imgData.url) return
      // 处理网址，主要是检测是否带有http/https开头的信息
      if (/http\b|https\b/.exec(this.imgData.url)) window.open(`${this.imgData.url}`)
      else window.open(`http://${this.imgData.url}`)
    }
  }
}
</script>

<style>
.UIImage {
  width: 3rem;
  height: 3rem;
  position: absolute;
  z-index: 3;
  top: 0rem;
  left: 0rem;

  transition: box-shadow 0.2s;
  user-select: none;
  pointer-events: none;
}

.UIImage.active {
  box-shadow: 0 0 7px 2px #0999ff;
}

.UIImage>img {
  width: 100%;
  height: 100%;
  pointer-events: auto;
}
</style>


